
.header-v3 {
    background: transparent;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 99999;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: var(--font-bg-clr);
        mix-blend-mode: multiply;
        opacity: 0.9;
    }
    .logo {
        img,
        svg {
            .hdr-l-white {
                fill: var(--white-txt-clr);
            }
            .hdr-l-theme {
                fill: var(--themev3-txt-clr);
            }
        }
    }
    &.sticky-header {
        background: #17242c;
        &::before {
            display: none;
        }
    }

    .hdr-srch, 
    .hdr-cart {
        background: var(--white-bg-clr);
        &:hover {
            img,
            svg {
                color: var(--white-txt-clr);
            }
        }
        &::before {
            background: var(--themev3-bg-clr);
        }
        img,
        svg {
            color: var(--themev3-txt-clr);
        }
    }
    .crt-dtls {
        background: var(--hv2-icon-bg);
        .btn-skew-y {
            color: var(--themev3-txt-clr);
            background: var(--hv2-icon-bg);
            border-color: var(--themev3-bg-clr) !important;

            &::before,&::after {
                color: var(--white-txt-clr);
                background-color: var(--themev3-bg-clr);
            }
        }
    }
    .single-crt-dtls {
        .title {
            span {
                color: var(--themev3-txt-clr);
            }
            h6 {
                a {
                    color: var(--white-txt-clr);
                }
            }
        }
    }

    // Search
    .hdr-search {
        background: var(--hv2-icon-bg);
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04,
        .shape-05 {
           img,
           svg {
               path {
                   fill: var(--themev3-bg-clr);
               }
           }
        }

    }
    .hdeClose {
        background: var(--themev3-bg-clr);
    }
    .hdrSrch-input {
        &:focus-within {
            input {
                border-color: var(--themev3-bdr-clr);
                color: var(--white-txt-clr);
            }
            button {
                img,
                svg {
                    color: var(--themev3-txt-clr);
                }
            }
        }
    }
    .hdrSrch-result {
        .title {
            h4 {
                color: var(--white-txt-clr);
            }
        }
        .result {
            &::-webkit-scrollbar-thumb {
                background: var(--themev3-bg-clr);
            }
        }
    }
    .single-srch-result {
        &:hover {
            h5 {
                a {
                    color: var(--themev3-txt-clr);
                }
            }
        }
        h5 {
            a {
                background-image: linear-gradient(#81BD4C, #81BD4C);
            }
        }
    }
    // Button 
    .lgn-rgstr {
        .btn-skew-y {
            color: var(--themev3-txt-clr);
            background: var(--white-bg-clr);
            border-color: var(--themev3-bg-clr) !important;

            &::before,&::after {
                color: var(--white-txt-clr);
                background-color: var(--themev3-bg-clr);
            }
        }
        .l-gray-btn {
            color: var(--themev3-txt-clr);
            background: transparent;
            border-color: var(--themev3-bg-clr) !important;

            &::before,&::after {
                color: var(--themev3-txt-clr);
                background-color: var(--white-bg-clr);
            }
        }
    }
}
@media screen and (min-width: 1200px) {
    .header-v3 {
        .main-menu {
            > ul {
               > li {
                   margin: 0;
                   &:hover {
                      > a {
                           color: var(--white-txt-clr);
                           &::before {
                                width: 100%;
                                right: auto;
                                left: 0;
                           }
                           &::after {
                               transform: translate(-50%, -50%);
                           }
                           img,svg {
                               color: var(--white-txt-clr);
                           }
                       }
                   }
                    > a {
                        color: var(--white-txt-clr);
                        z-index: 4;
                        padding-left: 20px;
                        padding-right: 20px;
                        z-index: 1;
                        white-space: nowrap;
                        &::after {
                            color: var(--white-txt-clr);
                            left: 42%;
                            transform: translateX(-50%);
                        }
                        &::before {
                            width: 0;
                            height: 50px;
                            background: var(--themev3-bg-clr);
                            top: 50%;
                            transform: translateY(-50%);
                            right: 0;
                            z-index: -1;
                            transition: all .3s ease-in;
                        }
                    }
                    .sub-menu {
                        background: var(--hv2-icon-bg);
                        li {
                            a {
                                color: var(--white-txt-clr);
                                &::before {
                                    background: var(--themev3-bg-clr);
                                    clip-path: unset;
                                }
                            }
                        }
                    }
                }
            }
        }
        .mega-menu {
            background: var(--hv2-icon-bg);
            .shape-1,
            .shape-2 {
                img,
                svg {
                    path {
                        fill: #89A174;
                    }
                }
            }
        }
        .single-menu {
            .title {
                h6 {
                    color: var(--white-txt-clr);
                }
            }
            &:hover {
                .title {
                    h6 {
                        color: var(--themev3-txt-clr);
                    }
                }
            }
            .menu {
                ul {
                    li {
                        a {
                            color: var(--white-txt-clr);
                            &::after {
                                color: var(--themev3-txt-clr);
                            }
                        }
                    }
                }
            }
        }
    }
}

// Responsive
@media screen and (max-width: 1400px) {
    .header-v3 {
        .main-menu {
            >ul {
                >li {
                    >a {
                        padding-left: 5px;
                        padding-right: 5px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .header-v3 {
        .hamgurger {
            ul {
                li {
                    background: var(--themev3-bg-clr);
                    &::before {
                        background: var(--themev3-bg-clr);  
                    }
                }
            }
        }

        // Main Menu
        .main-menu {
            background: var(--hv2-icon-bg);
            &::-webkit-scrollbar-thumb {
                background: var(--themev3-bg-clr);
            }
            ul {
                li {
                    &.active {
                       > a {
                            img,
                            svg {
                                color: var(--themev3-txt-clr);
                            }
                        }
                    }
                    a {
                        color: var(--white-txt-clr);
                        &::after {
                            color: var(--themev3-txt-clr);
                        }
                    }
                    .sub-menu {
                        li {
                            a {
                                &::after {
                                    color: var(--themev3-txt-clr) !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        // Mega Menu
        .single-menu {
            .title {
                &.active {
                    h6 {
                        color: var(--themev3-txt-clr);
                    }
                    img,
                    svg {
                        color: var(--themev3-txt-clr);
                    }
                }
                h6 {
                    color: var(--white-txt-clr);
                }
                img,
                svg {
                    color: var(--white-txt-clr);
                }
            }
        }
    }
}