.abutGallery-wrapper {
    margin: -15px;
}

.aboutGrid-sizer,
.aGallery-item {
    width: calc(100% / 4);
}


.singleAboutGallery {
    margin: 15px;
    position: relative;
    z-index: 1;
    border-radius: 10px;
    overflow: hidden;
    &:hover {
        &::before,
        &::after {
            transition: all .4s ease-in-out;
            animation-duration: 1s;
            clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
        }
        &::before {
            animation: g_top .3s ease-in-out;
            opacity: 0;
        }
        .g-icon {
            a {
                opacity: 1;
                &:last-child,
                &:first-child {
                    transform: translateY(0);
                }
            }
        }
        .title {
            span,
            h4 {
                opacity: 1;
                transform: translateY(0);
            }
        }
    }
    &::before,
    &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: #1F2228;
        mix-blend-mode: multiply;
        opacity: 0.7;
        border-radius: 10px;
        transition: all .4s ease-in-out;
    }
    &::before {
        top: 0;
        right: 0;
        clip-path: polygon(100% 0, 100% 0, 100% 0, 100% 0);
    }
    &::after {
        bottom: 0;
        left: 0;
        clip-path: polygon(0 100%, 0 100%, 0 100%, 0 100%);
    }
    .gallery-image {
        img {
            width: 100%;
            height: 100%;
        }
    }
    .g-icon {
        position: absolute;
        z-index: 2;
        right: 20px;
        top: 20px;
        a {
            width: 40px;
            height: 40px;
            background: var(--white-bg-clr);
            border-radius: 5px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 20px;
            color: var(--theme-txt-clr);
            opacity: 0;
            transition: all .4s ease-in-out;
            &:first-child {
                margin-right: 5px;
                transform: translateY(-60px);
                transition-duration: .3s;
            }
            &:last-child {
                transform: translateY(-60px);
                transition-duration: .5s;
            }
        }
    }
    .title {
        position: absolute;
        z-index: 2;
        bottom: 20px;
        left: 20px;
        span,
        h4 {
            color: var(--white-txt-clr);
            transition: all .4s ease-in-out;
            line-height: 1;
            opacity: 0;
            transform: translateY(50px);
        }
        span {
            
            font-size: 16px;
            display: block;
            transition-delay: .3s;
            
        }
        h4 {
            font-size: 20px;
            font-weight: 600;
            font-family: var(--raleway);
            margin-top: 10px;
            transition-delay: .4s;
            a {
                display: block;
                color: var(--white-txt-clr);
            }
        }
    }
}

@keyframes g_top {
    0% {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
   100% {
       opacity: 0;
   }
}


// Responsive 
@media screen and (max-width: 1199px) {
    .aboutGrid-sizer,
    .aGallery-item {
        width: calc(100% / 3);
    }
}
@media screen and (max-width: 991px) {
    .aboutGrid-sizer,
    .aGallery-item {
        width: calc(100% / 2);
    }
    .abt-long-hight {
        .singleAboutGallery {
            height: 683.96px;
            .gallery-image {
                height: 100%;
                img {
                    object-fit: cover;
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .abutGallery-wrapper {
        margin: -10px;
    }
    .singleAboutGallery {
        // margin: 10px;
        .title {
            h4 {
                font-size: 18px;
            }
        }
        .g-icon {
            a {
                width: 36px;
                height: 36px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .aboutGrid-sizer,
    .aGallery-item {
        width: 100%;
    }
    .abt-long-hight {
        .singleAboutGallery {
            height: auto;
        }
    }
}