
.hdr-cart-dtls {
    position: relative;
    &.active {
        .hdr-cart {
            &::before {
                clip-path: circle(70.7% at 50% 50%);
                opacity: 1;
            }
            img,
            svg {
                transform: scale(1.2);
                color: var(--white-txt-clr);
            }
        }
        .crt-dtls {
            opacity: 1;
            visibility: visible;
            transform: scale(1);
        }
    }
}


.crt-dtls {
    position: absolute;
    content: "";
    width: 335px;
    background: var(--white-bg-clr);
    top: 72px;
    right: 0;
    box-shadow: 10px 20px 30px var(--crt-shadow);
    padding: 30px;
    transition: all .4s ease-in-out;
    opacity: 0;
    visibility: hidden;
    transform: scale(1.1);
    .button {
        margin-top: 20px;
    }
}

.single-crt-dtls {
    display: flex;
    align-items: center;
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px solid var(--light-gray-bdr);
    &:first-child {
        margin-top: 0;
        padding-top: 0;
        border-top: none;
    }
    .img {
        width: 60px;
        margin-right: 20px;
        img {
            width: 100%;
        }
    }
    .title {
        flex: 1;
        span {
            font-weight: 500;
            color: var(--theme-txt-clr);
            font-size: 14px;
            line-height: 1;
        }
        h6 {
            line-height: 1;
            a {
                font-weight: 600;
                font-family: var(--raleway);
                color: var(--font-txt-clr);
                line-height: 1;
                display: block;
                margin-top: 10px;
            }
        }
    }
}

// Responsive

@media screen and (max-width: 1199px) {
    .crt-dtls {
        top: 64px;
    }
}
@media screen and (max-width: 767px) {
    .crt-dtls {
        top: 53px;
        width: 260px;
        padding: 20px;
        .button {
            a {
                padding: 12px 20px;
            }
        }
    }
    .single-crt-dtls {
        margin-top: 15px;
        padding-top: 15px;
        .title {
            span {
                font-size: 15px;
            }
            h6 {
                a {
                    font-size: 14px;
                    margin-top: 5px;
                }
            }
        }
        .img {
            margin-right: 15px;
            width: 50px;
        }
    }
}
@media screen and (max-width: 575px) {
    .crt-dtls {
        top: 47px;
    }
}