
.error-wrapper {
    display: flex;
    align-items: center;
    max-width: 1290px;
    margin: 0 auto;
    justify-content: space-between;
}

.error-content {
    max-width: 435px;
    width: 100%;
    margin-left: 30px;
    > span {
        color: var(--body-txt-clr);
        font-size: 30px;
        line-height: 1;
        font-weight: 700;
    }
    h2 {
        font-size: 48px;
        color: var(--theme-txt-clr);
        line-height: 1;
        font-weight: 400;
        margin-top: 15px;   
    }
    p {
        font-size: 20px;
        margin-top: 15px;
    }
    a {
        margin-top: 30px;
        background: var(--theme-bg-clr);
        display: inline-flex;
        align-items: center;
        transition: all .3s ease-in-out;
        border: 1px solid transparent;
        img,svg {
            margin-right: 8px;
            transition: all .3s ease-in-out;
        }
        &:hover {
            border-color: var(--theme-bg-clr);
            color: var(--theme-txt-clr);
            background: transparent;
            img,svg {
                path {
                    fill: var(--theme-txt-clr);
                }
            }
        }
    }
}

@media screen and (max-width: 767px) {
    .error-content {
        > span {
            font-size: 24px;
        }
        p {
            font-size: 18px;
        }
        a {
            margin-top: 20px;
        }
    }
}

@media screen and (max-width: 575px) {
    .error-wrapper {
        flex-direction: column-reverse;
        justify-content: center;
    }
    .error-content {
        text-align: center;
        margin-bottom: 30px;
        margin-left: 0;
        h2 {
            margin-top: 10px;
        }
        p {
            margin-top: 10px;
        }
        a {
            margin-top: 15px;
            padding: 12px 20px;
        }
    }
}