#preloader {
    position: fixed;
    content: "";
    width: 100%;
	height: 100%;
	transition: all .4s ease-in-out;
	z-index: 99999999;
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	top: 0;
	left: 0;

	&::before {
		position: absolute;
		content: "";
		height: 100%;
		width: 100%;
		z-index: -1;
		transition: all .4s ease-in-out;
		opacity: 1;
		visibility: visible;
		// background: rgba(255, 255, 255, 0.2);
		// backdrop-filter: blur(50px);
		top: 0;
		left: 0;

		background: rgba(0,0,0,0.7);
		mix-blend-mode: normal;
		backdrop-filter: blur(30px);
	}
    
}
.ctn-preloader {
	-webkit-box-align: center;
	align-items: center;
	// cursor: none;
	display: -webkit-box;
	display: flex;
	height: 100%;
	-webkit-box-pack: center;
	justify-content: center;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 9999;
	.animation-preloader {
		position: absolute;
		z-index: 99999;
		.txt-loading {
			font-family: var(--playFair);
            font-size: 64px;
            font-weight: bold;
            line-height: 1.3;
			text-align: center;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
			.letters-loading {
				&:before {
					-webkit-animation: letters-loading 4s infinite;
					animation: letters-loading 4s infinite;
					color: var(--theme-txt-clr);
					content: attr(data-text-preloader);
					left: 0;
					opacity: 0;
					position: absolute;
					top: 0;
					-webkit-transform: rotateY(-90deg);
					transform: rotateY(-90deg);
				}
				// color: rgba(0, 0, 0, 0.2);
				color: rgba(255,255,255,0.1);
				position: relative;
				&:nth-child(2) {
					&:before {
						-webkit-animation-delay: 0.2s;
						animation-delay: 0.2s;
					}
				}
				&:nth-child(3) {
					&:before {
						-webkit-animation-delay: 0.4s;
						animation-delay: 0.4s;
					}
				}
				&:nth-child(4) {
					&:before {
						-webkit-animation-delay: 0.6s;
						animation-delay: 0.6s;
					}
				}
				&:nth-child(5) {
					&:before {
						-webkit-animation-delay: 0.8s;
						animation-delay: 0.8s;
					}
				}
				&:nth-child(6) {
					&:before {
						-webkit-animation-delay: 1s;
						animation-delay: 1s;
					}
				}
				&:nth-child(7) {
					&:before {
						-webkit-animation-delay: 1.2s;
						animation-delay: 1.2s;
					}
				}
				&:nth-child(8) {
					&:before {
						-webkit-animation-delay: 1.4s;
						animation-delay: 1.4s;
					}
				}
			}
		}
	}
}
.loading {
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
	svg {
        max-width: 150px;
        max-height: 150px;
        #cap {
            transform-origin: center;
            animation: capAnimation 1s ease-in-out infinite;
            fill: rgba(255,255,255,0.3);
        }
        #body {
            fill: rgba(255,255,255,0.3);
        }
        .smell {
            opacity: 1;
            // fill: var(--theme-txt-clr);
			fill: rgba(255,255,255,0.1);
        }
    }
}

.preloader-v3 {
	&.ctn-preloader {
		.animation-preloader {
			.txt-loading {
				.letters-loading {
					&::before {
						color: var(--themev3-txt-clr);
					}
				}
			}
		}
	}
}
.preloader-v4 {
	&.ctn-preloader {
		.animation-preloader {
			.txt-loading {
				.letters-loading {
					&::before {
						color: var(--themev4-txt-clr);
					}
				}
			}
		}
	}
}

// Cup Shape
.hv3-preloader {
	// #preloader {
	// 	&::before {
	// 		background: rgba(255, 255, 255, 0.2);
	// 		backdrop-filter: blur(50px);
	// 		width: 100%;
	// 	}
	// }
	.ctn-preloader {
		.animation-preloader {
			.txt-loading {
				.letters-loading {
					color: var(rgba(55, 31, 12, 0.4));
					&::before {
						color: var(--themev2-txt-clr);
					}
				}
			}
		}
	}
}
.cup-shape {
	position: relative;
	width: 150px;
	height: 200px;
	margin: 0px auto 30px;
	.smoke {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100px;
		div {
			position: absolute;
			bottom: 0;
			background-color: rgba(255, 255, 255, 0.6);
			z-index: 3;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			opacity: 0;
			&:nth-child(1) {
				left: 20px;
				animation: loader-smoke-1 3s linear infinite;
				animation-delay: 0s;
			}
			&:nth-child(2) {
				left: 40px;
				animation: loader-smoke-2 3s linear infinite;
				animation-delay: 0.2s;
			}
			&:nth-child(3) {
				left: 60px;
				animation: loader-smoke-3 3s linear infinite;
				animation-delay: 0.4s;
			}
			&:nth-child(4) {
				left: 100px;
				animation: loader-smoke-4 3s linear infinite;
			}
		}
	}
	.cup {
		position: absolute;
		bottom: 0;
		height: 100px;
		width: 100%;
		background-color: #fff;
		box-shadow: inset 10px 10px 25px 3px #ddd;
		border-radius: 0 0 50% 50%;
		.body {
			position: absolute;
			top: -15px;
			left: 0;
			width: 100%;
			height: 30px;
			background-color: #331906;
			border: 5px solid #fff;
			z-index: 1;
			border-radius: 50%;
		}
		.plate {
			position: absolute;
			top: calc(100% - 10px);
			width: 100%;
			height: 20px;
			border-radius: 50%;
			z-index: -1;
			background-color: #eee;
			box-shadow: inset 5px 5px 10px 1px #ccc;
		}
		.hand {
			position: absolute;
			left: 103%;
			top: 10px;
			height: 80px;
			width: 50px;
			border-radius: 50%;
			border: 5px solid #fff;
			border-bottom-color: transparent;
			transform: rotate(50deg);
			transform-origin: top center;
			z-index: -1;
		}
	}
}

@-webkit-keyframes letters-loading {
	0%,75%,100% {
		opacity: 0;
		-webkit-transform: rotateY(-90deg);
		transform: rotateY(-90deg);
	}
	25%,50% {
		opacity: 1;
		-webkit-transform: rotateY(0deg);
		transform: rotateY(0deg);
	}
}
@keyframes letters-loading {
	0%,75%,100% {
		opacity: 0;
		-webkit-transform: rotateY(-90deg);
		transform: rotateY(-90deg);
	}
	25%,50% {
		opacity: 1;
		-webkit-transform: rotateY(0deg);
		transform: rotateY(0deg);
	}
}

// Cap Animate
@keyframes capAnimation {
	0% {
		transform: rotate(5deg);
	}
	50% {
		transform: rotate(-5deg);
	}
	100% {
		transform: rotate(5deg);
	}
}

// Cup Shape
@keyframes loader-smoke-1 {
	from {
		bottom: 0;
		transform: scale(1);
		opacity: 1;
		filter: blur(5px);
	}
	to {
		bottom: 100%;
		transform: scale(3);
		opacity: 0;
		filter: blur(10px);
	}
}
@keyframes loader-smoke-2 {
	from {
		bottom: 0;
		left: 40px;
		transform: scale(1);
		opacity: 1;
		filter: blur(5px);
	}
	to {
		bottom: 100%;
		transform: scale(3);
		left: 120px;
		opacity: 0;
		filter: blur(10px);
	}
}
@keyframes loader-smoke-3 {
	0% {
		bottom: 0;
		left: 60px;
		transform: scale(1);
		opacity: 1;
		filter: blur(10px);
	}
	50% {
		left: 120px;
	}
	100% {
		bottom: 100%;
		transform: scale(3);
		left: 40px;
		opacity: 0;
		filter: blur(20px);
	}
}
@keyframes loader-smoke-4 {
	from {
		bottom: 0;
		left: 100px;
		transform: scale(1);
		opacity: 1;
		filter: blur(5px);
	}
	to {
		bottom: 100%;
		transform: scale(3);
		left: 0;
		opacity: 0;
		filter: blur(10px);
	}
}

// After Loaded
.loaded {
	#preloader {
		opacity: 0;
		visibility: hidden;
		z-index: -11111111;
	}
	.animation-preloader {
		opacity: 0;
		-webkit-transition: 0.3s ease-out;
		transition: 0.3s ease-out;
	}
	.hv3-preloader {
		#preloader {
			&::after {
				display: none;
			}
		}
	}
}

// Responsive 
@media screen and (max-width: 767px) {
	.ctn-preloader {
		.animation-preloader {
			.txt-loading {
				font-size: 40px;
			}
		}
	}
	.loading {
		margin-bottom: 20px;
		svg {
			max-width: 100px;
			max-height: 100px;
		}
	}
}
@media screen and (max-width: 575px) {
	.ctn-preloader {
		.animation-preloader {
			.txt-loading {
				font-size: 34px;
			}
		}
	}
	.loading {
		svg {
			max-width: 80px;
			max-height: 80px;
		}
	}
	.cup-shape {
		width: 100px;
		height: 150px;
		margin-bottom: 20px;
		.cup {
			height: 75px;
			.hand {
				height: 70px;
				width: 45px;
			}
		}
	}
}