
// Banner > =============================
.homeV3-banner-area {
    padding: 390px 0px 110px;
    position: relative;
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04 {
        position: absolute;
        bottom: 0;
        animation: hv3_tpbm 1s linear infinite alternate;
    }
    .shape-01 {
        left: 0;
    }
    .shape-02 {
        left: 10%;
        animation-duration: 1.2s;
    }
    .shape-03 {
        left: 20%;
        animation-duration: 1.4s;
    }
    .shape-04 {
        left: 28%;
        animation-duration: 1.6s;
    }
}
.hv3-banner-wrapper {
    max-width: 730px;
    position: relative;
    padding-bottom: 145px;
    &::before {
        position: absolute;
        content: "";
        background-image: url('../img/home-v3/banner/shape-01.png');
        top: 0;
        left: 0;
        width: 63px;
        height: 63px;
        z-index: -1;
    }
    .owl-dots {
        position: absolute;
        bottom: 0;
        left: 90%;
        display: flex; 
        align-items: center;
        .owl-dot {
            background: var(--white-bg-clr);
            width: 10px;
            height: 10px;
            margin: 0px 10px;
            transition: all .3s ease-in-out;
            &.active {
                background: var(--themev3-bg-clr);
            }
        }
    }
    .owl-item {
        .hv3-banner-content {
            h2,
            p,
            a {
                transform: translateY(100px);
                opacity: 0;
                transition: all 1s ease-in-out;
            }
            h2 {
                transition-duration: .5s;
            }
            p {
                transition-duration: .7s;
            }
            a {
                transition-duration: .9s;
            }
        }
        &.active, 
        &.cloned {
            .hv3-banner-content {
                h2,
                p,
                a {
                    transform: translateY(0px);
                    opacity: 1;
                }
            }
        }
    }
}
.hv3-banner-content {
    padding-left: 20px;
    h2 {
        font-size: 75px;
        font-weight: 900;
        color: var(--white-txt-clr);
        font-family: var(--playFair);
        line-height: 1.2;
        span {
            color: var(--themev3-txt-clr);
        }
    }
    p {
        font-size: 18px;
        font-weight: 500;
        color: var(--white-txt-clr);
        max-width: 507px;
        margin-top: 15px;
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
