// Category > =============================
.hv3-ctgry-area {
    background: var(--snow-bg);
    position: relative;
    z-index: 1;
    overflow: hidden;
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04 {
        position: absolute;
        z-index: -1;
        top: 0;
        animation: cTop_bottom 10s linear infinite;
    }
    .shape-02 {
        left: 20%;
        animation-delay: 12s;
    }
    .shape-03 {
        right: 20%;
        animation-delay: 14s;
    }
    .shape-04 {
        right: 2%;
        animation-delay: 16s;
    }
}
.hv3-ctgry-wrapper {
    max-width: 1410px;
    margin: 0 auto;
}
.hv3Ctgry-tab-navs {
    .nav-tabs {
        border-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        grid-gap: 25px;
        flex-wrap: wrap;
        .nav-link {
            border: 0 !important;
            padding: 27px 45px;
            background: var(--hv3ctgry-item);
            border-radius: 10px;
            img {
                margin-bottom: 15px;
                transition: all .3s ease-in;
            }
            span {
                display: block;
                font-size: 18px;
                font-weight: 500;
                line-height: 1;
                color: var(--font-txt-clr);
                margin-bottom: 8px;
                &:last-child {
                    font-size: 24px;
                    margin-bottom: 0;
                }
            }

            &:hover,
            &.active {
                box-shadow: -10px 30px 50px var(--hv3ctgry-sdow);
                background: var(--white-bg-clr);
                img {
                    transform: scale(1.1);
                }
            }
        }
    }
}
.hv3Ctgry-tab-content {
    margin-top: 50px;
}
.hv3-ctgry-items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
}
.hv3-ctgry-item {
    background: var(--hv3ctgry-item);
    padding: 13px;
    border-radius: 10px;
    transition: all .3s ease-in-out;
    &:hover {
        box-shadow: -10px 30px 50px var(--hv3ctgry-sdow);
        background: var(--white-bg-clr);
        .img {
            img {
                transform: scale(1.2) rotate(3deg);
            }
            .button {
                opacity: 1;
                transform: translateX(-50%) translateY(0);
            }
        }
    }
    .img {
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        z-index: 1;
        img {
            width: 100%;
            transition: all .7s ease-in-out;
        }
        .button {
            position: absolute;
            z-index: 2;
            left: 50%;
            transform: translateX(-50%) translateY(100%);
            bottom: 70px;
            opacity: 0;
            transition: all .3s ease-in-out;
            width: 100%;
            display: flex;
            justify-content: center;
            a {
                border-radius: 5px;
                background: var(--themev3-bg-clr);
                align-items: center;
                display: flex;
                line-height: 1;
                border: 1px solid transparent;
                img,svg {
                    margin-right: 10px;
                    max-width: 20px;
                    max-height: 20px;
                    path {
                        transition: all .4s ease-in-out;
                    }
                }
                &:hover {
                    color: var(--themev3-txt-clr);
                    background: var(--white-bg-clr);
                    border-color: var(--themev3-bdr-clr);
                    img,svg {
                        path {
                            fill: var(--themev3-txt-clr);
                        }
                    }
                }
            }
        }
    }
    .content {
        margin: 35px 10px 25px;
        text-align: center;
        h2 {
            font-family: var(--playFair);
            font-weight: 700;
            line-height: 1;
            font-size: 30px;
        }
        a {
            display: block;
            color: var(--font-txt-clr);
            font-size: 20px;
            font-weight: 500;
            margin-top: 20px;
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .hv3-ctgry-items {
        grid-gap: 30px;
    }
}
@media screen and (max-width: 991px) {
    .hv3Ctgry-tab-navs {
        .nav-tabs {
            grid-gap: 20px;
            .nav-link {
                padding: 20px 30px;
                img {
                    margin-bottom: 10px;
                }
                span {
                    font-size: 16px;
                    &:last-child {
                        font-size: 20px;
                    }
                }
            }
        }
    }
    .hv3-ctgry-items {
        grid-template-columns: 1fr 1fr;
    }
    .hv3-ctgry-item {
        .img {
            .button {
                bottom: 40px;
            }
        }
        .content {
            margin: 20px 10px 10px;
            h2 {
                font-size: 30px;
            }
            a {
                margin-top: 10px;
                font-size: 18px;
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .hv3-ctgry-area {
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04 {
            max-width: 100px;
            img {
                width: 100%;
            }
        }
    }
    .hv3Ctgry-tab-navs {
        .nav-tabs {
            grid-gap: 15px;
            .nav-link {
                padding: 15px 20px;
                border-radius: 5px;
                img {
                   display: none;
                }
                span {
