}
@media screen and (max-width: 575px) {
    .hv3-ctgry-area {
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04 {
            max-width: 50px;
            animation-delay: 30s;
        }
        .shape-02 {
            left: 10%;
            animation-delay: 32s;
        }
        .shape-03 {
            right: 10%;
            animation-delay: 34s;
        }
        .shape-04 {
            animation-delay: 36s;
        }
    }
    .hv3Ctgry-tab-navs {
        .nav-tabs {
