    }
    .hv3Ctgry-tab-content {
        margin-top: 30px;
    }
    .hv3-ctgry-items {
        grid-template-columns: 1fr;
    }
    .hv3-ctgry-item {
        .img {
            .button {
                bottom: 30px;
            }
        }
        .content {
            margin: 20px 10px 10px;
            h2 {
                font-size: 28px;
            }
            a {
                margin-top: 10px;
                font-size: 16px;
            }
        }
    }
}

