// Chif > ========================================
.hv3-chef-area {
    position: relative;
    z-index: 1;
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04 {
        position: absolute;
        z-index: -1;
    }
    .shape-02 {
        top: 10%;
        left: 15%;
    }
    .shape-01 {
        bottom: 40%;
        left: 5%;
    }
    .shape-04 {
        top: 20%;
        right: 10%;
    }
    .shape-03 {
        bottom: 10%;
        right: 10%;
    }
}
.hv3-single-chif {
    border-radius: 10px;
    overflow: hidden;
    &:hover {
        .img {
            &::before {
                clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
            }
        }
        .content {
            &::before {
                clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
            }
            .social {
                a {
                    color: var(--white-txt-clr);
                }
            }
            .title {
                a,span {
                    color: var(--white-txt-clr);
                }
            }
        }
    }
    .img {
        position: relative;
        img {
            width: 100%;
            height: 100%;
            border-radius: 10px 10px 0px 0px;
        }
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: var(--hv3-chif);
            mix-blend-mode: multiply;
            bottom: 0;
            left: 0;
            clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
            transition: all .4s ease-in-out;
            border-radius: 10px 10px 0px 0px;
        }
    }
    .content {
        text-align: center;
        padding: 30px;
        position: relative;
        z-index: 1;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: var(--themev3-bg-clr);
            z-index: -1;
            top: 0;
            left: 0;
            clip-path: polygon(0 0, 100% 0%, 100% 0, 0 0);
            transition: all .4s ease-in-out;
            border-radius: 0px 0px 10px 10px;
        }
        .social {
            display: flex;
            justify-content: center;
            a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 34px;
                height: 34px;
                font-size: 16px;
                color: var(--body-txt-clr);
                border-radius: 50%;
                overflow: hidden;
                margin: 0px 5px;
                position: relative;
                transition: all .3s ease-in-out;
                &:hover {
                    color: var(--body-txt-clr);
                    &::before {
                        clip-path: circle(70.7% at 50% 50%);
                        opacity: 1;
                    }
                }
                &::before {
                    position: absolute;
                    content: "";
                    background: var(--white-bg-clr);
                    width: 100%;
                    height: 100%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    clip-path: circle(0% at 50% 50%);
                    opacity: 0;
                    transition: all .4s ease-in-out;
                    z-index: -1;
                }
            }
        }
        .title {
            text-align: center;
            margin-top: 15px;
            a {
                display: block;
                color: var(--font-txt-clr);
                font-size: 20px;
                line-height: 1;
                font-weight: 600;
                transition: all .3s ease-in-out;
            }
            span {
                color: var(--font-txt-clr);
                font-size: 16px;
                font-weight: 400;
                margin-top: 5px;
                display: block;
                transition: all .3s ease-in-out;
            }
        }
    }
}
