// Testimonial > ==================================
.hv3-testimonial-area {
    position: relative;
    z-index: 1;
    .shape-1,
    .shape-2 {
        position: absolute;
        left: 0;
        width: 100%;
        z-index: -1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .shape-1 {
        top: 0;
    }
    .shape-2 {
        bottom: 0;
    }
}
.hv3-tstimnl-wrapper {
    background: var(--blog);
    position: relative;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 1;
    .shape {
        position: absolute;
        top: 0;
        right: 0;
        max-width: 520px;
        z-index: -1;
    }
}
.hv3-testimonial {
    max-width: 736px;
    margin: -42px auto -10px;
    margin: 0 auto;
    
    .owl-nav {
        position: absolute;
        top: 50%;
        width: 100%;
        left: 50%;
        transform: translate(-50%, -50%);
        .owl-prev,
        .owl-next {
            position: absolute;
            img,
            svg {
                width: 34px;
                height: 34px;
                max-width: 100%;
                max-height: 100%;
                path {
                    color: var(--font-txt-clr);
                    transition: all .3s ease-in;
                }
            }
            &:hover {
                img,
                svg {
                    path {
                        color: var(--themev3-txt-clr);
                    }
                }
            }
        }
        .owl-prev {
            left: -20%;
        }
        .owl-next {
            right: -20%;
        }
    }
}
.tmnl-inner {
    padding: 42px 0px 12px;
}
.single-v3-tstmnl {
    border: 1px solid var(--themev3-bdr-clr);
    text-align: center;
    padding: 0px 35px;
    .client {
        width: 85px;
        height: 85px;
        margin: -42px auto 30px;
        position: relative;
        z-index: 4;
        img {
           border-radius: 50%;
           z-index: 2;
           position: relative;
        }
        &::before {
            position: absolute;
            content: "";
            width: calc(100% + 40px);
            height: 100%;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
            border-radius: 50%;
            background: var(--white-bg-clr);
        }
    }
    a {
        color: var(--font-txt-clr);
        font-size: 20px;
        font-weight: 600;
        line-height: 1;
        display: block;
        margin-top: 10px;
        span {
            display: block;
            font-weight: 400;
            font-size: 16px;
            margin-top: 10px;
        }
    }
    h4 {
        color: var(--font-txt-clr);
        font-size: 18px;
        margin-top: 10px;
        font-family: var(--raleway);
        font-weight: 400;
    }
    .quite {
        margin: 30px auto -10px;
        position: relative;
        width: 32px;
        height: 25px;
        &::before {
            position: absolute;
            content: "";
            width: calc(100% + 40px);
            height: 100%;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
            border-radius: 50%;
            background: var(--white-bg-clr);
        }
        img,svg {
            max-width: 32px;
            max-height: 23px;
            position: relative;
            z-index: 2;
            path {
                fill: var(--themev3-txt-clr);
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 1350px) {
    .hv3-testimonial {
        .owl-nav {
            .owl-prev {
                left: -10%;
            }
            .owl-next {
                right: -10%;
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .hv3-testimonial-area {
        .shape-1,
        .shape-2 {
            height: 30px;
        }
    }
    .hv3-tstimnl-wrapper {
        .shape {
            max-width: 200px;
        }
    }
    .hv3-testimonial {
        padding-bottom: 30px;
        .owl-nav {
            top: 100%;
            transform: translate(-50%, 0%);
            max-width: 50px;
            .owl-next,
            .owl-prev {
                img,
                svg {
                    max-width: 24px;
                    max-height: 24px;
                }
            }
            .owl-prev {
                left: -10%;
            }
            .owl-next {
                right: -10%;
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .hv3-testimonial-area {
        .shape-1,
        .shape-2 {
            display: none;
        }
    }
}
@media screen and (max-width: 575px) {
    .single-v3-tstmnl {
        padding: 0px 10px;
        .client {
            margin-bottom: 20px;
        }
        h4 {
            font-size: 16px;
            margin-top: 10px;
        }
        a {
            font-size: 18px;
            margin-top: 10px;
        }
        .quite {
            margin: 20px auto -10px;
        }
    }
}
// Subscribe 
.hv3-subscrive {
    .section-title {
        h2 {
            color: var(--themev3-txt-clr);
            font-family: var(--playFair);
            font-weight: 600;
        }
        span {
            background: var(--themev3-bg-clr);
        }
    }
    .sb-from {
        .sb-btn {
            background: var(--themev3-bg-clr);
            color: var(--white-txt-clr);
        }
    }
}

// Reservation > ===============================
.hv3-reservation-area {
    &::before {
        background: var(--hv3-reservation);
    }
}
.hv3-reservation {
    max-width: 940px;
    margin-left: auto;
    .reservation-wrap {
        .title {
            h3 {
                color: var(--themev3-txt-clr);
                font-family:var(--playFair);
            }
        }
        .input-fild {
            &:focus-within {
                input {
                    border-color: var(--themev3-bdr-clr);
                }
            }
            button {
                background: var(--themev3-bg-clr);
            }
        }
        .reservation-from {
            .nice-select {
                &:focus {
                    border-color:var(--themev3-bdr-clr);
                }
                &::before {
                    background: var(--themev3-bg-clr);
                }
            }
        }
    }
    .hv2-rsvvtn-content {
        .content h4 {
            color: var(--themev3-txt-clr);
            font-family: var(--playFair);
        }
        .contact {
            a {
                span {
                    background: var(--themev3-bg-clr);
                }
            }
        }
    }
}