
.hdr-search {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    height: 100vh;
    background: var(--white-bg-clr);
    z-index: 9999999;
    visibility: hidden;
    opacity: 0;
    &.active {
        opacity: 1;
        visibility: visible;
        transition: .2s ease-in;
    }
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04,
    .shape-05 {
        position: absolute;
        z-index: -1;
        img,
        svg {
            max-width: 100%;
            max-height: 100%;
        }
    }
    .shape-01 {
        top: 20%;
        left: 20%;
    }
    .shape-02 {
        top: 50%;
        left: 15%;
    }
    .shape-03 {
        bottom: 20%;
        left: 20%;
    }
    .shape-04 {
        top: 25%;
        right: 20%;
    }
    .shape-05 {
        bottom: 20%;
        right: 15%;
    }
}

.hdeClose {
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
    height: 50px;
    background: var(--theme-bg-clr);
    display: flex;
    align-items: center;
    justify-content: center;
    img,
    svg {
        color: var(--white-txt-clr);
        font-size: 30px;
    }
}

.hdrSrch-wrapper {
    max-width: 720px;
    margin: 0 auto;
}

.hdrSrch-input {
    position: relative;
    margin-right: 30px;
    &:focus-within {
        button {
            svg,
            img {
                color: var(--theme-bg-clr);
            }
        }
    }
    input {
        border-width: 0px 0px 1px 0px;
        background: transparent;
        padding: 10px 50px 10px 0px;
        font-size: 24px;
        font-weight: 300;
        
    }
    button {
        background: transparent;
        width: auto;
        img,
        svg {
            font-size: 24px;
            max-width: 100%;
            max-height: 100%;
            transition: all .3s ease-in-out;
            color: var(--body-txt-clr);
        }
    }
}

.hdrSrch-result {
    .title {
        margin: 30px 0px;
        h4 {
            font-size: 30px;
            font-weight: 700;
            font-family: var(--raleway);
            line-height: 1.3;
        }
    }
    .result {
        max-height: calc(100vh - 450px);
        overflow-y: auto;
        padding-right: 30px;
        &::-webkit-scrollbar {
            width: 8px;
        }
        &::-webkit-scrollbar-track {
            background-color: var(--light-gray-bg);
            box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            border-radius: 20px;
        }
        &::-webkit-scrollbar-thumb {
            background: var(--theme-bg-clr);
            border-radius: 20px;
        }
    }
}
.single-srch-result {
    padding: 30px;
    background: var(--ls-gray-bg);
    margin-bottom: 10px;
    &:last-child {
        margin-bottom: 0;
    }
    span {
        color: var(--font-txt-clr);
        font-size: 16px;
        line-height: 1;
        display: block;
        margin-bottom: 15px;
    }
    h5 {
        line-height: 1;
        a {
            color: var(--font-txt-clr);
            font-weight: 600;
            font-family: var(--raleway);
            line-height: 1.5;
            transition: all .4s ease-in-out;
            background-image: linear-gradient(#EA5455, #EA5455);
            background-repeat: no-repeat;
            background-position: left bottom;
            background-size: 0% 2px;
            text-decoration: none;

        }
    }
    &:hover {
        h5 {
            a {
                color: var(--theme-txt-clr);
                background-size: 100% 2px;
            }
        }
    }
}

@media screen and (max-width: 1350px) {
    .hdr-search {
        .shape-01 {
            top: 10%;
            left: 10%;
        }
        .shape-02 {
            left: 5%;
        }
        .shape-04 {
            top: 10%;
            right: 5%;
        }
        .shape-03 {
            bottom: 5%;
        }
    }
    .hdrSrch-result {
        .result {
            max-height: calc(100vh - 300px);
        }
    }
}
@media screen and (max-width: 1199px) {
    .hdr-search {
        .shape-01 {
            top: 5%;
            left: 5%;
        }
        .shape-05 {
            bottom: 15%;
            right: 5%;
        }
    }
}
@media screen and (max-width: 991px) {
    .hdr-search {
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04,
        .shape-05 {
            display: none;
        }
    }
}

@media screen and (max-width: 575px) {
    .hdeClose {
        width: 40px;
        height: 40px;
        img,
        svg {
            font-size: 24px;
        }
    }
    .hdrSrch-input {
        margin-right: 20px;
        input {
            font-size: 16px;
            height: 50px;
        }
        button {
            img,
            svg {
                font-size: 18px;
            }
        }
    }
    .hdrSrch-result {
        .title {
            margin: 20px 0px;
            h4 {
                font-size: 20px;
            }
        }
        .result {
            max-height: calc(100vh - 250px);
            padding-right: 15px;
        }
    }
    .single-srch-result {
        padding: 20px;
        span {
            font-size: 14px;
            margin-bottom: 10px;
        }
        h5 {
            a {
                font-size: 16px;
            }
        }
    }
}