
.profile-wrapper {
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    position: relative;
}
.progile-img {
    border-radius: 10px;
    overflow: hidden;
    margin-right: 70px;
}
.profile-content {
    .title {
        font-size: 24px;
        font-weight: 600;
        color: var(--body-txt-clr);
        line-height: 1;
        display: block;
        span {
            display: block;
            font-size: 16px;
            font-weight: 400;
            margin-top: 10px;
        }
    }
    ul {
        margin-top: 40px;
        li {
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0;
            }
            a {
                color: var(--font-txt-clr);
                font-size: 18px;
                font-weight: 500;
                position: relative;
                padding-left: 30px;
                line-height: 1.3;
                display: block;
                img,
                svg {
                    position: absolute;
                    left: 0;
                    top: 3px;
                    max-width: 18px;
                    color: var(--theme-txt-clr);
                }
            }
        }
    }
    .edit-info {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
        border: 1px solid var(--body-txt-clr);
        color: var(--body-txt-clr);
        font-size: 16px;
        font-weight: 500;
        line-height: 1;
        padding: 12px 15px;
        border-radius: 5px;
        transition: all .4s ease-in-out;
        img,
        svg {
            margin-right: 5px;
        }
        &:hover {
            background: var(--body-bg-clr);
            color: var(--white-txt-clr);
        }
    }
}

// Table Code
.mp-history-wrapper {
    max-width: 1000px;
    margin: 0 auto;
    .title {
        margin-bottom: 50px;
        h2 {
            font-size: 24px;
            font-weight: 400;
            line-height: 1;
            position: relative;
            padding-left: 55px;
            color: var(--theme-txt-clr);
            &::before,
            &::after {
                position: absolute;
                content: "";
                height: 2px;
                background: var(--theme-txt-clr);
                left: 0;
            }
            &::before {
                width: 35px;
                top: 2px;
            }
            &::after {
                width: 15px;
                bottom: 2px;
            }
        }
    }
} 
#mp-hstry-table {
    border-bottom: inherit !important;
    .table-header {
        tr {
            background: transparent;
            th {
                padding: 0px 0px 20px 0px;
                border-bottom: 1px dashed var(--theme-bdr-clr);
                font-size: 18px;
                font-weight: 600;
                line-height: 1.1;
                width: calc(100% / 4);
                color: var(--theme-txt-clr);
                &:nth-child(3),
                &:nth-child(4) {
                    text-align: center;
                }
                &::before, 
                &::after {
                    display: none;
                }
                &:last-child {
                    text-align: right;
                }
            }
        }
    }
    .table-body {
        tr {
            td {
                border-bottom: 1px solid var(--light-gray-bdr);
                padding: 30px 0px;
                &:last-child {
                    text-align: right;
                }
                &:nth-child(3),
                &:nth-child(4) {
                    text-align: center;
                }
                .pdct-info {
                    span {
                       font-size: 14px;
                       line-height: 1;
                       font-weight: 500;
                       color: var(--theme-txt-clr);
                    }
                    h4 {
                        font-size: 20px;
                        line-height: 1;
                        font-weight: 600;
                        font-family: var(--raleway);
                        color: var(--font-txt-clr);
                        margin-top: 10px;
                    }
                }
                .quantity,
                .status,
                .date,
                .price {
                    span {
                        font-size: 18px;
                        color: var(--body-txt-clr);
                        line-height: 1;
                    }
                }
                .price {
                    span {
                        font-weight: 600;
                    }
                }
            }
        }
    }
}


// Responsive
@media screen and (max-width: 991px) {
    .progile-img {
        margin-right: 30px;
    }
}

@media screen and (max-width: 767px) {
    .profile-content {
        .title {
            font-size: 20px;
            span {
                font-size: 15px;
            }
        }
        ul {
            margin-top: 20px;
            li {
                margin-bottom: 10px;
                a {
                    font-size: 16px;
                }
            }
        }
    }

    // Table Code
    #mp-hstry-table {
        .table-body {
            tr {
                td {
                    padding: 20px 0px 20px 40px;
                    .pdct-info {
                        h4 {
                            font-size: 16px;
                            margin-top: 7px;
                        }
                    }
                    &.dtr-control {
                        &::before {
                            left: 10px;
                            height: 20px;
                            width: 20px;
                            margin-top: 0px;
                            border: 0px;
                            line-height: 20px;
                            background-color: var(--theme-bg-clr);
                            transform: translateY(-50%);
                            box-shadow: none;
                        }
                    }
                    &.child {
                        text-align: left;
                        padding: 10px 0px;
                        background: var(--header-bg);
                        ul {
                            display: block;
                            text-align: left;
                            li {
                                padding-left: 40px;
                                span {
                                    &.dtr-title {
                                        margin-bottom: 5px;
                                        font-size: 17px;
                                        color: var(--theme-txt-clr);
                                    }
                                }
                                .quantity,
                                .status,
                                .date,
                                .price {
                                    span {
                                        font-size: 16px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 575px) {
    .profile-wrapper {
        flex-direction: column;
        align-items: flex-start;
    }
    .progile-img {
        max-width: 200px;
        width: 100%;
        margin: 0;
    }
    .profile-content {
        position: relative;
        margin-top: 30px;
        width: 100%;
        .edit-info {
            position: unset;
            display: inline-flex;
            margin-top: 20px;
        }
    }

    // Table Code
    .mp-history-wrapper {
        .title {
            margin-bottom: 30px;
            h2 {
                font-size: 20px;
            }
        }
    }
    #mp-hstry-table {
        .table-header {
            tr {
                th {
                    padding: 0px 0px 10px 0px;
                    font-size: 17px;
                }
            }
        }
    }
}

