
.counter-wrapper {
    padding: 75px 20px;
    .background-img {
        &::before {
            background: var(--counter-bg);
            mix-blend-mode: multiply;
        }
    }
}
.counters {
    max-width: 922px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
}
.single-counter {
    background: var(--sCounter-bg);
    border: 1px solid var(--light-gray-bdr);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 208px;
    padding: 15px;
    h2 {
        font-family: var(--raleway);
        font-weight: 700;
        font-size: 48px;
        line-height: 1;
        color: var(--white-txt-clr);
        span {
            font-family: var(--raleway);
        }
        &.plus {
            position: relative;
            &::before {
                position: absolute;
                content: url('https://api.iconify.design/dashicons/plus.svg?color=white&width=24&height=24');
                top: -15px;
                right: -15px;
            }
        }
    }
    p {
        font-size: 20px;
        line-height: 1;
        color: var(--white-txt-clr);
        margin-top: 10px;
    }
}

// Responsive 
@media screen and (max-width: 1199px) {
    .counter-wrapper {
        padding: 50px 100px;
    }
    .counters {
        grid-gap: 20px;
    }
    .single-counter {
        height: 150px;
        h2 {
            font-size: 34px;
            &.plus {
                &::before {
                    content: url('https://api.iconify.design/dashicons/plus.svg?color=white&width=18&height=18');
                    top: -10px;
                    right: -15px;
                }
            }
        }
        p {
            font-size: 18px;
        }
    }
}
@media screen and (max-width: 991px) {
    .counter-wrapper {
        padding: 50px 20px;
    }
}
@media screen and (max-width: 767px) {
    .counters {
        max-width: 400px;
        grid-template-columns: repeat(2, 1fr);
    }
}
@media screen and (max-width: 575px) {
    .counters {
        max-width: 300px;
        grid-gap: 15px;
    }
    .single-counter {
        height: 120px;
        h2 {
            font-size: 24px;
        }
        p {
            font-size: 16px;
            margin-top: 8px;
        }
    }
}