

.about-wrapper {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-thumb {
    max-width: 470px;
    width: 100%;
    margin-right: 40px;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 265px;
        height: 265px;
        background: var(--theme-bg-clr);
        bottom: 0;
        left: 0;
        z-index: -1;
    }
    .shape {
        position: absolute;
        left: 0;
        top: 27%;
        max-width: 63px;
        z-index: -1;
        img,
        svg {
            max-width: 100%;
            max-height: 100%;
        }
    }
    .thumb {
        padding: 0px 0px 35px 85px;
        img {
            width: 100%;
        }
    }
    
}
//about-thumb animation

.about-wrapper{
    .about-thumb{
        .anime-thumb-about-one{
            img{
                transition: 600ms cubic-bezier(0.4, 0, 0.2, 1);
                clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
            }
        }
        &:before{
            transition: 600ms cubic-bezier(0.4, 0, 0.2, 1);
            clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
        }
    }
    .anime-thumb-about-content{
        h4,p,ul li,a{
            transform: translateY(100px);
            transition: transform .5s ease, clip-path .8s ease;
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
            opacity: 0;
        }
    }
    &.show-animation{
        .about-thumb{
            .anime-thumb-about-one{
                img{
                    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
                }
            }
            &:before{
                transition-delay: .4s;
                clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
            }
        }
        .anime-thumb-about-content{
            p{
                transition-delay: .3s;
            }
            ul{
                li{
                    &:nth-child(1){
                        transition-delay: .4s;
                    }
                    &:nth-child(2){
                        transition-delay: .5s;
                    }
                    &:nth-child(3){
                        transition-delay: .6s;
                    }
                    &:nth-child(4){
                        transition-delay: .7s;
                    }
                }
                
            }
            a{
                transition-delay: .7s;
            }
            h4,p,ul li,a{
                transform: translateY(0px);
                opacity: 1;
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            }
        }
    }
}


.about-content {
    max-width: 540px;
    width: 100%;

    h4 {
        color: var(--theme-txt-clr);
        font-size: 24px;
        line-height: 1;
        font-weight: 400;
    }
    p {
        margin-top: 20px;
    }
    ul {
        margin-top: 20px;
        max-width: 460px;
        li {    
            padding-left: 17px;
            position: relative;
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                position: absolute;
                content: "";
                width: 7px;
                height: 7px;
                background: var(--theme-bg-clr);
                left: 0;
                top: 7px;
            }
        }
    }
}




@media screen and (max-width: 991px){
    .about-thumb {
        margin-right: 30px;
        &::before {
            width: 200px;
            height: 200px;
        }
        .shape {
            top: 28%;
            max-width: 30px;
        }
        .thumb {
            padding: 0px 0px 30px 40px;
        }
    }
}

@media screen and (max-width: 767px) {
    .about-wrapper {
        flex-direction: column;
    }
    .about-thumb {
        margin-right: 0;
        max-width: 500px;
        .shape {
            display: none;
        }
    }
    .about-content {
        margin-top: 40px;
        max-width: 500px;
    }
}

@media screen and (max-width: 575px) {
    
    .about-content {
        margin-top: 30px;
        h4 {
            font-size: 22px;
        }
        p {
            margin-top: 15px;
        }
        ul {
            margin-top: 15px;
        }
    }
}