
.footer-v4 {
    background: var(--ftr-v4-bg);
    .shape {
        background: var(--white-bg-clr);
        position: absolute;
        width: 100%;
        height: 150px;
        &::before,
        &::after {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background-image: url('../img/shape/footer-shape.png');
            top: 0;
            left: 0;
            background-size: 2000px 100%;
        }
        &::before {  
            animation: animate 10s linear infinite reverse;
        }
        &::after {
            animation: animate_2 10s linear infinite reverse;
            opacity: 0.7;
        }
    }
    .footer-wrapper {
        padding-top: 150px;
    }
    .ftr-logo {
        p {
            color: var(--font-txt-clr);
        }
    }
    .ftr-single-item {
        .title {
            h4 {
                color: var(--themev4-txt-clr);
            }
        }
    }
    .ftrItems {
        ul {
            li {
                a {
                    color: var(--font-txt-clr);
                    &::before {
                        background: var(--font-txt-clr);
                    }
                }
            }
        }
    }
    .ftrSitemap {
        .ftrItems {
            ul {
                li {
                    a {
                        &:hover {
                            color: var(--themev4-txt-clr);
                            &::before {
                                background: var(--themev4-bg-clr);
                            }
                        }
                    }
                }
            }
        }
    }
    .ftraddress {
        .ftrItems {
            ul {
                li {
                    a {
                        img, 
                        svg {
                            color: var(--themev4-txt-clr);
                        }
                    }
                }
            }
        }
    }

    .social-icon {
        .button {
            background: var(--font-txt-clr);
            &:hover {
                background: var(--themev4-bg-clr);
                .icon {
                    color: var(--white-txt-clr);
                }
            }
            .icon {
                color: var(--themev4-txt-clr);
                color: var(--white-txt-clr);
            }
        }
    }

    .input-fild {
        input {
            border-color: var(--themev4-bdr-clr);
            color: var(--font-txt-clr);
            &::-moz-placeholder {
                color: var(--font-txt-clr);
            }
            &::placeholder {
                color: var(--font-txt-clr);
            }
            &:focus {
                border-color: var(--themev4-bdr-clr);
            }
        }
        button {
            background: var(--themev4-bg-clr);
        }
    }

    #scrollUp {
        background: var(--themev4-bg-clr);
    }
}


@keyframes animate {
    0% {
       background-position-x: 2000px;
    }
    100% {
       background-position-x: 0px;
    }
}

@keyframes animate_2 {
    0% {
       background-position-x: 0px;
    }
    100% {
       background-position-x: 2000px;
    }
}

// Responsive
@media screen and (max-width: 575px) {
    .footer-v4 {
        .shape {
            height: 100px;
            z-index: -1;
            &::before,
            &::after {
                background-size: 1000px 100%;
            }
        }
        .footer-wrapper {
            padding-top: 70px;
        }
    }
    @keyframes animate {
        0% {
           background-position-x: 1000px;
        }
        100% {
           background-position-x: 0px;
        }
    }
    
    @keyframes animate_2 {
        0% {
           background-position-x: 0px;
        }
        100% {
           background-position-x: 1000px;
        }
    }
}