

@media screen and (max-width: 1199px) {
    .small-ftr-wrapper {
        .ftr-logo {
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
        }
    }
}
@media screen and (max-width: 575px) {
    .small-ftr-wrapper {
        .ftr-logo {
            margin: 0;
            display: block;
            text-align: left;
        }
    }
}