
.about-chef-area {
    .background-img {
        &::before {
            background: var(--ab-chef-bg);
            mix-blend-mode: multiply;
        }
    }
}

.about-chef-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
    max-width: 1100px;
    margin: 0 auto;
}

.singleAbout-chef {
    position: relative;
    z-index: 1;
    overflow: hidden;
    &:hover {
        .content {
            height: 150px;
            .social {
                a {
                    &:nth-child(1),
                    &:nth-child(2),
                    &:nth-child(3) {
                        transform: translateY(0px);
                        opacity: 1;
                    }
                }
            }
            .title {
                a,
                span {
                    transform: translateY(0);
                    opacity: 1;
                }
            }
        }
    }
    .img {
        img {
            width: 100%;
            height: 100%;
        }
    }
    .content {
        position: absolute;
        height: 0px;
        width: 100%;
        left: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        clip-path: ellipse(70% 100% at 50% 100%);
        overflow: hidden;
        z-index: 1;
        transition: all .3s ease-in-out;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: var(--theme-bg-clr);
            opacity: 0.9;
            bottom: 0;
            left: 0;
            z-index: -1;
        }
        .social {
            display: flex;
            a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 34px;
                height: 34px;
                font-size: 16px;
                color: var(--white-txt-clr);
                border-radius: 50%;
                overflow: hidden;
                margin: 0px 5px;
                position: relative;
                &:nth-child(1),
                &:nth-child(2),
                &:nth-child(3) {
                    transform: translateY(50px);
                    opacity: 0;
                    transition: all .3s ease-in-out;
                }
                &:nth-child(1) {
                    transition-duration: .2s;
                }
                &:nth-child(2) {
                    transition-duration: .4s;
                }
                &:nth-child(3) {
                    transition-duration: .5s;
                }
                &:hover {
                    &::before {
                        clip-path: circle(70.7% at 50% 50%);
                        opacity: 1;
                    }
                }
                &::before {
                    position: absolute;
                    content: "";
                    background: var(--as-icon-bg);
                    width: 100%;
                    height: 100%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    clip-path: circle(0% at 50% 50%);
                    opacity: 0;
                    transition: all .4s ease-in-out;

                }
            }
        }
        .title {
            text-align: center;
            margin-top: 15px;
            a {
                display: block;
                color: var(--white-txt-clr);
                font-size: 20px;
                line-height: 1;
                font-weight: 600;
                transform: translateY(30px);
                opacity: 0;
                transition-duration: .3s;
                transition: all .3s ease-in-out;
            }
            span {
                color: var(--white-txt-clr);
                font-size: 16px;
                font-weight: 400;
                margin-top: 5px;
                display: block;
                transform: translateY(30px);
                opacity: 0;
                transition-duration: .3s;
                transition: all .4s ease-in-out;
            }
        }
    }
}


// Responsive
@media screen and (max-width: 991px) {
    .about-chef-wrapper {
        grid-template-columns: 1fr 1fr;
    }
}
@media screen and (max-width: 767px) {
    .about-chef-wrapper {
        grid-gap: 20px;
    }
}
@media screen and (max-width: 575px) {
    .about-chef-wrapper {
        grid-template-columns: 1fr;
        max-width: 400px;
    }
    .singleAbout-chef {
        .content {
            .title {
                margin-top: 10px;
                a {
                    font-size: 18px;
                }
                span {
                    font-size: 15px;
                }
            }
        }
    }
}