 

.blog-dtls-banner-area {
    padding-top: 430px;
    &::before {
        background: var(--blog-detls);
    }
}
.bdb-content {
    max-width: 865px;
    margin: 0 auto;
    h2 {
        color: var(--white-txt-clr);
        font-size: 48px;
        font-weight: 800;
        line-height: 1;
        font-family: var(--raleway);
    }
    ul {
        display: flex;
        align-items: center;
        margin-top: 30px;
        li {
            margin-right: 30px;
            &:last-child {
                margin-right: 0;
            }
            a {
                font-size: 18px;
                color: var(--white-txt-clr);
                font-weight: 500;
                line-height: 1;
                img,
                svg {
                    margin-right: 10px;
                }
            }
        }
    }
}

// Blog Details
.blog-details-wrapper {
    max-width: 865px;
    margin: 30px auto 0px;
    p {
        color: var(--font-txt-clr);
        font-size: 18px;
    }
    .gallery {
        display: flex;
        img {
            margin-right: 20px;
            width: calc(100% / 2 - 10px);
            &:last-child {
                margin-right: 0px;
            }
        }
    }
    .quite {
        background: var(--ls-gray-bg);
        padding: 50px;
        .content {
            display: flex;
            align-items: center;
            max-width: 520px;
            margin: 0 auto;
            img,
            svg {
                max-width: 34px;
                max-height: 34px;
                width: 100%;
                path {
                    fill: var(--theme-txt-clr);
                }
            }
            h4 {
                font-size: 24px;
                line-height: 1.3;
                margin-left: 25px;
                padding-left: 25px;
                border-left: 2px dashed var(--theme-bdr-clr);
            }
        }
    }
    h6 {
        font-size: 24px;
        font-family: var(--raleway);
        line-height: 1.4;
        font-weight: 600;
        max-width: 767px;
    }
    ul {
        li {
            color: var(--font-txt-clr);
            font-size: 16px;
            font-weight: 600;
            line-height: 1.2;
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0;
            }
            img,
            svg {
                color: var(--theme-txt-clr);
                margin-right: 10px;
                font-size: 20px;
            }
        }
    }
}
.bd-share {
    background: var(--ls-gray-bg);
    padding: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .profile {
        margin-right: 30px;
        display: flex;
        align-items: center;
        .img {
            border-radius: 50%;
            padding: 5px;
            border: 1px solid var(--light-gray-bdr);
            margin-right: 20px;
            overflow: hidden;
            img{
                border-radius: 50%;
            }
        }
        .title {
            a {
                display: block;
                font-size: 20px;
                color: var(--font-txt-clr);
                font-weight: 600;
                line-height: 1.3;
                white-space: nowrap;
            }
            span {
                font-size: 14px;
                line-height: 1;
                color: var(--font-txt-clr);
                display: block;
                margin-top: 7px;
            }
        }
    }
    .share {
        .title {
            span {
                color: var(--font-txt-clr);
                font-weight: 600;
                font-size: 16px;
                line-height: 1;
            }
        }
        .social {
            margin-top: 20px;
            display: flex;
            a {
                display: flex;
                align-items: center;
                font-size: 14px;
                color: var(--font-txt-clr);
                font-weight: 600;
                line-height: 1;
                padding: 6px 10px;
                background: var(--light-gray-bg);
                transition: all .4s ease-in-out;
                margin-right: 10px;
                &:last-child {
                    margin-right: 0;
                }
                img,
                svg {
                    color: var(--body-txt-clr);
                    margin-right: 10px;
                    font-size: 18px;
                    transition: all .4s ease-in-out;
                }
                &:hover {
                    background: var(--theme-bg-clr);
                    color: var(--white-txt-clr);
                    img,
                    svg {
                        color: var(--white-txt-clr);
                    }
                }
            }
        }
    }
}

// Comment Section
.blog-comment-wrapper {
    max-width: 865px;
    margin: 0 auto;
    .title {
        margin-bottom: 30px;
        h4 {
            font-size: 36px;
            font-weight: 400;
            color: var(--theme-txt-clr);
            line-height: 1;
        }
        span {
            margin-top: 25px;
            display: block;
            width: 150px;
            height: 2px;
            background: var(--theme-bg-clr);
            &:last-child {
                width: 70px;
                margin-top: 15px;
            }
        }
    }
    .textCenter {
        display: flex;
        align-items: flex-end;
        grid-gap: 40px;
        .input {
            width: 50%;
            input {
                padding: 20px;
            }
        }
        .message {
            width: 50%;
            textarea {
                padding: 20px;
                resize: none;
                height: 178px;
                margin-bottom: -10px;
            }
        }
    }
}

// Related Post
.rltd-post-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
    .blogContent {
        max-width: 370px;
        max-height: 200px;
        padding: 30px;
        h4 {
            a {
                font-size: 18px;
                margin-top: 10px;
            }
        }
        .button {
            margin-top: 10px;
        }
    }
}

// Responsive 
@media screen and (max-width: 1199px) {
    // Related Post
    .rltd-post-wrapper {
        grid-template-columns: 1fr 1fr;
        
    }
}
@media screen and (max-width: 991px) {
    .blog-dtls-banner-area {
        padding-top: 250px;
    }
    .bdb-content {
        h2 {
            font-size: 40px;
        }
    }
    // Comment Section
    .blog-comment-wrapper {
        .textCenter {
            grid-gap: 30px;
        }
    }
}
@media screen and (max-width: 767px) {
    .blog-dtls-banner-area {
        padding-top: 200px;
    }
    .bdb-content {
        h2 {
            font-size: 34px;
        }
        ul {
            margin-top: 20px;
            li {
                margin-right: 20px;
                a {
                    font-size: 16px;
                }
            }
        }
    }
    // Blog Details
    .blog-details-wrapper {
        p {
            font-size: 16px;
        }
    }
    .bd-share {
        align-items: flex-start;
        flex-direction: column;
        .profile {
            margin-right: 30px;
        }
        .share {
            margin-top: 20px;
        }
    }

    // Comment Secton
    .blog-comment-wrapper {
        .textCenter {
            align-items: flex-start;
            grid-gap: 20px;
            flex-direction: column;
            .input {
                width: 100%;
            }
            .message {
                width: 100%;
            }
        }
    }

    // Related Post
    .rltd-post-wrapper {
        grid-template-columns: 1fr;
        grid-gap: 20px;
        max-width: 550px;
        margin: 0 auto;
    }
       
}
@media screen and (max-width: 575px) {
    .blog-dtls-banner-area {
        padding-top: 150px;
    }
    .bdb-content {
        h2 {
            font-size: 24px;
            line-height: 1.2;
        }
        ul {
            margin-top: 10px;
            flex-wrap: wrap;
            li {
                margin-right: 15px;
                a {
                    font-size: 16px;
                    margin: 5px 0px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                    img,
                    svg {
                        margin-right: 5px;
                    }
                }
            }
        }
    }

    // Blog Details
    .blog-details-wrapper {
        margin-top: 20px;
        .gallery {
            flex-direction: column;
            img {
                margin-right: 0px;
                margin-bottom: 20px;
                width: 100%;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        .quite {
            padding: 30px 20px;
            .content {
                h4 {
                    font-size: 18px;
                    margin-left: 15px;
                    padding-left: 15px;
                }
            }
        }
        h6 {
            font-size: 18px;
        }
        ul {
            li {
                font-size: 15px;
                margin-bottom: 15px;
                img,svg {
                    margin-right: 5px;
                }
            }
        }
    }
    .bd-share {
        padding: 20px;
        .profile {
            .img {
                margin-right: 10px;
                max-width: 80px;
                width: 100%;
            }
            .title {
                a {
                    font-size: 18px;
                }
            }
        }
        .share {
            .social {
                margin-top: 0px;
                flex-wrap: wrap;
                a {
                    margin: 10px 10px 0px 0px;
                }
            }
        }
    }

    // Comment Secton
    .blog-comment-wrapper {
        .title {
            h4 {
                font-size: 24px;
            }
            span {
                margin-top: 20px;
                width: 100px;
                &:last-child {
                    width: 50px;
                    margin-top: 10px;
                }
            }
        }
        .textCenter {
            .message {
                textarea {
                    height: 140px;
                }
            }
        }
    }
}