.blog-title {
    max-width: 1305px;
    margin: 0 auto 20px;
    span {
        font-size: 20px;
        font-weight: 600;
        color: var(--font-txt-clr);
        line-height: 1;
    }
}
.blog-wrapper {
    max-width: 1304px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
}

.single-blog {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    &:hover {
        .blogImg {
            img {
                transform: scale(1.1);
                filter: saturate(100%);
            }
        }
        .blogContent {
            background: var(--white-bg-clr);
            .date {
                span {
                    img,
                    svg {
                        color: var(--theme-bg-clr);
                    }
                }
                a {
                    background: var(--theme-bg-clr);
                }
            }
            .button {
                a {
                    background: var(--theme-bg-clr);
                }
            }
        }
    }
}

.blogImg {
    img {
        width: 100%;
        transition: all .3s ease-in-out;
    }
}

.blogContent {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 90%;
    max-width: 466px;
    max-height: 247px;
    background: var(--blog);
    backdrop-filter: blur(20px);
    padding: 40px;
    transition: all .3s ease-in-out;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 5px;
    .date {
        display: flex;
        align-items: center;
        span {
            display: flex;
            align-items: center;
            font-size: 16px;
            color: var(--body-txt-clr);
            img,
            svg {
                font-size: 18px;
                margin-right: 10px;
                transition: all .3s ease-in-out;
            }
        }
        a {
            background:var(--themev2-bg-clr);
            color: var(--white-txt-clr);
            font-size: 15px;
            font-weight: 500;
            line-height: 1;
            padding: 7px 10px;
            border-radius: 40px;
            margin-left: 20px;
            display: inline-block;
            transition: all .3s ease-in-out;
        }
    }
    h4 {
        line-height: 1;
        a {
            color: var(--font-txt-clr);
            font-size: 20px;
            font-weight: 600;
            font-family: var(--raleway);
            display: block;
            margin-top: 15px;
            line-height: 1.5;
        }
    }
    .button {
        margin-top: 20px;
        a {
            padding: 12px 20px;
            background: var(--body-bg-clr);
            transition: all .3s ease-in-out;
        }
    }
}

@keyframes  blog {
    0% {
        transform: scale(1) rotate(0deg);
    }
    50% {
        transform: scale(1.4) rotate(15deg);
    }
    100% {
        transform: scale(1) rotate(0deg);
    }
}

// Pagination
.blog-pagination {
    max-width: 1304px;
    margin: 50px auto 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--ls-gray-bg);
    a {
        padding: 16px 5px;
        margin: 0px 20px;
        font-size: 24px;
        font-weight: 500;
        line-height: 1;
        color: var(--body-txt-clr);
        position: relative;
        border-bottom: 2px solid transparent;
        transition: all .3s ease-in-out;
        &:hover {
            border-color: var(--theme-bdr-clr);
            color: var(--theme-txt-clr);
        }
    }
}

// Responsive 
@media screen and (max-width: 1199px) {
    .blogContent {
        padding: 30px;
        h4 {
            a {
                font-size: 18px;
            }
        }
    }
}

@media screen and (max-width: 991px) {
    .blog-wrapper {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 767px) {
    .blog-wrapper {
        grid-gap: 20px;
    }
    // Pagination
    .blog-pagination {
        margin: 30px auto 0px;
        a {
            padding: 15px 5px;
            margin: 0px 10px;
            font-size: 18px;
        }
    }
}

@media screen and (max-width: 575px) {
    .blog-title {
        span {
            font-size: 18px;
        }
    }
    .blogContent {
        width: 90%;
        height: auto;
        max-width: 100%;
        max-height: 100%;
        padding: 20px;
    }
    .blogContent {
        .date {
            span {
                font-size: 14px;
                img,svg {
                    font-size: 16px;
                    margin-right: 5px;
                }
            }
            a {
                font-size: 14px;
                padding: 5px 8px;
                border-radius: 15px;
                margin-left: 10px;
            }
        }
        h4 {
            a {
                font-size: 16px;
                margin-top: 10px;
            }
        }
        .button {
            margin-top: 10px;
            a {
                padding: 12px 15px;
            }
        }
    }
}

