.chef-profile-area {
    position: relative;
    z-index: 1;
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04,
    .shape-05 {
        position: absolute;
        z-index: -1;
    }
    .shape-01 {
        top: 10%;
        left: 15%;
    }
    .shape-04 {
        bottom: 15%;
        left: 10%;
    }
    .shape-02 {
        top: 10%;
        right: 20%;
    }
    .shape-03 {
        top: 40%;
        right: 10%;
    }
    .shape-05 {
        bottom: 10%;
        right: 20%;
    }
}
.chef-profile-wrapper {
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chefProfile {
    max-width: 440px;
    width: 100%;
    margin-right: 30px;
    img {
        border-radius: 50%;
    }
}
.chefContent {
    max-width: 600px;
    width: 100%;
   > a {
        font-size: 24px;
        font-weight: 700;
        color: var(--font-txt-clr);
        line-height: 1;
        display: block;
        span {
            display: block;
            font-size: 18px;
            line-height: 1;
            font-weight: 400;
            margin-top: 10px;
        }
    }
    p {
        font-size: 16px;
        color: var(--font-txt-clr);
        margin-top: 30px;
    }
    .social-icon {
        margin-top: 30px;
        a {
            border: 1px solid var(--theme-bdr-clr);
        }
    }
}

// Brand 
.cd-brand-wrapper {
    max-width: 1270px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 50px 80px;
}


@media screen and (max-width: 1350px) {
    .chef-profile-area {
        .shape-01 {
            top: 5%;
            left: 5%;
        }
        .shape-04 {
            bottom: 5%;
            left: 5%;
        }
        .shape-03 {
            display: none;
        }
    }
}
@media screen and (max-width: 1199px) {
    .chefProfile {
        max-width: 300px;
    }
    .cd-brand-wrapper {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 991px) {
    .chef-profile-area {
        .shape-02 {
            top: 5%;
            right: 5%;
        }
        .shape-05 {
            bottom: 5%;
            right: 5%;
        }
    }
    // Brand 
    .cd-brand-wrapper {
        grid-gap: 50px;
    }
}

@media screen and (max-width: 767px) {
    .chef-profile-area {
        .shape-01,
        .shape-04,
        .shape-02,
        .shape-05 {
            display: none;
        }
        
    }
    .chef-profile-wrapper {
        flex-direction: column;
        text-align: center;
    }
    .chefProfile {
        max-width: 200px;
        margin-right: 0;
        margin-bottom: 20px;
    }
    .chefContent {
       > a {
            font-size: 20px;
            span {
                font-size: 16px;
                margin-top: 7px;
            }
        }
        p {
            font-size: 16px;
            margin-top: 20px;
        }
        .social-icon {
            margin-top: 20px;
            justify-content: center;
        }
    }
    // Brand 
    .single-cd-brand {
        max-width: 150px;
    }
}
@media screen and (max-width: 575px) {
    .chefProfile {
        max-width: 150px;
    }
    // Brand 
    .cd-brand-wrapper {
        grid-gap: 30px;
    }
    .single-cd-brand {
        max-width: 100px;
    }
}