

.subscribe-wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 970px;
    margin: 0 auto;

    .section-title {
        margin: 0px 30px 0px 0px;
        width: 100%;
        text-align: left;
        span {
            margin: 15px 0px 0px;
        }
    }
}

.sb-from {
    max-width: 400px;
    width: 100%;
    .input-fild {
        input{
            background: transparent;
            &:focus {
                border-color: var(--font-txt-clr);
            }
        }
        button {
            background: var(--font-bg-clr);
        }
    }
    .sb-btn {
        background: var(--theme-bg-clr);
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
    }
}

// Responsive 
@media screen and (max-width: 767px) {
    .subscribe-wrapper {
        flex-direction: column;
        max-width: 500px;
        .section-title {
            text-align: center;
            max-width: 100%;
            margin: 0;
            span {
                margin: 10px auto 0px;
            }
        }
    }
    .sb-from {
        margin: 20px auto 0px;
        max-width: 100%;
        .sb-btn {
            margin-top: 10px;
        }
    }
}