.foodDetails-area {
    position: relative;
    z-index: 1;
    .shape {
        position: absolute;
        max-width: 490px;
        top: 50%;
        transform: translateY(-50%);
        right: 10%;
        z-index: -1;
        img,svg {
            max-width: 100%;
            max-height: 100%;
        }
    }
}

.foodDetails-banner {
    .breadcrumb {
        justify-content: center;
        margin: 0;
        margin-top: 40px;
        li.breadcrumb-item {
            font-weight: 600;
            font-size: 18px;
            a {
                color: var(--body-txt-clr);
            }
            &.active {
                color: var(--theme-txt-clr);
            }
        }
    }
}

// Slider 
.foodDetails-wrapper {
    display: flex;
    max-width: 1100px;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}
.foodDetails-slider-wrap {
    max-width: 555px;
    width: 100%;
    margin-right: 60px;
    position: relative;
    .price {
        position: absolute;
        right: -45px;
        top: 30px;
        content: "";
        width: 94px;
        height: 94px;
        background: var(--theme-bg-clr);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 99;
        span {
            color: var(--white-txt-clr);
            font-family: var(--lobster);
            font-size: 30px;
            line-height: 1;
        }
    }
}
.foodDetails-slider {
    &.owl-carousel {
        padding-bottom: 137px;
        width: auto;
        div {
            width: 100%;
        }
        .owl-item {
            .single-item {
                width: calc(100% - 2px);
            }
            img {
                width: 100%;
                height: 100%;
                border-radius: 20px;
            }
        }
        .owl-dots {
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 117px;
            left: 0;
            display: flex;
            .owl-dot {
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                margin-right: 20px;
                object-fit: cover;
                width: calc(100% / 3 - 16px) !important;
                height: calc(100% - 2px) !important;
                border-radius: 20px;
                &:last-child {
                    margin-right: 0;
                }
            }
        }
        
    }
}

.foodDetails-slider-content {
    max-width: 446px;
    width: 100%;
    > .title {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid var(--light-gray-bdr);
        h3 {
            color: var(--theme-txt-clr);
            line-height: 1.3;
        }
        p {
            margin-top: 10px;
        }
    }
    .list {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid var(--light-gray-bdr);
        ul {
            li {
                font-weight: 500;
                line-height: 1.3;
                padding-left: 17px;
                position: relative;
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 0;
                }
                &::before {
                    position: absolute;
                    content: "";
                    width: 7px;
                    height: 7px;
                    background: var(--theme-bg-clr);
                    left: 0;
                    top: 5px;
                }
            }
        }
    }
    .quantity {
        display: flex;
        align-items: center;
        .title {
            margin-right: 30px;
            span {
                font-size: 18px;
                font-weight: 600;
                line-height: 1;
                color: var(--theme-txt-clr);
                display: block;
            }
        }
        .inc-dec {
            display: flex;
            align-items: center;
            input {
                text-align: center;
                height: 30px;
                width: 40px;
                color: var(--font-txt-clr);
                border: 0;
                outline: none;
                font-size: 18px;
                font-weight: 600;
            }
            .button {
                background-color: var(--light-gray-bdr);
                height: 32px;
                width: 30px;
                display: flex;
                align-items: center;
                justify-content: space-evenly;
                line-height: 1;
                button {
                    color: var(--font-txt-clr);
                    font-size: 16px;
                }
            }
        }
    }

    > .button {
        margin-top: 30px;
        a {
            background: var(--theme-bg-clr);
            border: 1px solid transparent;
            img,
            svg {
                margin-right: 8px;
            }
            &:hover {
                border-color: var(--theme-bg-clr);
                color: var(--theme-txt-clr);
                background: var(--white-bg-clr);
            }
        }
    }
}


// Food Description And Review
.food-decption-wrapper {
    max-width: 1100px;
    margin: 40px auto 0px;
}

.fd-dcptn-tab-navs {
    max-width: 720px;
    border-bottom: 1px solid var(--theme-bdr-clr);
    padding-bottom: 30px;
    .nav-tabs {
        border: 0;
        .nav-link {
            border: 0 !important;
            font-size: 24px;
            font-weight: 500;
            line-height: 1;
            color: var(--font-txt-clr);
            border-radius: 0;
            display: block;
            padding: 0;
            transition: all .4s ease-in-out;
            margin-right: 100px;
            &:last-child {
                margin-right: 0;
            }
            &:hover,
            &.active {
                color: var(--theme-txt-clr);
            }
        }
    }
}

.fd-tab-content {
    margin-top: 25px;
}

.fd-description {
    max-width: 720px;   
    p {
        color: var(--font-txt-clr);
        &.pd-2 {
            max-width: 540px;
        }
    }
    ul {
        max-width: 450px;
        li {
            font-weight: 500;
            padding-left: 17px;
            position: relative;
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                position: absolute;
                content: "";
                width: 7px;
                height: 7px;
                background: var(--theme-bg-clr);
                left: 0;
                top: 8px;
            }
        }
    }
}

// Review
.fd-review {
    padding-bottom: 40px;
    .owl-dots {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        .owl-dot {
            background: var(--light-gray-bdr);
            width: 10px;
            height: 10px;
            border-radius: 50%;
            margin: 0px 10px;
            transition: all .3s ease-in-out;
            &.active {
                background: var(--theme-bg-clr);
            }
        }
    }
}

.single-review {
    position: relative;
    z-index: 1;
    padding: 30px;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: var(--ls-gray-bg);
        opacity: 0.9;
        z-index: -1;
        top: 0;
        left: 0;
    }
    a {
        font-size: 20px;
        font-weight: 600;
        color: var(--font-txt-clr);
        line-height: 1;
        display: block;
        span {
            font-size: 14px;
            font-weight: 400;
            color: var(--body-txt-clr);
            margin-top: 10px;
            line-height: 1;
            display: block;
        }
    }
    p {
        margin-top: 10px;
        color: var(--font-txt-clr);
    }
    ul {
        margin-top: 10px;
        display: flex;
        li {
            margin-right: 6px;
            color: var(--review-star-clr);
            &:last-child {
                margin-right: 0;
            }
        }
    }
}


// Responsive 
@media screen and (max-width: 1350px) {
    .foodDetails-area {
        .shape {
            max-width: 300px;
            right: 5%;
        }
    }
}
@media screen and (max-width: 1199px) {
    .foodDetails-area {
        .shape {
            max-width: 200px;
        }
    }
}
@media screen and (max-width: 991px) {
    .foodDetails-slider-wrap {
        max-width: 400px;
        margin-right: 30px;
        .price {
            border-radius: 0px 20px 0px 20px;
            right: 3px;
            top: 0;
        }
    }
    .fd-review {
        grid-gap: 20px;
    }
}
@media screen and (max-width: 767px) {
    .foodDetails-wrapper {
        flex-direction: column;
        max-width: 550px;
    }
    .foodDetails-slider-wrap {
        margin-right: 0;
        max-width: 100%;
        .price {
            width: 60px;
            height: 60px;
            border-radius: 0px 10px 0px 10px;
            span {
                font-size: 24px;
            }
        }
    }
    .foodDetails-slider {
        &.owl-carousel {
            .owl-item {
                img {
                    border-radius: 10px;
                }
            }
        }
    }
    .foodDetails-slider-content {
        margin-top: 30px;
        max-width: 100%;
    }
    .food-decption-wrapper {
        max-width: 550px;
    }
    .fd-dcptn-tab-navs {
        padding-bottom: 20px;
        .nav-tabs {
            .nav-link {
                font-size: 20px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .foodDetails-area {
        .shape {
            display: none;
        }
    }
    .foodDetails-banner {
        .breadcrumb {
            margin-top: 20px;
            li.breadcrumb-item {
                font-size: 16px;
            }
        }
    }
    .foodDetails-slider {
        &.owl-carousel {
            padding-bottom: 90px;
            .owl-dots {
                height: 80px;
                .owl-dot {
                    margin-right: 10px;
                    border-radius: 10px;
                }
            }
        }
    }
    .foodDetails-slider-content {
        margin-top: 20px;
        > .title {
            padding-bottom: 15px;
            margin-bottom: 15px;
            h3 {
                font-size: 24px;
            }
        }
        .list {
            padding-bottom: 15px;
            margin-bottom: 15px;
        }
        >.button {
            margin-top: 20px;
        }
    }

    .food-decption-wrapper {
        margin: 30px auto 0px;
    }
    .fd-dcptn-tab-navs {
        padding-bottom: 15px;
        .nav-tabs {
            .nav-link {
                font-size: 18px;
                margin-right: 30px;
            }
        }
    }
    .fd-tab-content {
        margin-top: 15px;
    }
    .fd-review {
        padding-bottom: 30px;
        .owl-dots {
            .owl-dot {
                margin: 0px 5px;
            }
        }
    }
    .single-review {
        padding: 20px;
        a {
            font-size: 18px;
            span {
                margin-top: 5px;
            }
        }
    }
}
