
.platter-menu-wrapper {
    background: var(--food-menu-v2);
    backdrop-filter: blur(40px);  
    padding: 100px 50px;
}

.platterMenu-top-bar {
    max-width: 1140px;
    margin: 0 auto;
    border-bottom: 1px solid var(--theme-bdr-clr);
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.pmt-selectBox {
    flex: 1;
    display: flex;
    align-items: center;
}
.pmt-selectBox,
.platter-content {
    .label-check {
        position: relative;
        user-select: none;
        cursor: pointer;
        margin-right: 50px;
        &:last-child {
            margin-right: 0;
        }

        input[type="radio"] {
            display: none;
            &:checked {
                ~ .name {
                    color: var(--theme-txt-clr);
                }
                ~ .box {
                    border-color: var(--theme-bdr-clr);
                    &::before {
                        opacity: 1;
                    }
                }
            }
        }
        .name {
            font-size: 20px;
            line-height: 1.3;
            padding-left: 35px;
            transition: all .3s ease-in-out;
            display: block;
            font-family: var(--lobster);
            color: var(--font-txt-clr);
        }
        .box {
            width: 20px;
            height: 20px;
            border: 2px solid var(--font-bdr-clr);
            border-radius: 2px;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            position: absolute;
            transition: all .3s ease-in-out;
            &::before {
                position: absolute;
                content: "";
                width: 6px;
                height: 12px;
                border-width: 0px 1.5px 1.5px 0px;
                border-color: var(--theme-bg-clr);
                border-style: solid;
                top: 1px; 
                left: 50%;
                transform: translateX( -50%) rotate(45deg);
                opacity: 0;
                transition: all .3s ease-in-out;
            }
        }
    }
}

.pmt-optionSelect {
    max-width: 245px;
    width: 100%;
    margin-left: 30px;
    .nice-select {
        background: var(--white-bg-clr);
        border: 1px solid transparent;
        color: var(--font-txt-clr);
        font-weight: 500;
        font-size: 16px;
        height: 50px;
        .option {
            font-size: 15px;
        }
    }
}

// Platter Item
.platter-wrapper {
    max-width: 630px;
    margin: 40px auto 0px;
}

.single-platter {
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    justify-content: space-between;
    &:last-child {
        margin-bottom: 0;
    }
    &:nth-child(even) {
        flex-direction: row-reverse;
        .platter-img {
            margin-right: 0;
            margin-left: 40px;
            border-color: var(--body-bdr-clr);
            .price {
                background: var(--body-bg-clr);
            }
        }
    }
}
.platter-img {
    position: relative;
    border: 6px solid var(--theme-bdr-clr);
    border-radius: 50%;
    width: 320px;
    height: 320px;
    margin-right: 40px;
    img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
    .price {
        position: absolute;
        width: 90px;
        height: 90px;
        border-radius: 50%;
        border: 6px solid var(--white-bdr-clr);
        background: var(--theme-bg-clr);
        display: flex;
        align-items: center;
        justify-content: center;
        right: -10px;
        top: -10px;
        h4 {
            color: var(--white-txt-clr);
            line-height: 1;
        }
    }
}

.platter-content {
    .label-check {
        margin: 0;
        margin-bottom: 20px;
        input[type="checkbox"] {
            display: none;
            &:checked {
                ~ .name {
                    color: var(--font-txt-clr);
                }
                ~ .box {
                    border-color: var(--theme-bdr-clr);
                    &::before {
                        opacity: 1;
                    }
                }
            }
        }
    }
    ul {
        padding-left: 20px;
        li {
            list-style: auto;
            font-weight: 600;
            font-size: 18px;
            color: var(--body-txt-clr);
            line-height: 1;
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0;
            }
            a {
                color: var(--body-txt-clr);
            }
        }
    }
}

// Total Price
.platterTotal-price {
    max-width: 1140px;
    margin: 40px auto 0px;
    border-top: 1px dashed var(--theme-bdr-clr);
    .fMenuV1-totalPrice {
        .content {
            h3 {
                color: var(--theme-txt-clr);
            }
            span {
                color: var(--theme-txt-clr);
            }
        }
    }
}









// Responsive 
@media screen and (max-width: 1199px) {
    .platterMenu-top-bar {
        padding-bottom: 0px;
        align-items: flex-start;
    }
    .pmt-selectBox {
        flex-wrap: wrap;
        .label-check {
            margin: 0px 30px 20px 0px;
        }
    }


}

@media screen and (max-width: 991px) {
    .platter-menu-wrapper {
        padding: 70px 40px;
    }
    .platter-content,
    .pmt-selectBox {
        .label-check {
            .name {
                font-size: 20px;
            }
        }
    }


}

@media screen and (max-width: 767px) {
    .platter-menu-wrapper {
        padding: 50px 30px;
    }
    .pmt-selectBox {
        flex-wrap: wrap;
        .label-check {
            margin: 0px 30px 20px 0px;
        }
    }
    .platter-content,
    .pmt-selectBox {
        .label-check {
             .name {
                font-size: 18px;
                padding-left: 30px;
            }
            .box {
                width: 20px;
                height: 20px;
                &::before {
                    width: 6px;
                    height: 11px;
                }
            }
        }
    }
    .pmt-optionSelect {
        max-width: 250px;
        margin-left: 20px;
    }

    // Platter
    .platter-wrapper {
        max-width: 500px;
        margin: 40px auto 0px;
    }
    .single-platter {
        margin-bottom: 40px;
        &:nth-child(even) {
            .platter-img {
                margin-left: 30px;
            }
        }
    }
    .platter-img {
        width: 220px;
        height: 220px;
        margin-right: 30px;
        .price {
            width: 70px;
            height: 70px;
            border-width: 4px;
            h4 {
                font-size: 20px;
            }
        }
    }
    .platter-content {
        ul {
            li {
                font-size: 16px;
                margin-bottom: 12px;
            }
        }
    }

}

@media screen and (max-width: 575px) {
    .platter-menu-wrapper {
        padding: 50px 20px;
    }
    .platterMenu-top-bar {
        padding-bottom: 20px;
        flex-direction: column-reverse;
    }
    .pmt-optionSelect {
        max-width: 300px;
        margin-left: 0;
        margin-bottom: 20px;
    }
    .pmt-selectBox {
        .label-check {
            margin: 0px 20px 15px 0px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    // Platter
    .single-platter {
        flex-direction: column;
        margin-bottom: 30px;
        &:nth-child(even) {
            flex-direction: column;
            .platter-img {
                margin: 0px 0px 20px;
            }
        }
    }
    .platter-img {
        margin: 0px 0px 20px;
        width: 170px;
        height: 170px;
        border-width: 4px;
        .price {
            width: 50px;
            height: 50px;
            border-width: 3px;
            top: -5px;
            right: -5px;
            h4 {
                font-size: 18px;
            }
        }
    }
    .platter-content {
        .label-check {
            margin-bottom: 15px;
        }
    }

    .platterTotal-price {
        margin: 30px auto 0px;
    }
}