// Banner ==============================================
.hv2-banner-area {
    padding: 340px 0px 245px;
    background-image: url('../img/home-v2/banner/banner-01.jpg');
    transition: all .5s;

    animation-name: hv2_Banner;
    animation-duration: 20s;
    animation-direction: alternate-reverse;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: ease-in-out;

    &.bg-overlay {
        &::before {
            background: var(--hdr-v2-bg);
            mix-blend-mode: multiply;
        }
    }
}
@keyframes hv2_Banner {
    0% {
        background-image: url('../img/home-v2/banner/banner-02.jpg');
    }
    25% {
        background-image: url('../img/home-v2/banner/banner-03.jpg');
    }
    50% {
        background-image: url('../img/home-v2/banner/banner-04.jpg');
    }
    75% {
        background-image: url('../img/home-v2/banner/banner-05.jpg');
    }
    100% {
        background-image: url('../img/home-v2/banner/banner-06.jpg');
    }
}
.hv2-banner-content {
    max-width: 850px;
    margin: 0 auto;
    text-align: center;
   > span {
        font-size: 36px;
        font-weight: 600;
        color: var(--white-txt-clr);
        line-height: 1;
        display: block;
    }
    h2 {
        font-size: 150px;
        color: var(--white-txt-clr);
        line-height: 1;
        margin-top: 20px;
        display: block;
    }
    p {
        max-width: 530px;
        margin: 50px auto 0px;
        color: var(--white-txt-clr);
        font-size: 18px;
        font-weight: 500;
    }
    a {
        margin-top: 30px;
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .hv2-banner-content {
        h2 {
            font-size: 130px;
        }
    }
}
@media screen and (max-width: 991px) {
    .hv2-banner-area {
        padding: 250px 0px 170px;
    }
    .hv2-banner-content {
        h2 {
            font-size: 100px;
        }
        p {
            margin-top: 30px;
        }
    }
}
@media screen and (max-width: 767px) {
    .hv2-banner-area {
        padding: 200px 0px 100px;
    }
    .hv2-banner-content {
        > span {
            font-size: 20px;
        }
        h2 {
            font-size: 45px;
            margin-top: 15px;
        }
        p {
            margin-top: 20px;
            font-size: 16px;
        }
        a {
            margin-top: 20px;
        }
    }
}
@media screen and (max-width: 575px) {
    .hv2-banner-area {
        padding: 150px 0px 70px;
    }
    .hv2-banner-content {
        > span {
            font-size: 18px;
        }
        h2 {
            margin-top: 10px;
        }
        p {
            margin-top: 20px;
            font-size: 16px;
        }
        a {
            margin-top: 20px;
        }
    }
}

// About ================================================
.hv2-about-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}
.hv2-about-content {
    padding: 0px 80px 30px 0px;
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        content: "";
        width: 230px;
        height: 40%;
        background: var(--themev2-bg-clr);
        bottom: 0;
        right: 0;
        z-index: -1;
    }
    .shape {
        position: absolute;
        right: 0;
        top: 50%;
        max-width: 65px;
        max-height: 65px;
        img,
        svg {
            max-width: 100%;
            max-height: 100%;
            path {
                fill: var(--themev2-txt-clr);
            }
        }
    }
    .content {
        background: var(--font-bg-clr);
        padding: 100px;
        .title {
            margin-bottom: 40px;
            display: flex;
            align-items: center;
            h2 {
                color: var(--themev2-txt-clr);
                line-height: 1;
                margin: 0px 20px;
            }
            .line {
                &:first-child {
                    transform: rotateY(180deg);
                }
                span {
                    width: 35px;
                    height: 2px;
                    display: block;
                    background: var(--themev2-bg-clr);
                    margin: 10px 0px;
                    &:last-child {
                        width: 15px;
                    }
                }
            }
        }
        h4 {
            color: var(--white-txt-clr);
            font-size: 24px;
            line-height: 1;
            font-weight: 400;
        }
        p {
            color: var(--white-txt-clr);
        }
        ul {
            margin-top: 20px;
            max-width: 475px;
            li {    
                padding-left: 17px;
                position: relative;
                margin-bottom: 10px;
                color: var(--themev2-txt-clr);
                &:last-child {
                    margin-bottom: 0;
                }
                &::before {
                    position: absolute;
                    content: "";
                    width: 7px;
                    height: 7px;
                    background: var(--themev2-bg-clr);
                    left: 0;
                    top: 7px;
                }
            }
        }
    }
}
.hv2-about-thumb {
    position: relative;
    .shape-01,
    .shape-02,
    .shape-03 {
        position: absolute;
        z-index: 2;
    }
    .shape-01 {
        top: 20px;
        left: 20px;
    }
    .shape-02 {
        right: 10%;
        top: 10%;
    }
    .shape-03 {
        bottom: 10%;
        left: 20%;
    }
}
// Responsive 
@media screen and (max-width: 1400px) {
    .hv2-about-thumb {
        .shape-01,
        .shape-02,
        .shape-03 {
            max-width: 300px;
        }
    }
}
@media screen and (max-width: 1199px) {
    .hv2-about-wrapper {
        display: flex;
        flex-direction: column;
    }   
    .hv2-about-thumb {
        margin-top: 20px;
        .shape-01,
        .shape-02,
        .shape-03 {
            max-width: 100%;
        }
    }
}
@media screen and (max-width: 991px) {
    .hv2-about-thumb {
        .shape-01,
        .shape-02,
        .shape-03 {
            max-width: 340px;
        }
    }
    .hv2-about-content {
        .content {
            padding: 70px;
        }
    }
}
@media screen and (max-width: 767px) {
    .hv2-about-content {
        padding: 0;
        .shape {
            display: none;
        }
        &::before {
            display: none;
        }
        .content {
            padding: 50px;
        }
    }
    .hv2-about-thumb {
        .shape-01,
        .shape-02,
        .shape-03 {
            max-width: 300px;
        }
    }
}
@media screen and (max-width: 575px) {
    .hv2-about-thumb {
        .shape-01,
        .shape-02,
        .shape-03 {
            max-width: 180px;
        }
        // .shape-01 {
        //     top: 10px;
        //     left: 10px;
        // }
        // .shape-02 {
        //     right: 8%;
        //     top: 8%;
        // }
        // .shape-03 {
        //     bottom: 8%;
        //     left: 15%;
        // }
    }
    .hv2-about-content {
        .content {
            padding: 30px 20px;
            .title {
                margin-bottom: 30px;
                .line {
                    span {
                        margin: 5px 0px;
                    }
                }
                h2 {
                    font-size: 24px;
                    margin: 0px 10px;
                }
            }
            h4 {
                font-size: 20px;
            }
        }
    }
}

// Reservation > ======================================
.hv2-reservation-area {
    position: relative;
    z-index: 1;
    overflow: hidden;
    .shape-1,
    .shape-2 {
        position: absolute;
        z-index: -1;
        top: 0;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .shape-1 {
        left: 5%;
    }
    .shape-2 {
        right: 5%;
    }
}
.hv2-rsvvtn-form {
    background-color: var(--white-bg-clr);
    max-width: 940px;
    margin: 0 auto;
    box-shadow: 0px 10px 40px var(--register-sdw);
    border-radius: 20px;
    overflow: hidden;
    .reservation-wrap {
        background-position: bottom center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        max-width: 100%;
        .title {
            h3 {
                color: var(--themev2-txt-clr);
            }
        }
        .input-fild {
            &:focus-within {
                input {
                    border-color: var(--themev2-bdr-clr);
                }
            }
            button {
                background: var(--themev2-bg-clr);
            }
        }
        .reservation-from {
            .nice-select {
                &:focus {
                    border-color:var(--themev2-bdr-clr);
                }
                &::before {
                    background: var(--themev2-bg-clr);
                }
            }
        }
    }
    // Date Time 
    .dtp div.dtp-date, 
    .dtp div.dtp-time {
        background: #0026ff !important;
    }

}
.hv2-rsvvtn-content {
    padding: 45px 50px 50px 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .content {
        max-width: 495px;
        width: 100%;
