            display: flex;
            align-items: center;
            margin-bottom: 20px;
            font-size: 18px;
            font-weight: 500;
            line-height: 1;
            color: var(--font-txt-clr);
            &:last-child {
                margin-bottom: 0;
            }
            span {
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 7px;
                background: var(--themev2-bg-clr);
                margin-right: 10px;
                img,
                svg {
                    max-width: 100%;
                    max-height: 100%;
