// Menu ===================================================
.hv2-menu-area {
    background: var(--font-bg-clr);
    z-index: 1;
}
.hv2-menu-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1350px;
    margin: 0 auto;
    grid-gap: 200px;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 1px;
        height: calc(100% - 100px);
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: var(--themev2-bg-clr);
    }
    .single-fMenu-item {
        width: 100%;
        > .title {
            h2 {
                color: var(--themev2-txt-clr);
            }
        }
    }
    .fMenu-item-select {
        &::before {
            border-bottom: 1px dashed var(--themev2-bdr-clr);
        }
        .title {
            .label-check {
                input[type="checkbox"] {
                    &:checked {
                        ~ .name {
                            color: var(--themev2-txt-clr);
                        }
                    }
                    ~ .box {
                        border-color: var(--themev2-bdr-clr);
                        &::before {
                            border-color: var(--themev2-bdr-clr);
                        }
                    }
                }
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .hv2-menu-wrapper {
        grid-gap: 100px;
    }
}
@media screen and (max-width: 991px) {
    .hv2-menu-wrapper {
        grid-gap: 100px;
        grid-template-columns: 1fr;
        &::before {
            width: 100%;
            height: 1px;
        }
    }
}
@media screen and (max-width: 575px) {
    .hv2-menu-wrapper {
        grid-gap: 50px;
    }
}

// Work ===================================================
.hv2-work-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.sing-hv2-work {
    &:hover {
        .content {
            .popup-video {
                img,
                svg {
                    transform: rotate(0deg);
                }
            }
        }
    }
    &:first-child {
        .content {
            h4 {
                max-width: 260px;
            }
        }
        
    }
    &:last-child {
        .content {
            h4 {
                max-width: 190px;
            }
        }
    }
    .content {
        padding: 310px 20px 200px;
        text-align: center;
        .popup-video {
            max-width: 78px;
            max-height: 78px;
            img,
            svg {
                max-width: 100%;
                max-height: 100%;
                transform: rotate(-50deg);
                transition: all .5s;
            }
        }
        p {
            color: var(--white-txt-clr);
            font-size: 14px;
            line-height: 1;
            margin-top: 40px;
        }
        h4 {
            line-height: 1;
            max-width: 330px;
            width: 100%;
            margin: 10px auto 0px;
            a {
                color: var(--white-txt-clr);
                font-size: 20px;
                font-family: var(--raleway);
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .sing-hv2-work {
        .content {
            padding: 200px 20px 150px;
        }
    }
}
@media screen and (max-width: 991px) {
    .hv2-work-wrapper {
        grid-template-columns: 1fr 1fr;
    }
}
@media screen and (max-width: 767px) {
    .sing-hv2-work {
        .content {
            padding: 130px 20px 100px;
            .popup-video {
                max-width: 50px;
                max-height: 50px;
                img,svg {
                    max-width: 60px;
                    max-height: 60px;
                }
            }
            p {
                margin-top: 20px;
            }
            h4 {
                margin: 5px auto 0px;
                a {
                    font-size: 18px;
                    line-height: 1.3;
                }
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .hv2-work-wrapper {
        grid-template-columns: 1fr;
    }
}

