// Reson ===============================================
.hv2-reason-area {
    &::before {
        background: var(--reson-overlay);
    }
}
.reson-v2-title {
    margin-bottom: 50px;
    margin-left: 110px;
    h2 {
        color: var(--themev2-txt-clr);
        line-height: 1;
    }
}
.hv2-reason-wrapper {
    max-width: 757px;
    margin-left: 110px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 70px 100px;
}
.single-hv2-reason {
    display: flex;
    align-items: flex-start;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 2px;
        height: calc( 100% + 70px);
        background: var(--themev2-bg-clr);
        top: 5px;
        left: 7px;
    }
    &:nth-child(4),
    &:nth-child(5) {
        &::before {
            height: 100%;
        }
    }
    .dot {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background: var(--themev2-bg-clr);
        margin: 5px 30px 0px 0px;
    }
    .content {
        flex: 1;
        h4 {
            color: var(--themev2-txt-clr);
            line-height: 1;
        }
        p {
            color: var(--white-txt-clr);
            margin-top: 25px;
            font-size: 18px;
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .reson-v2-title {
        margin-bottom: 50px;
        margin-left: 0px;
    }
    .hv2-reason-wrapper {
        margin-left: 0px;
    }
}
@media screen and (max-width: 991px) {
    .hv2-reason-wrapper {
        grid-gap: 50px 70px;
    }
}
@media screen and (max-width: 767px) {
    .hv2-reason-wrapper {
        grid-gap: 40px 30px;
    }
    .reson-v2-title {
        margin-bottom: 30px;
        h2 {
            font-size: 24px;
        }
    }
    .single-hv2-reason {
        .content {
            h4 {
                font-size: 20px;
            }
            p {
                margin-top: 10px;
                font-size: 16px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .hv2-reason-wrapper {
        grid-gap: 30px;
        grid-template-columns: 1fr;
    }
    .single-hv2-reason {
        &:nth-child(4) {
            &::before {
                height: calc(100% + 30px);
            }
        }
        .dot {
            margin-right: 20px;
        }
    }
}

// Chif ==============================================
.hv2-chef-wrapper {
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
}
.hv2-single-chif {
    overflow: hidden;
    &:hover {
        .content {
            &::before {
                clip-path: circle(70.7% at 50% 50%);
            }
            .content-inner {
                .social {
                    a {
                        color: var(--white-txt-clr);
                    }
                }
                .title {
                    a,span {
                        color: var(--white-txt-clr);
                    }
                }
            }
        }
    }
    .img {
        position: relative;
        z-index: -1;
        img {
            width: 100%;
        }
    }
    .content {
        overflow: hidden;
        border-radius: 0px 0px 30px 30px;
        position: relative;
        margin-top: -31px;
        z-index: 1;
        width: 100%;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: -1px;
            background-image: url('../img/home-v2/cheaf-shape-clr.svg');
            background-position: top center;
            background-repeat: no-repeat;
            background-size: cover;
            z-index: -1;
            transition: all .4s ease-in-out;
            clip-path: circle(0.0% at 50% 0);
        }
        .shape {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: -2;
            top: 0;
            left: 0;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .content-inner {
            text-align: center;
            padding: 60px 30px 30px;
            .social {
                display: flex;
                justify-content: center;
                a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 34px;
                    height: 34px;
                    font-size: 16px;
                    color: var(--body-txt-clr);
                    border-radius: 50%;
                    overflow: hidden;
                    margin: 0px 5px;
                    position: relative;
                    transition: all .3s ease-in-out;
                    &:hover {
                        color: var(--body-txt-clr);
                        &::before {
                            clip-path: circle(70.7% at 50% 50%);
                            opacity: 1;
                        }
                    }
                    &::before {
                        position: absolute;
                        content: "";
                        background: var(--white-bg-clr);
                        width: 100%;
                        height: 100%;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        clip-path: circle(0% at 50% 50%);
                        opacity: 0;
                        transition: all .4s ease-in-out;
                        z-index: -1;
                    }
                }
            }
            .title {
                text-align: center;
                margin-top: 15px;
                a {
                    display: block;
                    color: var(--font-txt-clr);
                    font-size: 20px;
                    line-height: 1;
                    font-weight: 600;
                    transition: all .3s ease-in-out;
                }
                span {
                    color: var(--font-txt-clr);
                    font-size: 16px;
                    font-weight: 400;
                    margin-top: 5px;
                    display: block;
                    transition: all .3s ease-in-out;
                }
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .hv2-single-chif {
        .content {
            margin-top: -41px;
        }
    }
}
@media screen and (max-width: 991px) {
    .hv2-chef-wrapper {
        grid-template-columns: 1fr 1fr;
    }
}
@media screen and (max-width: 767px) {
    .hv2-chef-wrapper {
        grid-gap: 20px;
    }
    .hv2-single-chif {
        .content {
            .content-inner {
                padding: 60px 20px 30px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .hv2-chef-wrapper {
        grid-template-columns: 1fr;
        max-width: 450px;
    }
    .hv2-single-chif {
        .content {
            overflow: visible;
            &::before {
                height: calc(100% + 12px);
                top: -12px;
                border-radius: 0px 0px 30px 30px;
            }
            .content-inner {
                padding: 60px 20px 30px;
                .title {
                    margin-top: 10px;
                    a {
                        font-size: 18px;
                    }
                    span {
                        font-size: 15px;
                    }
                }
            }
        }
    }
}

// Subscribe 
.hv2-subscrive {
    .section-title {
        h2 {
            color: var(--themev2-txt-clr);
        }
        span {
            background: var(--themev2-bg-clr);
        }
    }
    .sb-from {
        .sb-btn {
            background: var(--themev2-bg-clr);
            color: var(--font-txt-clr);
        }
    }
}
