
.footer-area {
    position: relative;
    z-index: 1;
    .background-img {
        &::before {
            background: var(--ftr-bg);
            mix-blend-mode: normal;
            opacity: 0.95;
        }
    }
}

.ftr-logo {
    max-width: 450px;
    p {
        color: var(--white-txt-clr);
        margin-top: 25px;
    }
    .social-icon {
        margin-top: 25px;
    }
    .logo {
        margin-right: 0;
    }
}

.ftr-single-item {
    .title {
        margin-bottom: 30px;
        h4 {
            font-size: 20px;
            font-weight: 430;
            color: var(--theme-txt-clr);
            line-height: 1.3;
            font-family: var(--raleway);
        }
    }
}

.ftrItems {
    ul {
        li {
            margin-bottom: 25px;
            &:last-child {
                margin-bottom: 0;
            }
            a {
                display: block;
                color: var(--white-txt-clr);
                font-size: 16px;
                font-weight: 500;
                line-height: 1.4;
                transition: all .3s ease-in-out;
                span {
                    margin-left: 20px;
                }
            }
        }
    }
}

.ftrSitemap {
    .ftrItems {
        display: flex;
        grid-gap: 40px;
        ul {
            li {
                a {
                    position: relative;
                    padding-left: 20px;
                    &::before {
                        position: absolute;
                        content: "";
                        width: 7px;
                        height: 7px;
                        background: var(--white-bg-clr);
                        border-radius: 100px;
                        left: 0;
                        top: 7px;
                        transition: all .3s ease-in-out;
                    }
                    &:hover {
                        color: var(--theme-txt-clr);
                        &::before {
                            background: var(--theme-bg-clr);
                        }
                    }
                }
            }
        }
    }
}

.ftraddress {
    .ftrItems {
        ul {
            li {
                a {
                    font-weight: 400;
                    position: relative;
                    padding-left: 25px;
                    img,
                    svg {
                        position: absolute;
                        left: 0;
                        top: 2px;
                        color: var(--theme-txt-clr);
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

.ftrSubscribe {
    max-width: 400px;
    .input-fild {
        input {
            border: 1px solid var(--white-bdr-clr);
            background: transparent;
            color: var(--white-txt-clr);
            &::-moz-placeholder {
                color: var(--white-txt-clr);
            }
            &::placeholder {
                color: var(--white-txt-clr);
            }
            &:focus {
                border-color: var(--theme-bdr-clr);
            }
        }
    }
}

.ftrPymnt-method {
    display: flex;
    align-items: center;
    a {
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
}

// copyright
.copyright {
    background: var(--cpright);
    padding: 20px 0px;
    text-align: center;
    p {
        color: var(--white-txt-clr);
        font-weight: 500;
    }
}


// scrollUp
#scrollUp {
    position: fixed;
    z-index: 999999;
    width: 40px;
    height: 40px;
    background: var(--theme-bg-clr);
    bottom: 20px;
    right: 20px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    animation: fadeTop 1.5s linear infinite alternate;
    img,
    svg {
        color: var(--white-txt-clr);
        font-size: 24px;
    }
}

@keyframes fadeTop {
    to {
        transform: translateY(-5px) scale(0.8);
    }from {
        transform: translateY(5px) scale(1);
    }
}

// Responsive 
@media screen and (max-width: 991px) {
    .ftr-single-item {
        .title {
            margin-bottom: 20px;
        }
    }
    .ftr-logo {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        p {
            text-align: center;
        }
    }
    .ftrPayment {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .order-cm-2 {
        order: 2;
    }
    .order-cm-3 {
        order: 3;
    }
    .order-cm-4 {
        order: 4;
    }
    .order-cm-5 {
        order: 5;
    }
    .order-cm-6 {
        order: 6;
    }
    .order-cm-7 {
        order: 7;
    }
    .order-cm-8 {
        order: 8;
    }
}
@media screen and (max-width: 767px) {
    .ftr-single-item {
        .title {
            margin-bottom: 20px;
            h4 {
                font-size: 18px;
            }
        }
    }
    .ftrItems {
        ul {
            li {
                margin-bottom: 15px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
   
    .ftr-logo {
        margin: 0;
        display: block;
        text-align: left;
        p {
            text-align: left;
            margin-top: 15px;
        }
        .social-icon {
            margin-top: 20px;
        }
    }
    .ftr-single-item {
        .title {
            margin-bottom: 15px;
        }
    }

    // copyright
    .copyright {
        padding: 15px 0px;
    }
}

