
.footer-v2 {
    .background-img {
        &::before {
            background: var(--ftr-v2-bg);
            opacity: unset;
        }
    }
    .logo {
        img,
        svg {
            .ftr-l-clr {
                 fill: var(--themev2-txt-clr);   
            }
        }
    }
    .ftr-single-item {
        .title {
            h4 {
                color: var(--themev2-txt-clr);
            }
        }
    }
    .ftrSitemap {
        .ftrItems {
            ul {
                li {
                    a {
                        &:hover {
                            color: var(--themev2-txt-clr);
                            &::before {
                                background: var(--themev2-bg-clr);
                            }
                        }
                    }
                }
            }
        }
    }
    .ftraddress {
        .ftrItems {
            ul {
                li {
                    a {
                        img, 
                        svg {
                            color: var(--themev2-txt-clr);
                        }
                    }
                }
            }
        }
    }

    .social-icon {
        .button {
            &:hover {
                background: var(--themev2-bg-clr);
                .icon {
                    color: var(--white-txt-clr);
                }
            }
            .icon {
                color: var(--themev2-txt-clr);
            }
        }
    }

    .input-fild {
        input {
            &:focus {
                border-color: var(--themev2-bdr-clr);
            }
        }
        button {
            background: var(--themev2-bg-clr);
        }
    }
    #scrollUp {
        background: var(--themev2-bg-clr);
    }
}