
.section-title {
    text-align: center;
    max-width: 465px;
    margin: 0 auto 50px;
    h2 {
        color: var(--theme-txt-clr);
        font-size: 30px;
        font-weight: 400;
        line-height: 1;
    }
    p {
        margin-top: 10px;
    }
    img,
    svg {
        max-width: 100%;
        max-height: 100%;
        margin-top: 20px;
    }
    span {
        display: block;
        width: 148px;
        height: 2px;
        background: var(--theme-txt-clr);
        margin: 15px auto 0px;
        &:last-child {
            width: 65px;
        }
    }
}

.wSection-title {
    h2 {
        color: var(--white-txt-clr);
    }
    p {
        color: var(--white-txt-clr);
    }
    img,
    svg {
        path {
            fill: var(--white-txt-clr);
        }
    }
    span {
        background: var(--white-txt-clr);
    }
}
.clr-white {
    color: var(--white-txt-clr);
}

// Section Title V2
.section-titlev2 {
    text-align: center;
    max-width: 464px;
    margin: 0 auto 50px;
    .title {
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        h2 {
            color: var(--themev2-txt-clr);
            line-height: 1;
            margin: 0px 20px;
        }
        .line {
            &:first-child {
                transform: rotateY(180deg);
            }
            span {
                width: 35px;
                height: 2px;
                display: block;
                background: var(--themev2-bg-clr);
                margin: 10px 0px;
                &:last-child {
                    width: 15px;
                }
            }
        }
    }
    p {
        color: var(--font-txt-clr);
        font-size: 18px;
        font-weight: 600;
    }
}

.fz-16 {
    font-size: 16px !important;
    font-weight: 400 !important;
}

.Wsection-titlev2 {
    .title {
        .line {
            span {
                background: var(--white-txt-clr);
            }
        }
        h2 {
            color: var(--white-txt-clr);
        }
    }
    p {
        color: var(--themev2-txt-clr);
    }
}

.section-titlev3 {
    h2 {
        color: var(--themev3-txt-clr);
        font-family: var(--playFair);
        font-weight: 700;
    }
    img,
    svg {
        path {
            fill: var(--themev3-bg-clr);
        }
    }
    span {
       background: var(--themev3-bg-clr);
    }
}

// V4 Section Title 
.v4-section-title {
    max-width: 600px;
    margin: 0 auto 40px;
    text-align: center;
    .title {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: relative;
        .img {
            margin-right: 10px;
        }
        h2 {
            font-family: var(--comic);
            font-weight: 700;
            font-size: 30px;
        }
        .shape {
            margin-top: -20px;
        }
    }
    p {
        font-size: 18px;
        color: var(--font-txt-clr);
        margin-top: 20px;
    }
}



@keyframes secZoom {
    to {
        width: 24px;
    }
    from {
        width: 0;
    }
}

// Responsive 
@media screen and (max-width: 767px) {
    .section-title {
        margin: 0 auto 30px;
        h2 {
            font-size: 24px;
        }
        p {
            margin-top: 7px;
        }
        img,
        svg {
            margin-top: 15px;
        }
        span {
            width: 120px;
            margin: 10px auto 0px;
        }
    }

    // Section Title V2
    .section-titlev2 {
        margin: 0 auto 30px;
        .title {
            margin-bottom: 10px;
            h2 {
                margin: 0px 10px;
                font-size: 24px;
            }
            .line {
                span {
                    margin: 5px 0px;
                }
            }
        }
        p {
            font-size: 16px;
        }
    }
    // V4 Section Title 
.v4-section-title {
    margin: 0 auto 30px;
    max-width: 450px;
    .title {
        h2 {
            font-family: var(--comic);
            font-weight: 700;
            font-size: 24px;
        }
        .img {
            max-width: 40px;
        }
    }
    p {
        font-size: 16px;
        margin-top: 10px;
    }
}
}