
.testimonial-wrapper {
    max-width: 1310px;
    margin: 0 auto;
    .title {
        margin-bottom: 30px;
        h4 {
            line-height: 1;
            font-size: 20px;
            font-weight: 600;
            font-family: var(--raleway);
        }
    }
}

.testimonials {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
}

.single-testimonial {
    position: relative;
    border: 1px solid var(--light-gray-bdr);
    padding: 50px 57px 50px 40px;
    .quiet {
        position: absolute;
        top: 0;
        right: 0;
        width: 50px;
        height: 50px;
        background: var(--theme-bg-clr);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    ul {
        li {
            display: inline-block;
            color: var(--tangerine-clr);
            font-size: 20px;
            margin-right: 10px;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    p {
        margin-top: 15px;
    }
}

// Responsive 
@media screen and (max-width: 991px){
    .testimonials {
        grid-template-columns: 1fr;
    }
}
@media screen and (max-width: 767px){
    .testimonials {
        grid-gap: 20px;
    }
}
@media screen and (max-width: 575px) {
    .single-testimonial {
        padding: 20px 30px 20px 20px;
        .quiet {
            width: 40px;
            height: 40px;
            img,
            svg {
                max-width: 20px;
            }
        }
        ul {
            li {
                font-size: 18px;
                margin-right: 7px;
            }
        }
        p {
            margin-top: 10px;
        }
    }
}