
.cartList-wrapper {
    max-width: 1180px;
    margin: 0 auto;
}

#cartList-table {
    border-bottom: inherit !important;
    .table-header {
        tr {
            background: transparent;
            th {
                padding: 0px 0px 20px 0px;
                border-bottom: 1px dashed var(--theme-bdr-clr);
                font-size: 18px;
                font-weight: 600;
                line-height: 1.1;
                width: calc(100% / 4);
                color: var(--theme-txt-clr);
                &:nth-child(3),
                &:nth-child(4) {
                    text-align: center;
                }
                &::before, 
                &::after {
                    display: none;
                }
                &:last-child {
                    text-align: right;
                    width: 190px !important;
                    display: flex !important;
                    display: block !important;
                    margin-top: 20px;
                }
            }
        }
    }
    .table-body {
        tr {
            td {
                border-bottom: 1px solid var(--light-gray-bdr);
                padding: 30px 20px 30px 0px;
                position: relative;
                &:last-child {
                    padding-right: 0;
                    display: table-cell !important;
                }
                &:nth-child(3),
                &:nth-child(4) {
                    text-align: center;
                }
                .product {
                    display: flex;
                    align-items: center;
                    .pdct-image {
                        width: 60px;
                        margin-right: 20px;
                    }
                    .content {
                        span {
                           font-size: 14px;
                           line-height: 1;
                           font-weight: 500;
                           color: var(--theme-txt-clr);
                        }
                        h4 {
                            font-size: 20px;
                            line-height: 1;
                            font-weight: 600;
                            font-family: var(--raleway);
                            color: var(--font-txt-clr);
                            margin-top: 10px;
                        }
                    }
                }
                .inc-dec {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    input {
                        text-align: center;
                        height: 30px;
                        width: 40px;
                        color: var(--font-txt-clr);
                        border: 0;
                        outline: none;
                        font-size: 18px;
                        font-weight: 600;
                    }
                    .button {
                        background-color: var(--light-gray-bdr);
                        height: 32px;
                        width: 30px;
                        display: flex;
                        align-items: center;
                        justify-content: space-evenly;
                        line-height: 1;
                        button {
                            color: var(--font-txt-clr);
                            font-size: 16px;
                        }
                    }
                }                
                .total,
                .price {
                    span {
                        font-size: 20px;
                        color: var(--body-txt-clr);
                        line-height: 1;
                        font-weight: 600;
                    }
                }
                .tble-close {
                    width: 45px;
                    height: 45px;
                    background-color: var(--light-gray-bdr);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .4s ease-in-out;
                    margin: 0px 0px 0px auto;
                    cursor: pointer;
                    img,
                    svg {
                        font-size: 24px;
                    }
                }
                &.dtr-control {
                    &::before {
                        left: unset !important;
                        right: 6px;
                        opacity: 0;
                    }
                }
            }
            &:hover {
                td {
                    .tble-close {
                        background-color: var(--theme-bg-clr);
                        color: var(--white-txt-clr);
                    }
                }
            }
        }
    }
}

.cartList-bottom {
    max-width: 1000px;
    margin: 30px auto 0px;
    .total-price {
        text-align: right;
        p {
            font-size: 24px;
            font-weight: 600;
            color: var(--theme-txt-clr);
            line-height: 1;
        }
    }
    .button {
        text-align: center;
    }
}

// Related Product
.cart-rp-wrapper {
    max-width: 1080px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
}


// Responsive
@media screen and (max-width: 1199px) {
    #cartList-table {
        .table-header {
            tr {
                th {
                    &:last-child {
                        width: 150px !important;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 991px) {
    // Related Product
    .cart-rp-wrapper {
        grid-template-columns: 1fr 1fr;
        grid-gap: 30px;
    }
    #cartList-table {
        .table-header {
            tr {
                th {
                    &:last-child {
                        width: 120px !important;
                    }
                }
            }
        }
        .table-body {
            tr {
                td {
                    padding: 20px 40px 20px 0px;
                    &:last-child {
                        padding-right: 40px;
                    }
                    .product-info {
                        h4 {
                            font-size: 16px;
                            margin-top: 7px;
                        }
                    }
                    &.dtr-control {
                        &::before {
                            left: auto;
                            right: 0;
                            height: 20px;
                            width: 20px;
                            margin-top: 0px;
                            border: 0px;
                            line-height: 20px;
                            background-color: var(--theme-bg-clr);
                            transform: translateY(-50%);
                            box-shadow: none;
                            opacity: 1;
                        }
                    }
                    &.child {
                        text-align: left;
                        padding: 10px 0px;
                        margin: 0;
                        height: 100%;
                        width: 100%;
                        display: table-cell !important;
                        ul {
                            display: block;
                            text-align: left;
                            li {
                                padding-left: 30px;
                                span {
                                    &.dtr-title {
                                        margin-bottom: 10px;
                                        font-size: 17px;
                                        color: var(--theme-txt-clr);
                                    }
                                }
                                .total,
                                .price {
                                    span {
                                        font-size: 16px;
                                    }
                                }
                                .tble-close {
                                    margin: -15px auto 0px; 
                                    width: 34px;
                                    height: 34px;
                                    img,
                                    svg {
                                        font-size: 18px;
                                    }
                                }
                               .inc-dec {
                                    justify-content: flex-start;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
