@media screen and (max-width: 575px) {
    #cartList-table {
        .table-header {
            tr {
                &:last-child {
                    width: 80px !important;
                }
                th {
                    padding: 0px 0px 15px 0px;
                }
            }
        }
        .table-body {
            tr {
                td {
                    padding: 15px 0px 20px 0px;
                    &:last-child {
                        padding-right: 30px;
                    }
                    .product {
                        .pdct-image {
                            margin-right: 10px;
                        }
                        .content {
                            h4 {
                                font-size: 16px;
                                margin-top: 5px;
                            }
                            span {
                                font-size: 12px;
                            }
                        }
                    }
                    .tble-close {
                        width: 34px;
                        height: 34px;
                        img,svg {
                            font-size: 20px;
                        }
                    }
                    &.child {
                        ul {
                            li {
                                padding-left: 20px;
                            }
                        }
                    }
                }
            }
        }
    }
    .cartList-bottom {
        margin-top: 20px;
        .total-price {
            text-align: center;
            p {
                font-size: 18px;
            }
        }
    }
    // Related Product
    .cart-rp-wrapper {
        grid-template-columns: 1fr;
        grid-gap: 20px;
        max-width: 450px;
    }
}

// Extra 
@media screen and (max-width: 450px) {
    #cartList-table {
        .table-body {
            tr {
                td {
                    .product {
                        flex-direction: column;
                        align-items: flex-start;
                        .pdct-image {
                            margin-right: 0;
                            margin-bottom: 10px;
                        }
                    }
                }
            }
        }
    }
}