
.register-wrapper {
    &::before {
        background: var(--register);
        mix-blend-mode: normal;
        opacity: 0.7;
    }
}

.register-box {
    max-width: 535px;
    background: var(--white-bg-clr);
    padding: 50px 65px;
    box-shadow: 0px 10px 40px var(--register-sdw);
    border-radius: 20px;
    margin: 0 auto;
    .title {
        margin-bottom: 35px;
        h3 {
            line-height: 1;
            color: var(--theme-txt-clr);
            font-weight: 400;
        }
    }
    p {
        margin-top: 40px;
        font-weight: 500;
        font-size: 16px;
        line-height: 1.3;
        a {
            color: var(--theme-txt-clr);
        }
    }
}

// Responsive 
@media screen and (max-width: 575px) {
    .register-wrapper {
        background-image: none !important;
        &::before {
            display: none;
        }
    }
    .register-box {
        padding: 30px 20px;
        border-radius: 0px;
        .title {
            margin-bottom: 30px;
            h3 {
                font-size: 24px;
            }
        }
        p {
            margin-top: 20px;
            font-size: 15px;
            // a {
            //     display: block;
            //     margin-top: 10px;
            //     text-align: right;
            // }
        }
    }
}














