
.header-v2 {
    background: transparent;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 99999;
    border-bottom: 1px solid var(--themev2-bdr-clr);
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: var(--hdr-v2-bg);
        mix-blend-mode: multiply;
    }
    .logo {
        img,
        svg {
            .hdr-l-white {
                fill: var(--white-txt-clr);
            }
            .hdr-l-theme {
                fill: var(--themev2-txt-clr);
            }
        }
    }
    &.sticky-header {
        background: #17242c;
        &::before {
            display: none;
        }
    }

    .hdr-srch, 
    .hdr-cart {
        background: var(--hv2-icon-bg);
        &:hover {
            img,
            svg {
                color: var(--white-txt-clr);
            }
        }
        &::before {
            background: var(--themev2-bg-clr);
        }
        img,
        svg {
            color: var(--themev2-txt-clr);
        }
    }
    .crt-dtls {
        background: var(--hv2-icon-bg);
        .btn-skew-y {
            color: var(--themev2-txt-clr);
            background: var(--hv2-icon-bg);
            border-color: var(--themev2-bg-clr) !important;

            &::before,&::after {
                color: var(--white-txt-clr);
                background-color: var(--themev2-bg-clr);
            }
        }
    }
    .single-crt-dtls {
        .title {
            span {
                color: var(--themev2-txt-clr);
            }
            h6 {
                a {
                    color: var(--white-txt-clr);
                }
            }
        }
    }

    // Search
    .hdr-search {
        background: var(--hv2-icon-bg);
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04,
        .shape-05 {
           img,
           svg {
               path {
                   fill: var(--themev2-bg-clr);
               }
           }
        }

    }
    .hdeClose {
        background: var(--themev2-bg-clr);
    }
    .hdrSrch-input {
        &:focus-within {
            input {
                border-color: var(--themev2-bdr-clr);
                color: var(--white-txt-clr);
            }
            button {
                img,
                svg {
                    color: var(--themev2-txt-clr);
                }
            }
        }
    }
    .hdrSrch-result {
        .title {
            h4 {
                color: var(--white-txt-clr);
            }
        }
        .result {
            &::-webkit-scrollbar-thumb {
                background: var(--themev2-bg-clr);
            }
        }
    }
    .single-srch-result {
        &:hover {
            h5 {
                a {
                    color: var(--themev2-txt-clr);
                }
            }
        }
        h5 {
            a {
                background-image: linear-gradient(#C9AB81, #C9AB81);
            }
        }
    }

    // Button 
    .lgn-rgstr {
        .btn-skew-y {
            color: var(--themev2-txt-clr);
            background: var(--hv2-icon-bg);
            border-color: var(--themev2-bg-clr) !important;

            &::before,&::after {
                color: var(--white-txt-clr);
                background-color: var(--themev2-bg-clr);
            }
        }
        .l-gray-btn {
            color: var(--themev2-txt-clr);
            background: transparent;
            border-color: var(--themev2-bg-clr) !important;

            &::before,&::after {
                color: var(--themev2-txt-clr);
                background-color: var(--hv2-icon-bg);
            }
        }
    }
}

@media screen and (min-width: 1200px) {
    .header-v2 {
        .main-menu {
            > ul {
               > li {
                   &:hover {
                       a {
                           &::before {
                                clip-path: polygon(50% 100%, 0 0, 100% 0);
                           }
                           img,svg {
                               color: var(--themev2-txt-clr);
                           }
                       }
                   }
                    > a {
                        color: var(--white-txt-clr);
                        &::after {
                            color: var(--themev2-txt-clr);
                        }
                        &::before {
                            width: 25px;
                            height: 15px;
                            background: var(--themev2-bg-clr);
                            left: 50%;
                            transform: translateX(-50%);
                            clip-path: polygon(50% 0, 0 0, 100% 0);
                        }
                    }
                    .sub-menu {
                        background: var(--hv2-icon-bg);
                        li {
                            a {
                                color: var(--white-txt-clr);
                                &::before {
                                    background: var(--themev2-bg-clr);
                                    clip-path: unset;
                                }
                                img,svg {
                                    color: var(--white-txt-clr);
                                }
                            }
                        }
                    }
                }
            }
        }
        .mega-menu {
            background: var(--hv2-icon-bg);
            .shape-1,
            .shape-2 {
                img,
                svg {
                    path {
                        fill: #887862;
                    }
                }
            }
        }
        .single-menu {
            .title {
                h6 {
                    color: var(--white-txt-clr);
                }
            }
            &:hover {
                .title {
                    h6 {
                        color: var(--themev2-txt-clr);
                    }
                }
            }
            .menu {
                ul {
                    li {
                        a {
                            color: var(--white-txt-clr);
                            &::after {
                                color: var(--themev2-txt-clr);
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .header-v2 {
        .hamgurger {
            ul {
                li {
                    background: var(--themev2-bg-clr);
                    &::before {
                        background: var(--themev2-bg-clr);  
                    }
                }
            }
        }

        // Main Menu
        .main-menu {
            top: 101%;
            background: var(--hv2-icon-bg);
            &::-webkit-scrollbar-thumb {
                background: var(--themev2-bg-clr);
            }
            ul {
               li {
                    &.active {
                       > a {
                            img,
                            svg {
                                color: var(--themev2-txt-clr);
                            }
                        }
                    }
                    a {
                        color: var(--white-txt-clr);
                        &::after {
                            color: var(--themev2-txt-clr);
                        }
                    }
                    .sub-menu {
                        li {
                            a {
                                &::after {
                                    color: var(--themev2-txt-clr) !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        // Mega Menu
        .single-menu {
            .title {
                &.active {
                    h6 {
                        color: var(--themev2-txt-clr);
                    }
                    img,
                    svg {
                        color: var(--themev2-txt-clr);
                    }
                }
                h6 {
                    color: var(--white-txt-clr);
                }
                img,
                svg {
                    color: var(--white-txt-clr);
                }
            }
        }
    }
}