
.contact-area {
    &.bg-overlay {
        &::before {
            background: var(--contact);
            opacity: 0.7;
        }
    }
}

.contact-from {
    max-width: 585px;
    margin: 0 auto;
    background: var(--contact-from);
    border-radius: 10px;
    padding: 50px;
    h5 {
        color: var(--white-txt-clr);
        font-family: var(--raleway);
        line-height: 1;
        font-weight: 600;
    }
    p {
        color: var(--ls-gray-txt);
        margin-top: 15px;
    }
    .from {
        max-width: 420px;
        label {
            color: var(--ls-gray-txt);
        }
        input,
        textarea {
            background-color: transparent;
            border-color: var(--light-gray-bdr);
            padding: 20px;
            color: var(--white-txt-clr);
            resize: none;
            &::-moz-placeholder {
                color: var(--ls-gray-txt);
            }
            &::placeholder {
                color: var(--ls-gray-txt);
            }
        }
        textarea {
            height: 140px;
        }
    }
}

// Contact Decription 
.contact-dec-area {
    background: var(--contact-dec);
}
.contact-dec-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .content {
        display: flex;
        align-items: center;
        .title {
            max-width: 300px;
            width: 100%;
            margin-right: 70px;
            h5 {
                color: var(--white-txt-clr);
                font-family: var(--raleway);
                line-height: 1;
                font-weight: 600;
            }
            p {
                color: var(--ls-gray-txt);
                margin-top: 15px;
            }
        }
        .contact {
            a {
                color: var(--white-txt-clr);
                display: inline-flex;
                align-items: center;
                margin: 5px 40px 5px 0px;
                line-height: 1;
                &:first-child {
                    margin-top: 0;
                }
                &:last-child {
                    margin-right: 0;
                    margin-bottom: 0;
                }
                img,svg {
                    color: var(--theme-txt-clr);
                    font-size: 20px;
                    margin-right: 10px;
                }
            }
        }
    }
}
.contact-social {
    max-width: 180px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
        width: 34px;
        height: 34px;
        border-radius: 50%;
        color: var(--white-txt-clr);
        border: 1px solid var(--white-bdr-clr);
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        transition: all .4s ease-in-out;
        position: relative;
        overflow: hidden;
        z-index: 1;
        &:last-child {
            margin-right: 0;
        }
        &::before {
            position: absolute;
            content: "";
            width: 110%;
            height: 110%;
            bottom: 0;
            left: 0;
            background: var(--theme-bg-clr);
            clip-path: polygon(100% 100%, 100% 100%, 0 100%, 0 100%);
            opacity: 0;
            transition: all .3s ease-in-out;
            z-index: -1;
        }
        &:hover {
            border: transparent;
            &::before {
                clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 0);
                opacity: 1;
            }
        }
    }
}

// Responsive 
@media screen and (max-width: 991px) {
    .contact-dec-wrapper {
        .content {
            align-items: flex-start;
            flex-direction: column;
            margin-right: 20px;
            .title {
                margin-right: 0;
            }
            .contact {
                margin-top: 25px;
                display: flex;
                flex-direction: column;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .contact-from {
        padding: 30px 20px 20px;
        .from {
            textarea {
                height: 120px;
            }
        }
    }
    .contact-dec-wrapper {
        align-items: flex-start;
        flex-direction: column;
        .content {
            .contact {
                a {
                    img,
                    svg {
                        font-size: 18px;
                    }
                }
            }
        }
    }
    .contact-social {
        max-width: 130px;
        margin-top: 25px;
    }
}