
.os-wrapper {
    max-width: 900px;
    margin: 0 auto;
}

.single-os-wrap {
    display: flex;
    margin-bottom: 50px;
    &:last-child {
        .os-content {
            border-bottom: none;
            padding-bottom: 0;
        }
    }
}

.os-image {
    width: 158px;
    margin-right: 10px;
    img {
        border-radius: 50%;
    }
}

.os-content {
    flex: 1;
    padding-bottom: 45px;
    border-bottom: 1px dashed var(--theme-bdr-clr);
    .title {
        margin-bottom: 40px;
        padding-left: 30px;
        span {
            font-size: 14px;
            color: var(--theme-txt-clr);
            font-weight: 500;
            display: block;
            line-height: 1;
        }
        h5 {
            font-family: var(--raleway);
            line-height: 1;
            margin-top: 10px;
            font-weight: 600;
        }
    }
}

.order-map {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
}

.single-oMap {
    text-align: center;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: calc(100% + 20px);
        height: 3px;
        background: var(--light-gray-bdr);
        right: 50%;
        top: 52%;
        z-index: -1;
    }
    &:first-child {
        &::before {
            display: none;
        }
    }
    h6 {
        font-family: var(--raleway);
        font-weight: 600;
        line-height: 1;
        font-size: 16px;
        white-space: nowrap;
    }
    .dot {
        width: 18px;
        height: 18px;
        background: var(--os-dot-bg);
        border-radius: 50%;
        margin: 25px auto 20px;
        cursor: pointer;
    }
    p {
        color: var(--font-txt-clr);
        font-weight: 500;
        font-size: 14px;
        line-height: 1;
    }
    &.active {
        &::before {
            background: var(--theme-bg-clr);
        }
        .dot {
            background: var(--theme-bg-clr);
        }
        p {
            color: var(--theme-txt-clr);
        }
    }
}


// Responsive 
@media screen and (max-width: 1199px) {
    .single-oMap {
        &::before {
            width: calc(100% + 20px);
        }
    }
}

@media screen and (max-width: 991px) {
    .single-os-wrap {
        flex-direction: column;
        text-align: center;
    }
    .os-image {
        margin: 0 auto 20px;
    }
    .os-content {
        .title {
            padding-left: 0px;
        }
    }
}

@media screen and (max-width: 800px) {
    .os-wrapper {
        max-width: 550px;
    }
    .order-map {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 40px 20px;
    }
    .single-oMap {
        &:nth-child(3) {
            &::before {
                display: none;
            }
        }
    }
    .os-content {
        padding-bottom: 30px;
    }
    .single-os-wrap {
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 575px) {
    .os-wrapper {
        padding: 0px 30px;
    }
    .os-image {
        width: 100px;
    }
    .order-map {
        grid-template-columns: 1fr;
        grid-gap: 40px;
    }
    .os-content {
        .title {
            margin-bottom: 30px;
            h5 {
                font-size: 18px;
            }
        }
    }
    .single-oMap {
        text-align: left;
        padding-left: 50px;
        &:nth-child(1),
        &:nth-child(3) {
            &::before {
                display: block;
            }
        }
        &:last-child {
            &::before {
                display: none;
            }
        }
        &::before {
            height: calc(100% + 40px);
            width: 3px;
            right: auto;
            left: 8px;
            top: 15px;
        }
        .dot {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            margin:0;
        }
        p {
            margin-top: 10px;
        }
    }
    .single-os-wrap {
        margin-bottom: 20px;
    }
}