
.footer-v3 {
    .logo {
        img,
        svg {
            .ftr-l-clr {
                 fill: var(--themev3-txt-clr);   
            }
        }
    }
    .ftr-single-item {
        .title {
            h4 {
                color: var(--themev3-txt-clr);
            }
        }
    }
    .ftrSitemap {
        .ftrItems {
            ul {
                li {
                    a {
                        &:hover {
                            color: var(--themev3-txt-clr);
                            &::before {
                                background: var(--themev3-bg-clr);
                            }
                        }
                    }
                }
            }
        }
    }
    .ftraddress {
        .ftrItems {
            ul {
                li {
                    a {
                        img, 
                        svg {
                            color: var(--themev3-txt-clr);
                        }
                    }
                }
            }
        }
    }

    .social-icon {
        .button {
            &:hover {
                background: var(--themev3-bg-clr);
                .icon {
                    color: var(--white-txt-clr);
                }
            }
            .icon {
                color: var(--themev3-txt-clr);
            }
        }
    }

    .input-fild {
        input {
            &:focus {
                border-color: var(--themev3-bdr-clr);
            }
        }
        button {
            background: var(--themev3-bg-clr);
        }
    }

    #scrollUp {
        background: var(--themev3-bg-clr);
    }
}