
.w-2 {
    width: calc(100% / 2);
}

@media screen and (max-width: 1199px){
    .w-2 {
        width: 66%;
    }
    .long-hight {
        .singleAboutGallery {
            height: 555px;
            .gallery-image {
                height: 100%;
                img {
                    height: 100%;
                }
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .w-2 {
        width: 100%;
    }
    .long-hight {
        
        .singleAboutGallery {
            height: 624.38px;
        }
    }
}
@media screen and (max-width: 767px) {
    .long-hight {
        .singleAboutGallery {
            height: 637.38px;
        }
    }
}
@media screen and (max-width: 767px) {
    .long-hight {
        .singleAboutGallery {
            height: auto;
        }
    }
}