.fMenu-v1-wrapper {
    position: relative;
    z-index: 1;
    border-radius: 20px;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: var(--fMenu-bg);
        border: 1px solid var(--fMenu-bdr);
        z-index: -1;
        backdrop-filter: blur(40px);
        border-radius: 20px;
    }
}


.fMenuV1-tab-navs {
    .nav-tabs {
        border-bottom: 0;
        background: var(--snow-bg);
        padding: 0px 10px;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        .nav-link {
            border: 0 !important;
            background: transparent !important;
            display: flex;
            padding: 30px 10px;
            position: relative;
            &.active {
                &::before {
                    clip-path: polygon(50% 100%, 0 0, 100% 0);
                }
                &::after {
                    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
                }
                .title {
                    .name {
                        color: var(--theme-txt-clr);
                        &::before,
                        &::after {
                            background: var(--theme-bg-clr);
                        }
                    }
                }
            }
            &::before,
            &::after {
                position: absolute;
                content: "";
                width: 38px;
                height: 18px;
                background: var(--theme-bg-clr);
                left: 50%;
                transform: translateX(-50%);
                transition: all .4s ease-in-out;
            }
            &::before {
                top: 0;
                clip-path: polygon(50% 0, 0 0, 100% 0);
            }
            &::after {
                bottom: 0;
                clip-path: polygon(50% 99%, 0 100%, 100% 100%);
            }
            .img {
                margin-right: 50px;
            }
            .title {
                text-align: left;
                .name {
                    font-size: 24px;
                    font-weight: 400;
                    line-height: 1;
                    position: relative;
                    padding-left: 55px;
                    transition: all .4s ease-in-out;
                    font-family: var(--lobster);
                    display: block;
                    &::before,
                    &::after {
                        position: absolute;
                        content: "";
                        height: 2px;
                        background: var(--font-txt-clr);
                        left: 0;
                        transition: all .4s ease-in-out;
                    }
                    &::before {
                        width: 35px;
                        top: 0;
                    }
                    &::after {
                        width: 15px;
                        bottom: 0;
                    }
                }
                .time {
                    display: flex;
                    align-items: center;
                    font-size: 18px;
                    color: var(--theme-txt-clr);
                    margin-top: 20px;
                    img,
                    svg {
                        margin-right: 10px;
                    }
                }
            }
        }
    }
}

