.fMenuV1-content {
    margin: 95px 130px 0px;
    padding-bottom: 40px;
    display: flex;
    grid-gap: 100px 0px;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 1px dashed var(--theme-bdr-clr);
}

.single-fMenu-item {
    width: calc(100% / 2 - 70px);
    > .title {
        margin-bottom: 40px;
        h2 {
            color: var(--theme-txt-clr);
            font-size: 30px;
            font-weight: 400;
            line-height: 1;
        }
    }
}

.middle-bdr {
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 1px;
        height: calc(100% - 80px);
        background: var(--theme-bdr-clr);
        bottom: 0;
        right: -15%;
    }
}

.fMenu-item-select {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--white-txt-clr);
    position: relative;
    padding-bottom: 30px;
    margin-bottom: 30px;
    &:last-child {
        padding-bottom: 0;
        margin-bottom: 0;
        &::before {
            display: none;
        }
    }
    &::before {
        position: absolute;
        content: "";
        width: calc(100% - 50px);
        bottom: 0;
        right: 0;
        border-bottom: 1px dashed var(--theme-bdr-clr);
    }
    .title {
        margin-right: 20px;
        .label-check {
            position: relative;
            user-select: none;
            cursor: pointer;
            input[type="checkbox"] {
                display: none;
                &:checked {
                    ~ .name {
                        color: var(--theme-txt-clr);
                    }
                    ~ .box {
                        border-color: var(--theme-bdr-clr);
                        &::before {
                            opacity: 1;
                        }
                    }
                }
            }
            .name {
                font-size: 20px;
                font-weight: 500;
                line-height: 1.3;
                padding-left: 50px;
                transition: all .3s ease-in-out;
                display: block;
            }
            .box {
                width: 20px;
                height: 20px;
                border: 1px solid var(--white-bdr-clr);
                border-radius: 2px;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                position: absolute;
                transition: all .3s ease-in-out;
                &::before {
                    position: absolute;
                    content: "";
                    width: 6px;
                    height: 12px;
                    border-width: 0px 1.5px 1.5px 0px;
                    border-color: var(--theme-bg-clr);
                    border-style: solid;
                    top: 1px; 
                    left: 50%;
                    transform: translateX( -50%) rotate(45deg);
                    opacity: 0;
                    transition: all .3s ease-in-out;
                }
            }
        }
        p {
            padding-left: 50px;
            font-size: 16px;
            line-height: 1.3;
            margin-top: 8px;
        }
    }
    .price {
        span {
            font-size: 24px;
            font-weight: 400;
            font-family: var(--lobster);
        }
    }
}

.fMenuV1-totalPrice {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 470px;
    margin: 0 auto;
    padding: 40px 20px 0px;
    .content {
        h3 {
            color: var(--white-txt-clr);
            line-height: 1;
        }
        span {
            color: var(--white-txt-clr);
            font-weight: 500;
            display: block;
            margin-top: 10px;
        }
    }
}


// Responsive 
@media screen and (max-width: 1350px) {
    .single-fMenu-item {
        width: calc(100% / 2 - 50px);
    }   
}

@media screen and (max-width: 1199px) {
    .fMenuV1-tab-navs {
        .nav-tabs {
            .nav-link {
                padding: 30px 10px;
                .img {
                    margin-right: 20px;
                    max-width: 50px;
                    width: 100%;
                }
                .title {
                    flex-direction: 1;
                    .name {
                        font-size: 20px;
                        padding-left: 50px;
                    }
                }
            }
        }
    }
    .fMenuV1-content {
        margin: 70px 70px 0px;
        grid-gap: 50px 0px;
    }
    .single-fMenu-item {
        width: 100%;
    }   
    .middle-bdr {
        &::before {
            display: none;
        }
    }
    .fMenuV1-totalPrice {
        .content {
            h3 {
                font-size: 25px;
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .fMenuV1-tab-navs {
        .nav-tabs {
            .nav-link {
                padding: 25px 10px;
                &::before,
                &::after {
                    width: 30px;
                    height: 15px;
                }
                .img {
                    display: none;
                }
                .title {
                    .time {
                        font-size: 16px;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 767px) {
    .fMenuV1-tab-navs {
        .nav-tabs {
            .nav-link {
                .img {
                    margin-right: 20px;
                    max-width: 50px;
                }
                .title {
                    .name {
                        &::before {
                            top: 5px;
                        }
                        &::after {
                            bottom: 5px;
                        }
                    }
                    .time {
                        font-size: 16px;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
    .fMenuV1-content {
        margin: 30px 30px 0px;
        padding-bottom: 30px;
        grid-gap: 40px 0px;
    }
    .single-fMenu-item {
        >.title {
            margin-bottom: 30px;
            h2 {
                font-size: 24px;
            }
        }
    }
    .fMenu-item-select {
        margin-bottom: 20px;
        padding-bottom: 20px;
        &::before {
            width: calc(100% - 30px);
        }
        .title {
            .label-check {
                
                .name {
                    font-size: 18px;
                    padding-left: 30px;
                }
                .box {
                    width: 20px;
                    height: 20px;
                    border-width: 1px;
                    &::before {
                        width: 5px;
                        height: 11px;
                        border-width: 0px 1px 1px 0px;
                    }
                }
            }
            p {
                padding-left: 30px;
                margin-top: 6px;
            }
        }
        .price {
            span {
                font-size: 18px;
            }
        }
    }
    .fMenuV1-totalPrice {
        max-width: 400px;
        padding: 30px 20px 0px;
        .content {
            h3 {
                font-size: 24px;
            }
        }
    }
}

@media screen and (max-width: 575px) {
    .fMenu-v1-wrapper {
        border-radius: 10px;
        padding-bottom: 30px;
        &::before {
            border-radius: 10px;
        }
    }
    
    .fMenuV1-tab-navs {
        .nav-tabs {
            border-radius: 10px;
            .nav-link {
                padding: 20px 0px;
                &::before,
                &::after {
                    width: 20px;
                    height: 10px;
                }
                .img {
                    margin-right: 15px;
                    max-width: 40px;
                }
                .title {
                    .name {
                        font-size: 18px;
                        padding-left: 20px;
                        &::before {
                            width: 15px;
                        }
                        &::after {
                            width: 5px;
                        }
                    }
                    .time {
                        display: none;
                    }
                }
            }
        }
    }
    .fMenuV1-content {
        margin: 20px 20px 0px;
        grid-gap: 30px 0px;
    }
    .fMenu-item-select {
        margin-bottom: 15px;
        padding-bottom: 15px;
        flex-direction: column;
        align-items: flex-start;
        .price {
            span {
                padding-left: 30px;
                margin-top: 10px;
                display: block;
            }
        }
        .title {
            p {
                font-size: 14px;
            }
        }
    }
    .fMenuV1-totalPrice {
        flex-direction: column;
        text-align: center;
        padding: 20px 20px 0px;
        .content {
            margin-bottom: 15px;
            h3 {
                font-size: 20px;
            }
        }
    }
}