// Header
.demo-header {
    padding: 30px 0px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
}
.demo-header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
        max-width: 236px;
        img,
        svg {
            max-width: 100%;
        }
    }
    a {
        border: none;
    }
    .button {
        display: flex;
        flex-wrap: wrap;
        .btn-anime {
            background-color: var(--font-bg-clr);
            margin-left: 10px;
            span {
                color: var(--white-txt-clr);
            }
            &:before {
                background-color: var(--white-bg-clr);
                color: var(--font-txt-clr);
                border: 1px solid var(--font-bdr-clr);
                border-radius: 5px;
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 767px) {
    .demo-header {
        padding: 20px 0px;
    }
    .demo-header-wrapper {
        .logo {
            max-width: 160px;
        }
    }
}
@media screen and (max-width: 575px) {
    .demo-header-wrapper {
        flex-direction: column;
        .logo {
            margin: 0px 0px 20px 0px;
        }
    }
}

// Banner 
.demo-banner-area {
    padding: 150px 0px 35px;
    overflow: hidden;
    .background-img {
        &::before {
            background: linear-gradient(180deg, rgba(32, 34, 42, 0.3) 28.36%, #20222A 100%);
            mix-blend-mode: multiply;
            transform: rotate(-180deg);
        }
    }
}
.demo-banner-wrapper {
    border-radius: 30px;
    .shape {
        position: absolute;
        top: 40%;
        right: 10%;
        max-width: 30%;
        img {
            width: 100%;
            height: 100%;
        }
    }
}
.d-banner-content {
    text-align: center;
    > h2 {
        font-size: 48px;
        font-weight: 700;
        line-height: 1.4;
        font-family: var(--raleway);
        color: var(--white-txt-clr);
        span {
            color: var(--theme-txt-clr);
        }
    }
    > h4 {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: -30px;
        font-size: 40px;
        font-weight: 500;
        color: var(--white-txt-clr);
        font-family: var(--raleway);
        img {
            position: relative;
            z-index: -1;
        }
    }
}
.p-number {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        content: "";
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 300%;
        height: 300%;
        background-image: url('../img/demo/pn-shape.png');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        z-index: -1;
    }
}
.p-single-number {
    padding: 0px 30px;
    text-align: right;
    h2 {
        font-weight: 800;
        font-size: 70px;
        line-height: 1;
        display: inline-block;
        font-family: var(--raleway);
        color: var(--white-txt-clr);
        &.plus {
            position: relative;
            &::before {
                position: absolute;
                content: "+";
                right: -20px;
                top: -25px;
                font-size: 70px;
                color: var(--white-txt-clr);
                font-weight: 300;
            }
        }
    }
    span {
        font-size: 24px;
        line-height: 1;
        font-weight: 600;
        color: var(--white-txt-clr);
        display: block;
        margin-top: 10px;
    }
    &.p-number-2 {
        text-align: left;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            width: 2px;
            height: 40px;
            background: var(--white-bg-clr);
            left: 0px;
            top: 20px;
        }
    }
}
.d-banner-thumb {
    margin-top: 100px;
    text-align: center;
    display: flex;
    .lThumb,
    .rThumb {
        transform: translateY(-60px);
        position: relative;
        z-index: -1;
        .lt-1,
        .lt-2,
        .rt-1,
        .rt-2 {
            border-radius: 10px;
            overflow: hidden;
        }
        .lt-1 {
            transform: translateX(100px);
            
        }
        .rt-1 {
            transform: translateX(-100px);
        }
        // .lt-1,
        // .rt-1 {
        //     animation: swing 5s linear infinite alternate;
        // }
        .lt-2,
        .rt-2 {
            position: absolute;
            z-index: 2;
            top: -70px;
        }
        .lt-2 {
            left: -20px;
        }
        .rt-2 {
            right: -70px;
        }

    }
    .thumb {
        margin: 0 auto;
        max-width: 820px;
        img {
            width: 100%;
        }
    }
}

