// Home Page 
.demo-home-page-area {
    background: var(--dh-bg);
}
.demo-hp-wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
}
.single-home-page {
    text-align: center;
    &:nth-child(2) {
        a {
            .image {
                .ani-img {
                    animation-duration: 85s;
                }
            }
        }
    }
    &:nth-child(3) {
        a {
            .image {
                .ani-img {
                    animation-duration: 70s;
                }
            }
        }
    }
    &:nth-child(4) {
        a {
            .image {
                .ani-img {
                    animation-duration: 60s;
                }
            }
        }
    }
    &:nth-child(3) {
        animation-delay: 75s;
    }
    &:nth-child(4) {
        animation-delay: 70s;
    }
    a {
        .image {
            transition: all .3s ease-in-out;
            border-radius: 10px;
            overflow: hidden;
            position: relative;
            img {
                width: 100%;
            }
            &::before {
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background: rgba(26, 40, 49, 0.5);
                mix-blend-mode: multiply;
                backdrop-filter: blur(10px);
                transition: .3s ease-in;
                opacity: 0;
                z-index: 99;
            }
            .ani-img {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 9;
                animation: scroll_grid 80s infinite linear;
                background-position: top left;
                background-repeat: repeat-y;
                background-size: contain;
                width: 100%;
                height: 7000px;
                transition: all .3s ease-in-out;
            }
        }
        span {
            font-size: 20px;
            font-weight: 600;
            line-height: 1;
            color: var(--font-txt-clr);
            margin-top: 30px;
            display: block;
            transition: all .3s ease-in-out;
        }
        &:hover {
            .image {
                filter: drop-shadow(0px 30px 30px rgba(0, 0, 0, 0.1));
                transform: translateY(-10px);
                .ani-img {
                    opacity: 0;
                }
            }
            span {
                color: var(--theme-txt-clr);
            }
        }
    }
    &.overlay {
        a {
            .image {
                &::before {
                    opacity: 1;
                }
                
            }
        }
    }
}
@keyframes scroll_grid{
    0%{
        transform:translateZ(0);
    }
    to{
        transform:translate3d(0,-1500px,0);
    }
}

// Responsive 
@media screen and (max-width: 1199px) {
    .demo-hp-wrapper {
        grid-gap: 20px;
    }
}
@media screen and (max-width: 991px) {
    .demo-hp-wrapper {
        grid-template-columns: repeat(2, 1fr);
    }
    .single-home-page {
        a {
            span {
                font-size: 18px;
                margin-top: 20px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .demo-hp-wrapper {
        grid-template-columns: 1fr;
        max-width: 400px;
        margin: 0 auto;
    }
    .single-home-page {
