            }
        }
    }
}

// Mega menu
.d-mega-menu-area {
    position: relative;
    z-index: 1;
    overflow: hidden;
    .shape-1,
    .shape-2,
    .shape-3,
    .shape-4,
    .shape-5,
    .shape-6,
    .shape-7 {
        position: absolute;
        z-index: -1;
        img,svg {
            max-width: 100%;
            max-height: 100%;
        }
    }
    .shape-1 {
        top: -100px;
        left: 50%;
        transform: translateX(-50%) rotate(180deg);
        max-width: 900px;
    }
    .shape-2 {
        top: 50%;
        left: 0;
    }
    .shape-3 {
        right: 0;
        top: 10%;
    }
    .shape-4 {
        top: 15%;
        left: 15%;
    }
    .shape-5 {
        top: 15%;
        right: 15%;
    }
    .shape-6 {
        bottom: 15%;
        left: 15%;
    }
    .shape-7 {
        bottom: 15%;
        right: 15%;
    }
}
.d-megamenu-thumb {
    position: relative;
    max-width: 940px;
    margin: 0 auto;
    padding: 30px;
    
}
.dMegamenu-slider {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    background-image: url('../img/demo/mega-menu/m1.png');
    transition: all .5s;

    animation-name: demo_megamenu;
    animation-duration: 5s;
    animation-direction: alternate-reverse;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: ease-in-out;
}
@keyframes demo_megamenu {
    0% {
        background-image: url('../img/demo/mega-menu/m2.png');
    }
    50% {
        background-image: url('../img/demo/mega-menu/m3.png');
    }
    100% {
        background-image: url('../img/demo/mega-menu/m4.png');
    }
}
