
.header-v4 {
    background: transparent;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 99999;
    .header-wrapper {
        max-width: 1368px;
        margin: 0 auto;
    }
    .logo {
        img,
        svg {
            .hdr-l-theme {
                fill: var(--themev4-txt-clr);
            }
        }
    }
    &.sticky-header {
        background: var(--white-bg-clr);
    }

    .hdr-srch, 
    .hdr-cart {
        background: var(--white-bg-clr);
        box-shadow: 0px 20px 30px var(--crt-shadow);
        border-radius: 5px;
        &:hover {
            img,
            svg {
                color: var(--white-txt-clr);
            }
        }
        &::before {
            background: var(--themev4-bg-clr);
        }
        img,
        svg {
            color: var(--themev4-txt-clr);
        }
    }
    .crt-dtls {
        background: var(--white-bg-clr);
        .btn-skew-y {
            color: var(--themev4-txt-clr);
            background: transparent;
            border-color: var(--themev4-bg-clr);

            &::before,&::after {
                color: var(--white-txt-clr);
                background-color: var(--themev4-bg-clr);
            }
        }
    }
    .single-crt-dtls {
        .title {
            span {
                color: var(--themev4-txt-clr);
            }
            h6 {
                a {
                    color: var(--font-txt-clr);
                }
            }
        }
    }

    // Search
    .hdr-search {
        background: var(--hv2-icon-bg);
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04,
        .shape-05 {
           img,
           svg {
               path {
                   fill: var(--themev4-bg-clr);
               }
           }
        }

    }
    .hdeClose {
        background: var(--themev4-bg-clr);
    }
    .hdrSrch-input {
        &:focus-within {
            input {
                border-color: var(--themev4-bdr-clr);
                color: var(--white-txt-clr);
            }
            button {
                img,
                svg {
                    color: var(--themev4-txt-clr);
                }
            }
        }
    }
    .hdrSrch-result {
        .title {
            h4 {
                color: var(--white-txt-clr);
            }
        }
        .result {
            &::-webkit-scrollbar-thumb {
                background: var(--themev4-bg-clr);
            }
        }
    }
    .single-srch-result {
        &:hover {
            h5 {
                a {
                    color: var(--themev4-txt-clr);
                }
            }
        }
        h5 {
            a {
                background-image: linear-gradient(#EC9D9D, #EC9D9D);
            }
        }
    }

    // Button 
    .lgn-rgstr {
        filter: drop-shadow(0px 20px 30px rgba(0, 0, 0, 0.1));
        .btn-skew-y {
            color: var(--themev4-txt-clr);
            background: transparent;
            border-color: var(--themev4-bg-clr) !important;

            &::before,&::after {
                color: var(--white-txt-clr);
                background-color: var(--themev4-bg-clr);
            }
        }
        .l-gray-btn {
            color: var(--themev4-bdr-clr);
            background: transparent;
            border-color: var(--white-bdr-clr) !important;

            &::before,&::after {
                color: var(--themev4-txt-clr);
                background-color: var(--white-bg-clr);
            }
            &:hover {
                border-color: var(--themev4-bdr-clr) !important;
            }
        }
    }
}

@media screen and (min-width: 1200px) {
    .header-v4 {
        .main-menu {
            > ul {
               > li {
                   &:hover {
                      > a {
                           &::before {
                                opacity: 1;
                           }
                       }
                   }
                    > a {
                        color: var(--body-txt-clr);
                        font-weight: 500;
                        &::after {
                            color: var(--themev4-txt-clr);
                        }
                        &::before {
                            width: 7px;
                            height: 7px;
                            background: var(--themev4-bg-clr);
                            left: 50%;
                            transform: translateX(-50%);
                            border-radius: 50%;
                            top: 80%;
                            opacity: 0;
                        }
                    }
                    .sub-menu {
                        background: var(--white-bg-clr);
                        li {
                            a {
                                color: var(--body-txt-clr);
                                &::before {
                                    background: var(--themev4-bg-clr);
                                    clip-path: unset;
                                }
                            }
                        }
                    }
                }
            }
        }


        .mega-menu {
            background: var(--white-bg-clr);
        }
        .single-menu {
            .title {
                h6 {
                    color: var(--font-txt-clr);
                }
            }
            &:hover {
                .title {
                    h6 {
                        color: var(--themev4-txt-clr);
                    }
                }
            }
            .menu {
                ul {
                    li {
                        a {
                            color: var(--body-txt-clr);
                            &::after {
                                color: var(--themev4-txt-clr);
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .header-v4 {
        .hamgurger {
            ul {
                li {
                    background: var(--themev4-bg-clr);
                    &::before {
                        background: var(--themev4-bg-clr);  
                    }
                }
            }
        }

        // Main Menu
        .main-menu {
            background: var(--white-bg-clr);
            &::-webkit-scrollbar-thumb {
                background: var(--themev4-bg-clr);
            }
            ul {
               li {
                    &.active {
                       > a {
                            img,
                            svg {
                                color: var(--themev4-txt-clr);
                            }
                        }
                    }
                    a {
                        color: var(--body-txt-clr);
                        &::after {
                            color: var(--themev4-txt-clr);
                        }
                    }
                    .sub-menu {
                        li {
                            a {
                                &::after {
                                    color: var(--themev4-txt-clr) !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        // Mega Menu
        .single-menu {
            .title {
                &.active {
                    h6 {
                        color: var(--themev4-txt-clr);
                    }
                    img,
                    svg {
                        color: var(--themev4-txt-clr);
                    }
                }
                h6 {
                    color: var(--body-txt-clr);
                }
                img,
                svg {
                    color: var(--body-txt-clr);
                }
            }
        }
    }
}