
.reservation-wrap {
    max-width: 930px;
    margin: 0 auto;
    background: var(--contact-from);
    border-radius: 10px;
    padding: 50px;
    .title {
        margin-bottom: 30px;
        h3 {
            color: var(--white-txt-clr);
            line-height: 1;
        }
    }
}

.reservation-from {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    label {
        color: var(--white-txt-clr);
        clip-path: polygon(100% 100%, 100% 0, 0 100%, 0 0);
    }
    input{
        background-color: transparent;
        border-color: var(--light-gray-bdr);
        padding: 20px;
        color: var(--white-txt-clr);
        position: relative;
        &::-moz-placeholder {
            color: var(--ls-gray-txt);
        }
        &::placeholder {
            color: var(--ls-gray-txt);
        } 
        &::-webkit-calendar-picker-indicator {
            opacity: 0;
            position: relative;
            z-index: 999;
        }
    }
    button {
        z-index: 1;
    }
    .nice-select {
        &::before {
            position: absolute;
            content: "";
            width: 60px;
            height: calc(100% + 2px);
            background: var(--theme-bg-clr);
            right: -1px;
            top: -1px;
        }
        &::after {
            content: url('https://api.iconify.design/akar-icons/chevron-down.svg?color=white&width=20&height=20');
            top: 58%;
        }
        &.open {
            &::after {
                top: 31%;
            }
        }
    }
}
.date-picker,
.time-picker {
    position: relative;
    button {
        pointer-events: none;
    }
}
