// Responsive
@media screen and (max-width: 991px) {
    .reservation-wrap {
        padding: 50px 30px;
    }
} 
@media screen and (max-width: 767px) {
    .reservation-wrap {
        max-width: 550px;
    }
    .reservation-from {
        grid-gap: 20px;
        grid-template-columns: 1fr;
    }
}
@media screen and (max-width: 767px) { 
    .reservation-wrap {
        max-width: 550px;
        padding: 30px 20px 20px;
        .title {
            margin-bottom: 20px;
            h3 {
                font-size: 24px;
            }
        }
    }
    .reservation-from {
        .nice-select {
            height: 50px;
            &::before {
                width: 50px;
            }
        }
    }
}