.relative {
    position: relative;
}
.bg-body {
    background: var(--body-bg-clr) !important;
}

.clr-white {
    color: var(--white-txt-clr) !important;
}
.clr-theme {
    color: var(--theme-txt-clr);
}

.fs-0 {
    font-size: 0px;
    margin: 0;
    padding: 0;
    line-height: 0;
}
.d-block {
    display: block;
}

// Overlay
.bg-overlay {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
    }
}

.background-img {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    top: 0;
    left: 0;
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    .simpleParallax {
        height: 100%;
    }
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 1;
    }
}


// Social
.social-icon {
    display: flex;
    align-items: center;
    .button {
        width: 40px;
        height: 40px;
        border-radius: 100px;
        margin-right: 20px;
        transition: all .4s ease-in-out;
        overflow: hidden;
        cursor: pointer;
        display: flex;
        align-items: center;
        background: var(--white-bg-clr);
        &:last-child {
            margin-right: 0;
        }
        &:hover {
            width: 140px;
            background: var(--theme-bg-clr);
            .icon {
                color: var(--white-txt-clr);
            }
            span {
                flex: 1;
            }
        }
        .icon {
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 100px;
            color: var(--theme-txt-clr);
            font-size: 18px;
            transition: all .4s ease-in-out;
        }
        span {
            font-size: 16px;
            font-weight: 500;
            color: var(--white-txt-clr);
            transition: all .4s ease-in-out;
            width: 0;
        }
    }
}


// Common Margin & Padding 
// Section Padding 
.s-py-100 {
    padding: 100px 0px;
}
.s-py-50 {
    padding: 50px 0px;
}
.s-py-100-50 {
    padding: 100px 0px 50px;
}
.s-py-50-100 {
    padding: 50px 0px 100px;
}
.s-pt-50 {
    padding-top: 50px;
}
.s-pb-50 {
    padding-bottom: 50px;
}
.s-pt-100 {
    padding-top: 100px;
}
.s-pb-100 {
    padding-bottom: 100px;
}
.s-py-70 {
    padding: 70px 0px;
}
.s-pt-70 {
    padding-top: 70px;
}
.s-pb-70 {
    padding-bottom: 70px;
}
.s-my-50-100 {
    margin: 50px 0px 100px;
}

.mw-1366 {
    max-width: 1366px;
    width: 100%;
    margin: 0 auto;
}

// Responsive =================================================

@media screen and (max-width: 991px) {
    .s-py-100 {
        padding: 70px 0px;
    }
    .s-py-50 {
        padding: 35px 0px;
    }
    .s-py-100-50 {
        padding: 70px 0px 35px;
    }
    .s-py-50-100 {
        padding: 35px 0px 70px;
    }
    .s-pt-50 {
        padding-top: 35px;
    }
    .s-pb-50 {
        padding-bottom: 35px;
    }
    .s-pt-100 {
        padding-top: 70px;
    }
    .s-pb-100 {
        padding-bottom: 70px;
    }
    .s-pt-80 {
        padding-top: 50px;
    }
    .s-my-50-100 {
        margin: 35px 0px 70px;
    }
}

@media screen and (max-width: 767px) {
    .s-py-100 {
        padding: 50px 0px;
    }
    .s-pt-50 {
        padding-top: 25px;
    }
    .s-py-50 {
        padding: 25px 0px;
    }
    .s-py-100-50 {
        padding: 50px 0px 25px;
    }
    .s-py-50-100 {
        padding: 25px 0px 50px;
    }
    .s-pb-50 {
        padding-bottom: 25px;
    }
    .s-pt-100 {
        padding-top: 50px;
    }
    .s-py-70 {
        padding: 50px 0px;
    }
    .s-pt-70 {
        padding-top: 50px;
    }
    .s-pb-70 {
        padding-bottom: 50px;
    }
    .s-pb-100 {
        padding-bottom: 50px;
    }
    .s-pt-80 {
        padding-top: 40px;
    }
    .s-my-50-100 {
        margin: 25px 0px 50px;
    }

    // Social Icon
    .social-icon {
        .button {
            width: 40px;
            height: 40px;
        }
        .icon {
            width: 40px;
            height: 40px;
            font-size: 16px;
        }
    }
}
@media screen and (max-width: 575px) {
    .d-sm-none {
        display: none;  
    }
}