
.checkout-wrapper {
    max-width: 1375px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.bi-wrapper {
    max-width: 860px;
    width: 100%;
    margin-right: 30px;
   > .title {
        margin-bottom: 30px;
        h3 {
            color: var(--theme-txt-clr);
            line-height: 1;
        }
    }
}

.billing-info { 
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    .input-box,.message-box {
        label {
            font-weight: 600;
            font-size: 16px;
        }
        .input-fild {
            input,
            textarea {
                background: transparent;
                padding: 20px;
                border-radius: 5px;
                &::-webkit-outer-spin-button,
                &::-webkit-inner-spin-button{
                    -webkit-appearance: none;
                    -moz-appearance: textfield;
                }
                &[type=number] {
                    -moz-appearance: textfield;
                }
            }
            textarea {
                height: 163px;
                resize: none;
            }
        }
    }
    .nice-select {
        background: var(--ls-gray-bg);
        color: var(--font-txt-clr);
        border-color: transparent;
        &::after {
            border-color: var(--font-txt-clr);
        }
    }
    .message-box {
        .title{
            h4 {
                font-family: var(--raleway);
                font-size: 24px;
                font-weight: 600;
                line-height: 1;
            }
        }
    }
}

.product-info {
    max-width: 386px;
    width: 100%;
    background: var(--ls-gray-bg);
    padding: 40px;
    border-radius: 5px;
    .title {
        margin-bottom: 30px;
        h3 {
            color: var(--theme-txt-clr);
            line-height: 1;
        }
    }
    .subtitle {
        font-size: 20px;
        font-weight: 600;
        color: var(--theme-txt-clr);
        line-height: 1;
        margin-top: 20px;
    }
}
.single-pdct-info {
    display: flex;
    padding-bottom: 30px;
    border-bottom: 1px solid var(--light-gray-bdr);
    
    .img {
        width: 60px;
        margin-right: 20px;
    }
    .content {
        flex: 1;
        span {
            font-size: 14px;
            color: var(--theme-txt-clr);
            display: block;
            font-weight: 500;
        }
        a {
            color: var(--font-txt-clr);
            font-size: 20px;
            font-weight: 600;
            line-height: 1.4;
            display: block;
            margin-top: 6px;
        }
        .quantity {
            font-weight: 600;
            margin-top: 10px;
            line-height: 1;
        }
        .price {
            font-size: 20px;
            font-weight: 600;
            color: var(--theme-txt-clr);
            margin-top: 10px;
            line-height: 1;
        }
    }
}

// PayMent Information
.payment-info-wrapper {
    max-width: 1375px;
    margin: 0 auto;
}
.payment-info {
    margin-top: 40px;
    padding: 30px 0px;
    max-width: 860px;
    border-width: 1px 0px 1px 0px;
    border-style: dashed;
    border-color: var(--theme-bdr-clr);
    .title {
        margin-bottom: 10px;
        h4 {
            font-weight: 600;
            line-height: 1;
            font-family: var(--raleway);
        }
    }
}

.pymnt-method {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.single-pymnt {
    padding: 20px 20px 0px 0px;
    .label-check {
        display: block;
        font-size: 18px;
        color: var(--font-txt-clr);
        margin-bottom: 0;
        padding-left: 30px;
        position: relative;
        cursor: pointer;
        display: inline-block;
        white-space: normal;
        font-weight: 600;
        user-select: none;
        input[type="radio"] {
            display: none;
            &:checked {
                ~ span {
                    &::before {
                        opacity: 1;
                    }
                }
            }
        }
        span {
            width: 18px;
            height: 18px;
            border-radius: 50%;
            border: 1px solid var(--body-bdr-clr);
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            position: absolute;
            &::before {
                width: 9px;
                height: 9px;
                position: absolute;
                content: "";
                background-color: var(--font-bdr-clr);
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                border-radius: 50%;
                opacity: 0;
                transition: all .3s ease-in-out;
            }
        }
    }
    p {
        padding-left: 30px;
        font-size: 14px;
        line-height: 1.2;
        margin-top: 10px;
        color: var(--font-txt-clr);
    } 
}

.terms-conditions {
    margin-top: 40px;
    .label-check {
        display: block;
        font-size: 16px;
        color: var(--font-txt-clr);
        margin-bottom: 0;
        padding-left: 40px;
        position: relative;
        cursor: pointer;
        display: inline-block;
        white-space: normal;
        font-weight: 400;
        user-select: none;
        input[type="checkbox"] {
            display: none;
            &:checked {
                ~ span {
                    &::before {
                        opacity: 1;
                    }
                }
            }
        }
        span {
            width: 24px;
            height: 24px;
            border: 2px solid var(--body-bdr-clr);
            border-radius: 2px;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            position: absolute;
            &::before {
                position: absolute;
                content: url('https://api.iconify.design/akar-icons/check.svg?color=%23555&width=18&height=18');
                color: var(--body-txt-clr);
                font-size: 24px;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                opacity: 0;
                border-radius: 50%;
            }
        }
    }
    .order-btn {
        margin-top: 30px;
    }
}


// Responsive 
@media screen and (max-width: 991px) {
    .checkout-wrapper {
        flex-direction: column;
    }
    .bi-wrapper {
        margin-right: 0;
    }
    .product-info {
        max-width: 100%;
        margin-top: 30px;
        padding: 30px;
        .product {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px;
            .single-pdct-info {
                margin-bottom: 0;
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .billing-info {
        grid-gap: 20px;
        .message-box {
            .input-fild {
                textarea {
                    height: 120px;
                }
            }
        }
    }
    .single-pdct-info {
        .content {
            a {
                font-size: 18px;
            }
            .price {
                font-size: 16px;
            }
        }
    }

    .payment-info {
        margin-top: 20px;
        padding: 20px 0px;
    }
    .pymnt-method {
        max-width: 500px;
    }
    .terms-conditions {
        margin-top: 20px;
        .label-check {
            padding-left: 30px;
            span {
                width: 24px;
                height: 24px;
                &::before {
                    content: url("https://api.iconify.design/akar-icons/check.svg?color=%23555&width=18&height=18");
                }
            }
        }
        .order-btn {
            margin-top: 20px;
        }
    }
}
@media screen and (max-width: 575px) {
    .bi-wrapper {
        >.title {
            margin-bottom: 20px;
            h3 {
                font-size: 24px;
            }
        }
    }
    .billing-info {
        grid-template-columns: 1fr;
        .message-box {
            .input-fild {
                textarea {
                    height: 120px;
                }
            }
            .title {
                h4 {
                    font-size: 20px;
                }
            }
        }
    }
    .product-info {
        padding: 20px;
        .title {
            margin-bottom: 20px;
            h3 {   
                font-size: 24px;
            }
        }
        .subtitle {
            font-size: 18px;
            margin-top: 15px;
        }
        .product {
            grid-template-columns: 1fr;
            grid-gap: 20px;
        }
    }
    .single-pdct-info {
        padding-bottom: 20px;
    }
    .payment-info {
        .title {
            margin-bottom: 0;
            h4 {
                font-size: 20px;
            }
        }
    }
    .single-pymnt {
        padding: 15px 20px 0px 0px;
        .label-check {
            font-size: 16px;
        }
        p {
            margin-top: 5px;
        }
    }
}
