// Banner > ==================================
.hv4-banner-area {
    padding: 278px 0px 147px;
    background: var(--ftr-v4-bg);
    position: relative;
    z-index: 1;
    overflow: hidden;
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04,
    .shape-05,
    .shape-06 {
        position: absolute;
        z-index: -1;
    }
    .shape-02,
    .shape-03,
    .shape-04,
    .shape-05,
    .shape-06 {
        animation: zoomIn 5s linear infinite;
    }
    .shape-01 {
        width: 100%;
        left: 0;
        top: 0;
    }
    .shape-02 {
        top: 30%;
        left: 10%;
        animation-delay: 6s;
    }
    .shape-03 {
        bottom: 20%;
        left: 20%;
        animation-delay: 7s;
    }
    .shape-04 {
        bottom: 5%;
        left: 5%;
        animation-delay: 8s;
    }
    .shape-05 {
        bottom: 25%;
        left: 40%;
        animation-delay: 9s;
    }
    .shape-06 {
        bottom: 5%;
        left: 40%;
        animation-delay: 10s;
    }
}
.hv4-banner-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1126px;
    margin-left: 105px;
}
.hv4-banner-content {
    max-width: 635px;
    width: 100%;
    margin-right: 40px;
    h3 {
        font-size: 30px;
        font-weight: 500;
        line-height: 1.3;
        font-family: var(--raleway);
    }
    h2 {
        font-size: 70px;
        font-family: var(--comic);
        font-weight: 700;
        line-height: 1;
        margin-top: 20px;
        span {
            color: var(--themev4-txt-clr);
        }
    }
    p {
        font-size: 18px;
        color: var(--font-txt-clr);
        font-weight: 500;
        max-width: 530px;
        margin-top: 40px;
    }
    a {
        filter: drop-shadow(0px 20px 30px var(--crt-shadow));
    }
    .social-icon {
        margin-top: 30px;
        .button {
            box-shadow: 0px 20px 30px var(--crt-shadow);
            img,svg {
                color: var(--themev4-txt-clr);
            }
            &:hover {
                background: var(--themev4-bg-clr);
                img,svg {
                    color: var(--white-txt-clr);
                }
            }
        }
    }
}
.hv4-banner-thumb {
    max-width: 447px;
    width: 100%;
    position: relative;
    .tShape-1,
    .tShape-2,
    .tShape-3 {
        position: absolute;
    }
    .tShape-1 {
        bottom: 85%;
        left: 0;
    }
    .tShape-2 {
        bottom: 70%;
        right: -35%;
    }
    .tShape-3 {
        top: 50%;
        right: -52%;
    }
}
// Responsive 
@media screen and (max-width: 1400px) {
    .hv4-banner-wrapper {
        margin-left: 0px;
    }
    .hv4-banner-thumb {
        .tShape-1,
        .tShape-2,
        .tShape-3 {
            max-width: 150px;
        }
        .tShape-2 {
            right: -10%;
        }
        .tShape-3 {
           display: none;
        }
    }
}
@media screen and (max-width: 1199px) {
    .hv4-banner-area {
        padding: 150px 0px 100px;
    }
    .hv4-banner-content {
        margin-right: 30px;
        h3 {
            font-size: 24px;
        }
        h2 {
            font-size: 50px;
            margin-top: 10px;
        }
        p {
            margin-top: 20px;
        }
    }
}
@media screen and (max-width: 991px) {
    .hv4-banner-area {
        padding: 150px 0px 70px;
        .shape-03 {
            bottom: 60%;
            max-width: 300px;
        }
        .shape-05 {
            bottom: 70%;
            left: 70%;
        }
    }
    .hv4-banner-wrapper {
        flex-direction: column;
    }
    .hv4-banner-content {
        margin-right: 0;
        margin-bottom: 30px;
    }
    .hv4-banner-thumb {
        max-width: 400px;
        .tShape-1,
        .tShape-2 {
            max-width: 100px;
        }
        .tShape-2 {
            right: -15%;
        }
    }
}
@media screen and (max-width: 767px) {
    .hv4-banner-content {
        h3 {
            font-size: 20px;
        }
        p {
            font-size: 16px;
        }
    }
}
@media screen and (max-width: 575px) {
    .hv4-banner-area {
        padding: 120px 0px 50px;
        .shape-03 {
            max-width: 200px;
        }
    }
    .hv4-banner-thumb {
        .tShape-2,
        .tShape-1 {
            display: none;
        }
    }
    .hv4-banner-content {
        h3 {
            font-size: 18px;
        }
        h2 {
            font-size: 38px;
        }
        p {
            margin-top: 15px;
        }
        .social-icon {
            margin-top: 20px;
        }
    }
}

// About > ======================================
.hv4-about-area {
    position: relative;
    z-index: 1;
    >.shape {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        z-index: -1;
    }
}
.hv4-about-wrapper {
    max-width: 955px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.hv4-about-thumb {
    max-width: 342px;
    width: 100%;
    margin-right: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    .thumb-01 {
        margin-bottom: -30px;
    }
    .thumb-03 {
        margin-top: -75px;
        margin-right: 5px;
    }
}
.hv4-about-content {
    max-width: 514px;
    width: 100%;
    h4 {
        font-size: 24px;
        line-height: 1;
        font-weight: 600;
        font-family: var(--raleway);
    }
    p {
        color: var(--font-txt-clr);
        font-size: 18px;
    }
    ul {
        margin-top: 20px;
        max-width: 475px;
        li {    
            padding-left: 17px;
            position: relative;
            margin-bottom: 10px;
            color: var(--font-txt-clr);
            font-size: 18px;
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                position: absolute;
                content: "";
                width: 7px;
                height: 7px;
                background: var(--font-bg-clr);
                left: 0;
                top: 9px;
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 991px) {
    .hv4-about-wrapper {
        flex-direction: column-reverse;
    }
    .hv4-about-thumb {
        margin: 0;
    }
    .hv4-about-content {
        margin-bottom: 30px;
        .v4-section-title {
            margin: 0 auto 30px;
            max-width: 100%;
        }
    }
}
@media screen and (max-width: 767px) {
    .hv4-about-content {
        margin-bottom: 30px;
        .v4-section-title {
            margin: 0 auto 20px;
        }
        h4 {
            font-size: 20px;
        }
        p {
            font-size: 16px;
        }
        ul {
            li {
                font-size: 16px;
            }
        }
    }
}

// Category > =======================================
.hv4-ctgry-wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
}
.single-hv4-ctgry {
    text-align: center;
    overflow: hidden;
    .img {
        background-color: var(--ftr-v4-bg);
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        overflow: hidden;
        img {
            transition: all .3s ease-in-out;
        }
    }
    a {
        display: block;
        font-size: 20px;
        font-weight: 600;
        color: var(--font-txt-clr);
        margin-top: 20px;
        transition: all .3s ease-in-out;
    }
    &:hover {
        .img {
            img {
                transform: scale(1.1);
            }
        }
        a {
            color: var(--themev4-txt-clr);
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .hv4-ctgry-wrapper {
        grid-template-columns: repeat(3, 1fr);
    }
}
@media screen and (max-width: 991px) {
    .hv4-ctgry-wrapper {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media screen and (max-width: 767px) {
    .hv4-ctgry-wrapper {
        grid-gap: 20px;
    }
    .single-hv4-ctgry {
        a {
            font-size: 18px;
        }
    }
}
@media screen and (max-width: 575px) {
    .hv4-ctgry-wrapper {
        grid-template-columns: 1fr;
    }
    .single-hv4-ctgry {
        a {
            font-size: 16px;
            margin-top: 15px;
        }
    }
}

// Table > ==========================================
.hv4table-tab-navs {
    .nav-tabs {
        border-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        grid-gap: 10px;
        flex-wrap: wrap;
        .nav-link {
            border: 0 !important;
            padding: 15px 30px;
            background: var(--ftr-v4-bg);
            border-radius: 0px;
            font-family: var(--comic);
            font-size: 20px;
            font-weight: 400;
            line-height: 1.2;
            color: var(--font-txt-clr);
            transition: all .3s ease-in-out;
            &:hover,
            &.active {
                color: var(--themev4-txt-clr);
            }
        }
    }
}
.hv4-table-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.single-hv4-table {
    background: var(--ftr-v4-bg);
    padding: 50px 60px;
    &.color {
        background: var(--hv4-tbl2-bg);
    }
    .content {
        margin: 20px 0px;
        ul {
            li {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0px 20px 30px 20px;
                margin-bottom: 30px;
                border-bottom: 1px solid var(--theme-bdr-clr);
                &:last-child {
                    margin-bottom: 0;
                }
                &:hover {
                    a {
                        color: var(--themev4-txt-clr);
                    }
                    .price {
                        color: var(--themev4-txt-clr);
                    }
                }
                a {
                    display: block;
                    margin-right: 20px;
                    font-size: 20px;
                    font-weight: 500;
                    color: var(--font-txt-clr);
                    line-height: 1.3;
                    transition: all .3s ease-in-out;
                    span {
                        display: block;
                        font-weight: 400;
                        font-size: 16px;
                        color: var(--font-txt-clr);
                        margin-top: 10px;
                    }
                }
                .price {
                    font-family: var(--comic);
                    font-size: 24px;
                    font-weight: 400;
                    color: var(--font-txt-clr);
                    line-height: 1;
                    display: block;
                    transition: all .3s ease-in-out;
                }
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 1199px) {
    .single-hv4-table {
        padding: 40px;
        .content {
            ul {
                li {
                    padding: 0px 0px 30px 0px;
                }
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .hv4table-tab-navs {
        .nav-tabs {
            .nav-link {
                padding: 12px 24px;
                font-size: 18px;
            }
        }
    }
    .hv4-table-wrapper {
        grid-template-columns: 1fr;
    }
}
@media screen and (max-width: 767px) {
    .hv4table-tab-navs {
        .nav-tabs {
            .nav-link {
                font-size: 16px;
            }
        }
    }
    .single-hv4-table {
        padding: 30px 30px;
        .shape {
            max-width: 200px;
            margin: 0 auto;
        }
        .content {
            ul {
                li {
                    padding-bottom: 20px;
                    margin-bottom: 20px;
                    a {
                        font-size: 20px;
                    }
                    .price {
                        font-size: 20px;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .single-hv4-table {
        padding: 30px 20px;
        .shape {
            max-width: 150px;
            margin: 0 auto;
        }
        .content {
            ul {
                li {
                    padding-bottom: 15px;
                    margin-bottom: 15px;
                    a {
                        margin-right: 10px;
                        font-size: 18px;
                        span {
                            margin-top: 7px;
                        }
                    }
                    .price {
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

// Work > =========================================
.hv4-work-wrapper {
    padding: 200px 20px;
    border-radius: 10px;
    overflow: hidden;
    &::before {
        background: var(--font-bg-clr);
        mix-blend-mode: multiply;
        opacity: 0.7;
        border-radius: 10px;
    }
}
.hv4-work-content {
    text-align: center;
    .popup-video {
        max-width: 78px;
        max-height: 78px;
        display: block;
        margin: 0 auto 40px;
        img,svg {
            max-width: 100%;
            max-height: 100%;
        }
    }
    span {
        font-weight: 500;
        font-size: 16px;
        color: var(--white-txt-clr);
        display: block;
        line-height: 1;
    }
    .title {
        max-width: 329px;
        color: var(--white-txt-clr);
        font-weight: 600;
        font-size: 20px;
        line-height: 1.4;
        display: inline-block;
        margin-top: 10px;
    }
}
// Responsive 
@media screen and (max-width: 991px) {
    .hv4-work-wrapper {
        padding: 120px 20px;
    }
}
@media screen and (max-width: 767px) {
    .hv4-work-wrapper {
        padding: 70px 20px;
        .popup-video {
            max-width: 60px;
            max-height: 60px;
            margin: 0 auto 25px;
        }
    }
}
@media screen and (max-width: 575px) {
    .hv4-work-wrapper {
        padding: 50px 20px;
        .popup-video {
            max-width: 50px;
            max-height: 50px;
        }
        .title {
            font-size: 18px;
            margin-top: 7px;
        }
    }
}

// Testimonial > ==================================
.hv4-testimonial {
    .owl-nav {
        .owl-prev,
        .owl-next {
            width: 40px;
            height: 40px;
            background: var(--ftr-v4-bg);
            display: flex;
            align-items: center;
            justify-content: center;
            img,
            svg {
                width: 25px;
                height: 25px;
                path {
                    fill: var(--themev4-txt-clr);
                }
            }
        }
        .owl-prev {
            left: -40%;
        }
        .owl-next {
            right: -40%;
        }
    }
    .single-v3-tstmnl {
        border: none;
        .client {
            width: 111px;
            height: 111px;
            border-radius: 50%;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
            }
            &::before {
                display: none;
            }
            margin: 0 auto 20px;
        }
        h4 {
            font-family: var(--raleway);
            margin-top: 40px;
        }
        .quite {
            margin: 40px auto 0px;
            &::before {
                display: none;
            }
            img,
            svg {
                path {
                    fill: var(--themev4-txt-clr) !important;
                }
            }
        }
    }
}
// Responsive 
@media screen and (max-width: 1350px) {
    .hv4-testimonial {
        .owl-nav {
            .owl-prev {
                left: -20%;
            }
            .owl-next {
                right: -20%;
            }
        }
    }
}
@media screen and (max-width: 1199px) {
    .hv4-testimonial {
        .owl-nav {
            .owl-prev {
                left: -10%;
            }
            .owl-next {
                right: -10%;
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .hv4-testimonial {
        padding: 0px 60px;
        .owl-nav {
            top: 50%;
            transform: translate(-50%, -50%);
            max-width: 100%;
            .owl-prev {
                left: 0;
            }
            .owl-next {
                right: 0;
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .hv4-testimonial {
        padding: 0px 50px;
        .owl-nav {
            .owl-prev,
            .owl-next {
                width: 34px;
                height: 34px;
                img,
                svg {
                    max-width: 20px;
                    max-height: 20px;
                }
            }
        }
        .single-v3-tstmnl {
            .client {
                width: 80px;
                height: 80px;
            }
            a {
                font-size: 18px;
            }
            h4 {
                margin-top: 15px;
                font-size: 16px;
            }
            .quite {
                margin: 20px auto 0px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .hv4-testimonial {
        padding: 0px 0px 60px;
        .owl-nav {
            top: auto;
            bottom: 34px;
            transform: translate(-50%, 0%);
            max-width: 85px;
        }
    }
}

// Chef > =========================================
.hv4-chef-wrapper {
    .hv3-single-chif {
        border-radius: 30px;
        overflow: inherit;
        &:hover {
            .content {
                filter: none;
            }
        }
        .img {
            border-radius: 30px 30px 0px 0px;
            overflow: hidden;
            &::before {
                display: none;
            }
        }
        .content {
            background: var(--white-bg-clr);
            filter: drop-shadow(0px 20px 30px rgba(0, 0, 0, 0.1));
            border-radius: 0px 0px 30px 30px;
            overflow: hidden;
            transition: all .3s ease-in-out;
            &::before {
                background: var(--themev4-bg-clr);
                border-radius: 0px 0px 30px 30px;
            }
            .social {
                a {
                    &:hover {
                        color: var(--themev4-txt-clr);
                    }
                }
            }
        }
    }
}
