
.shop-v2 {
    display: flex;
    justify-content: space-between;
    max-width: 1430px;
    margin: 0 auto;
}

.shop-v2-left {
    min-width: 280px;
    margin-right: 30px;
}

.single-spLeft-ctgry {
    background: var(--ls-gray-bg);
    padding: 30px 25px;
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
    >.title {
        margin-bottom: 20px;
        h5 {
            font-family: var(--raleway);
            font-weight: 600;
            line-height: 1.3;
        }
        span {
            display: block;
            width: 30px;
            height: 2px;
            background: var(--theme-bg-clr);
            margin-top: 20px;
        }
    }
}

.single-fdCtgry-item {
    margin-bottom: 30px;
    &:last-child {
        margin-bottom: 0;
    }
    .title {
        display: flex;
        align-items: center;
        cursor: pointer;
        h6 {
            font-family: var(--raleway);
            font-weight: 500;
            line-height: 1;
            margin-right: 10px;
        }
        img,
        svg {
            color: var(--theme-txt-clr);
        }
    }
    .item {
        padding-left: 15px;
        display: none;
        a {
            display: block;
            font-size: 18px;
            font-weight: 500;
            color: var(--font-txt-clr);
            margin-top: 15px;
            transition: all .3s ease-in-out;
            &:hover {
                color: var(--theme-txt-clr);
            }
        }
    }
}

.priceRange {
    padding-left: 20px;
    padding-right: 20px;
    .price-filter {
        .price-filter-content {
            input {
                font-size: 18px;
                color: var(--body-txt-clr);
                font-weight: 500;
                border: none;
                background-color: transparent;
                pointer-events: none;
                width: 85px;
            }
        }
    }
    #slider-container {
        height: 6px;
        width: calc(100% - 20px);
        background-color: #E74C3C;
        border-radius:0px;
        z-index: 1;
        position: relative;
        margin-bottom: 25px;
        .ui-slider-range {
            height: 100%;
            width: 100%;
            background: #eee;
            z-index: -1;
            border-radius: 0px;
        }
    }
    
    span.ui-slider-handle.ui-state-default.ui-corner-all {
        display: inline-block;
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background: #E74C3C;
        z-index: 9999;
        top: 50%;
        position: absolute;
        transform: translateY(-50%);
        border: none;
        margin-left: 0px;
    }
}

.arrivalFood-item {
    a {
        display: block;
        font-size: 18px;
        font-weight: 500;
        color: var(--font-txt-clr);
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.shop-v2-right {
    max-width: 1080px;
    width: 100%;
    .shop-v1-topBar {
        border: none;
        padding: 0;
        margin-bottom: 30px;
        .category-select {
            max-width: 276px;
            .select-active {
                width: 100%;
                margin-right: 0px;
            }
        }
    }
}

// Responsive
@media screen and (max-width: 1199px) {
    .shop-v2 {
        flex-direction: column;
    }
    .shop-v2-left {
        display: grid;
        min-width: 100%;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 20px;
        margin-bottom: 30px;
    }
    .single-spLeft-ctgry {
        margin: 0;
    }
}

@media screen and (max-width: 991px) {
    .shop-v2-left {
        grid-template-columns: 1fr 1fr;
    }
    .shop-v2-right {
        .shop-v1-topBar {
            flex-direction: row; 
            .category-select {
                margin-top: 0;
            }
        }
    }
}

@media screen and (max-width: 767px){
    .single-spLeft-ctgry {
        padding: 20px;
        >.title {
            h5 {
                font-size: 18px;
            }
            span {
                margin-top: 10px;
            }
        }
    }
    .single-fdCtgry-item {
        margin-bottom: 20px;
        .title {
            h6 {
                font-size: 16px;
            }
        }
        .item {
            a {
                font-size: 16px;
                margin-top: 10px;
            }
        }
    }
    .arrivalFood-item {
        a {
            font-size: 16px;
            margin-bottom: 10px;
        }
    }
}

@media screen and (max-width: 575px) {
    .shop-v2-left {
        grid-template-columns: 1fr;
        width: 100%;
        max-width: 450px;
        min-width: unset;
        margin: 0px auto 20px;
    }
    .shop-v2-right {
        .shop-v1-topBar {
            flex-direction: column; 
            margin-bottom: 20px;
            .category-select {
                margin-top: 20px;
                max-width: 100%;
            }
        }
    }
}