
.chif-awer {
    max-width: 1100px;
    margin: 0 auto 30px;
    padding: 25px 35px;
    display: flex;
    align-items: center;
    background: var(--font-bg-clr);
    .img {
        margin-right: 50px;
        width: 160px;
    }
    .content {
        flex: 1;
        padding-right: 145px;
        position: relative;
        h2 {
            color: var(--theme-txt-clr);
            font-size: 48px;
            line-height: 1.2;
        }
        p {
            font-size: 18px;
            font-weight: 500;
            color: var(--white-txt-clr);  
            margin-top: 20px;
        }
        .shape {
            position: absolute;
            content: "";
            right: 20px;
            top: 0;
            max-width: 150px;
            animation: awer_shape 1s linear infinite alternate;
            img,
            svg {
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
}

@keyframes awer_shape {
    to {
        top: 0%;
        transform: rotate(0);
    }
    from {
        top: 20%;
        transform: rotate(10deg);
    }
}




// Expert Chef
.expertChef-title {
    max-width: 1100px;
    margin: 0 auto 20px;
    span {
        font-size: 20px;
        font-weight: 600;
        color: var(--font-txt-clr);
        line-height: 1;
    }
}

.expertChef-wrapper {
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
}

.single-expertChef {
    position: relative;
    overflow: hidden;
    &:hover {
        &::before {
            opacity: 1;
        }
        .chef {
            img {
                transform: scale(1.1) rotate(-5deg);
            }
        }
        .title {
            span,
            a {
                opacity: 1;
                transform: translateY(0);
            }
        }
        .social {
            a {
                &:nth-child(1),
                &:nth-child(2),
                &:nth-child(3) {
                    transform: translateY(0px);
                    opacity: 1;
                }
            }
        }
    }
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: var(--expert-chef);
        top: 0;
        left: 0;
        opacity: 0;
        transition: all .3s ease-in-out;
        z-index: 1;
    }
    .chef {
        img {
            transition: all .4s ease-in-out;
            width: 100%;
        }
    }
    .title {
        position: absolute;
        z-index: 2;
        bottom: 30px;
        left: 30px;
        a,
        span {
            color: var(--white-txt-clr);
            transition: all .4s ease-in-out;
            line-height: 1;
            opacity: 0;
            transform: translateY(50px);
        }
        a {
            font-size: 20px;
            font-weight: 600;
            transition-delay: .3s;
            display: block;
        }
        span {
            font-size: 16px;
            display: block;
            transition-delay: .4s;
            margin-top: 10px;
        }
    }
    .social {
        position: absolute;
        top: 20px;
        right: 20px;
        z-index: 5;
        a {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 32px;
            height: 32px;
            background: var(--as-icon-bg);
            border-radius: 50%;
            color: var(--white-txt-clr);
            margin-bottom: 15px;
            position: relative;
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                transform: translateY(-50px);
                opacity: 0;
                transition: all .4s ease-in-out;
            }
            &:nth-child(1) {
                transition-delay: .4s;
            }
            &:nth-child(2) {
                transition-delay: .3s;
            }
            &:nth-child(3) {
                transition-delay: .2s;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &:hover {
                &::before {
                    clip-path: circle(70.7% at 50% 50%);
                }
            }
            &::before {
                position: absolute;
                content: "";
                width: 100%;
                height: 100%;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background: var(--theme-bg-clr);
                clip-path: circle(0% at 50% 50%);
                transition: all .4s ease-in-out;
                z-index: -1;
            }
        }
    }
}

// Responsive 
@media screen and (max-width: 991px){
    .chif-awer {
        .img {
            margin-right: 30px;
            width: 120px;
        }
        .content {
            padding-right: 0;
            .shape {
                display: none;
            }
            h2 {
                font-size: 40px;
            }
            p {
                font-size: 16px;
                margin-top: 10px;
            }
        }
    }
    .expertChef-wrapper {
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 767px) {
    .chif-awer {
        flex-direction: column;
        text-align: center;
        padding: 20px;
        .img {
            margin-right: 0;
            margin-bottom: 20px;
        }
    }
    .expertChef-wrapper {
        grid-gap: 20px;
    }
}

@media screen and (max-width: 575px) {
    .chif-awer {
        margin-bottom: 20px;
        .img {
            width: 100px;
            margin-bottom: 15px;
        }
       .content {
           h2 {
               font-size: 24px;
           }
       }
    }
    .expertChef-wrapper {
        grid-template-columns: 1fr;
        .title {
            left: 20px;
            bottom: 20px;
            a {
                font-size: 18px;
            }
        }
    }
}