
.shop-v1-topBar {
    max-width: 1080px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0px;
    border-width: 1px 0px 1px 0px;
    border-style: dashed;
    border-color: var(--theme-bdr-clr);
    .search {
        max-width: 420px;
        width: 100%;
        margin-right: 30px;
        .input-fild {
            input {
                padding: 20px 20px 20px 70px;
                height: 50px;
            }
            button {
                right: auto;
                left: 0;
            }
        }
    }
}


.category-select {
    display: flex;
    max-width: 606px;
    width: 100%;
    .select-active {
        width: 50%;
        margin-right: 40px;
        &:last-child {
            margin-right: 0;
        }
        &.nice-select {
            background: var(--light-gray-bg);
            color: var(--body-txt-clr);
            border-color: transparent;
            font-weight: 500;
            font-size: 16px;
            height: 50px;
            &::after {
                content: url('https://api.iconify.design/akar-icons/chevron-down.svg?color=%23ea5455');
            }
            .option {
                font-size: 15px;
            }

        }
    }
}

.shop-v1-wrapper {
    max-width: 1080px;
    margin: 30px auto 0px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px 30px;
}

// Shop Card
.shop-card {
    position: relative;
    .discount {
        position: absolute;
        top: 6px;
        right: 0;
        z-index: 9;
        cursor: pointer;
        span {
            background: var(--theme-bg-clr);
            color: var(--white-txt-clr);
            font-size: 18px;
            font-weight: 500;
            padding: 6px 10px;
            line-height: 1;
        }
    }
    &:hover {
        .cardImg {
            .title {
                bottom: 0;
                span {
                    animation: top 1s ease-in-out;
                }
            }
        }
        .content {
            .price {
                span {
                    opacity: 0;
                }
            }
        }
    }
    .cardImg {
        position: relative;
        overflow: hidden;
        img {
            width: 100%;
        }
        .title {
            position: absolute;
            content: "";
            bottom: -100%;
            left: 0;
            background: var(--theme-bg-clr);
            width: 100%;
            transition: all .4s ease-in-out;
            a {
                color: var(--white-txt-clr);
                font-size: 18px;
                font-weight: 600;
                line-height: 1;
                padding: 20px;
                display: flex;
                align-items: center;
                img,
                svg {
                    margin-right: 10px;
                    font-size: 20px;
                }
            }
            span {
                position: absolute;
                width: 100px;
                height: 100px;
                background: var(--white-bg-clr);
                border-radius: 50%;
                bottom: 20px;
                right: 20px;
                border: 5px solid var(--theme-bg-clr);
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 20px;
                font-family: var(--lobster);
                color: var(--font-txt-clr);
            }
        }
    }
    .content {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        .title {
            margin-right: 20px;
            ul {
                display: flex;
                padding-top: 20px;
                li {
                    color: var(--review-star-clr);
                    margin-right: 10px;
                    font-size: 15px;
                    &:last-child {
                        margin-right: 0;
                    }
                    &.blank {
                        color: var(--body-txt-clr);
                    }
                }
            }
            h4 {
                line-height: 1;
                a {
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--font-txt-clr);
                    font-family: var(--raleway);
                    display: block;
                    margin-top: 10px;
                    line-height: 1.3;
                }
            }
        }
        .price {
            span {
                font-size: 20px;
                font-family: var(--lobster);
                color: var(--font-txt-clr);
                transition: all .3s ease-in-out;
            }
        }
    }
}


.pagination {
    max-width: 1080px;
    margin: 50px auto 0px;
    display: flex;
    justify-content: center;
    a {
        font-size: 22px;
        font-weight: 500;
        color: var(--body-txt-clr);
        line-height: 1;
        margin: 0px 15px;
        transition: all .3s ease-in-out;
        &:hover {
            color: var(--theme-txt-clr);
        }
    }
}


// Responsive 
@media screen and (max-width: 1199px) {
    .category-select {
        .select-active {
            margin-right: 20px;
        }
    }
}
@media screen and (max-width: 991px) {
    .shop-v1-wrapper {
        grid-template-columns: 1fr 1fr;
    }
    .shop-v1-topBar {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        padding: 20px 0px;
    }
    .category-select {
        margin-top: 20px;
        max-width: 100%;
    }
}
@media screen and (max-width: 767px) {
    .shop-v1-wrapper {
        grid-gap: 30px 20px;
    }
    .shop-card {
        .cardImg {
            .title {
                a {
                    font-size: 16px;
                    padding: 15px;
                }
                span {
                    width: 80px;
                    height: 80px;
                    font-size: 18px;
                }
            }
        }
        .content {
            .title {
                margin-right: 10px;
                ul {
                    padding-top: 10px;
                }
                h4 {
                    a {
                        font-size: 18px;
                        margin-top: 5px;
                    }
                }
            }
            .price {
                span {
                    font-size: 20px;
                }
            }
        }
    }
    .pagination {
        a {
            font-size: 20px;
        }
    }
}
@media screen and (max-width: 575px) {
    .shop-v1-topBar {
        flex-direction: column;
        max-width: 450px;
        .search {
            margin-right: 0;
            max-width: 100%;
        }
    }
    .category-select {
        margin-top: 15px;
        max-width: 100%;
        flex-direction: column;
        .select-active {
            width: 100%;
            margin-right: 0;
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .shop-v1-wrapper {
        grid-gap: 20px;
        max-width: 450px;
        grid-template-columns: 1fr;
        margin: 20px auto 0px;
    }
    .shop-card {
        .discount {
            top: 4px;
            span {
                font-size: 16px;
            }
        }
    }
    .pagination {
        margin-top: 30px;
        a {
            font-size: 18px;
            margin: 0px 10px;
        }
    }
}