.header-area {
    position: relative;
    z-index: 999;
    background: var(--white-bg-clr);
    &.sticky-header {
        position: fixed;
        z-index: 99999999;
        left: 0;
        right: 0;
        top: 0;
        animation-name: fadeInDown;
        animation-duration: 1s;
        animation-fill-mode: both;
        box-shadow: 0 0 10px 10px var(--crt-shadow);
    }
}

.header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.logo {
    max-width: 235px;
    margin-right: 20px;
    img,
    svg {
        max-width: 100%;
    }
}
// Menu
.dropdown {
    position: relative;
}
.main-menu {
    ul {
        li {
            display: inline-block;
            margin: 0px 20px;
            a {
                display: flex;
                align-items: center;
                font-size: 16px;
                font-weight: 400;
                line-height: 1;
                color: var(--font-txt-clr);
                transition: all .3s ease-in-out;
                position: relative;
                padding: 40px 0px;
                img,
                svg {
                    margin-bottom: 3px;
                    margin-left: 5px;
                    transition: 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
                    // cubic-bezier(0.4, 0, 0.2, 1)
                }
                span {
                    transition: all .3s ease-in-out;
                    display: block;
                    transform: translateY(0px);
                }
                &::before {
                    position: absolute;
                    content: "";
                    width: 0%;
                    height: 5px;
                    background: var(--theme-bg-clr);
                    top: 0;
                    right: 0;
                    transition: all .3s ease-in-out;
                }
                &::after {
                    position: absolute;
                    content: attr(data-text);
                    left: 0;
                    top: 50%;
                    opacity: 0;
                    transition: all .3s ease-in-out;
                    transform: translateY(-50%);
                }
            }
            .sub-menu {
                li {
                    margin: 0;
                    display: block;
                    &:last-child {
                        border-bottom: none;
                    }
                    a {
                        padding: 12px 10px 12px 20px;
                        position: relative;
                        z-index: 1;
                        overflow: hidden;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                    }
                }
            }
        }
    }
}

.single-menu {
    margin: 20px 10px;
    &:hover {
        .title {
            h6 {
                color: var(--theme-txt-clr);
            }
        }
    }
    .title {
        margin-bottom: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        h6 {
            font-size: 20px;
            font-weight: 600;
            font-family: var(--raleway);
            line-height: 1;
            transition: all .3s ease-in-out;
        }
        img,
        svg {
            display: none;
            transition: all .3s ease-in-out;
        }
    }
    .menu {
        ul {
            li {
                display: block;
                margin: 0;
                padding: 10px 0px;
                &:first-child {
                    padding-top: 0;
                }
                &:last-child {
                    padding-bottom: 0;
                }
                a {
                    display: block;
                    padding: 0px;
                    line-height: 1.3;
                    &::before {
                        display: none;
                    }
                }
            }
        }
    }
}

.header-right {
    display: flex;
    align-items: center;
}

.hdr-srch,
.hdr-cart {
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--light-gray-bg);
    border-radius: 5px;
    box-shadow: 0px 10px 20px var(--hdr-icon-sdw);
    margin-right: 20px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    cursor: pointer;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        border-radius: 5px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        clip-path: circle(0% at 50% 50%);
        opacity: 0;
        background: var(--theme-bg-clr);
        z-index: -1;
        transition: all .3s ease-in-out;
    }
    img,
    svg {
        color: var(--theme-txt-clr);
        font-size: 20px;
        transition: all .3s ease-in-out;
    }
    &:hover {
        &::before {
            clip-path: circle(70.7% at 50% 50%);
            opacity: 1;
        }
        img,
        svg {
            transform: scale(1.2);
            color: var(--white-txt-clr);
        }
    }

}

.lgn-rgstr {
    a {
        &:first-child {
            &.btn-skew-y {
                border-color: var(--light-gray-bg);
                background-color: transparent;
            }
            &:hover {
                &.btn-skew-y {
                    border-color: var(--font-bdr-clr);
                }
            }
        }
        &:last-child {
            margin-left: -4px;
            &.btn-skew-y {
                border: 1px solid var(--theme-bdr-clr);
            }
            &:hover {
                &.btn-skew-y {
                    border-color: var(--theme-bdr-clr);
                }
            }
        }
    }
}

// HumBurger
.hamgurger {
    margin-left: 10px;
    display: none;
    ul {
        position: relative;
        width: 30px;
        height: 50px;
        overflow: hidden;
        cursor: pointer;
        // background: var(--light-gray-bg);
        // border-radius: 5px;
        &.active {
            li {
                &:nth-of-type(1) {
                    left: 100%;
                    transition-delay: 0s;
                    opacity: 0;
                }
                &:nth-of-type(2) {
                    transform: translateY(-50%) rotate(405deg);
                    transition-delay: .3s;
                    &::before {
                        top: 0;
                        transition-delay: .5s;
                    }
                }
                &:nth-of-type(3) {
                    right: 100%;
                    transition-delay: 0s;
                    opacity: 0;
                }
            }
        }
        li {
            list-style: none;
            position: absolute;
            transform: translateY(-50%);
            width: 100%;
            height: 3px;
            background: #262626;
            transition: .4s;
            border-radius: 10px;
            &:nth-of-type(1) {
                top: 30%;
                left: 0;
                transition-delay: 1s;
                opacity: 1;
            }
            &:nth-of-type(2) {
                top: 50%;
                left: 0;
                transition-delay: 0s;
                &::before {
                    content: '';
                    position: absolute;
                    top: -150px;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: #262626;
                    transform: rotate(90deg);
                    transition: 1s;
                    transition-delay: .5s;
                    border-radius: 10px;
                }
            }
            &:nth-of-type(3) {
                top: 70%;
                right: 0%;
                transition-delay: 1s;
                opacity: 1;
            }
        }
    }
}

@media screen and (min-width: 1200px) {
    .main-menu {
        ul {
            li {
                &:hover {
                    > a {
                        img,svg {
                            color: var(--theme-txt-clr);
                            transform: rotateX(-180deg);
                        }
                        span {
                            opacity: 0;
                            transform: translateY(-20px);
                        }
                        &::after {
                            opacity: 1;
                            color: var(--theme-txt-clr);
                        }
                        &::before {
                            width: 100%;
                            right: auto;
                            left: 0;
                        }
                    }
                   > .sub-menu, .mega-menu {
                        opacity: 1;
                        visibility: visible;
                        transform: scale(1);
                    }
                }
                // Sub Menu
                .sub-menu {
                    position: absolute;
                    min-width: 150px;
                    background-color: var(--white-bg-clr);
                    filter: drop-shadow(10px 20px 30px rgba(0, 0, 0, 0.1));
                    left: 0;
                    top: 100%;
                    z-index: 99999;
                    transition: all .3s ease-in-out;
                    opacity: 0;
                    visibility: hidden;
                    transform: scale(1.1);
                    // padding: 10px 0px;
                    display: block !important;
                    li {
                        &:hover {
                            > a {
                                color: var(--white-txt-clr);
                                span {
                                    transform: translateX(100%);
                                }   
                                &::before {
                                    opacity: 1;
                                    width: 100%;
                                }
                                &::after {
                                    color: var(--white-txt-clr);
                                    transform: translateX(0);
                                }
                                img,svg {
                                    transform: rotate(0deg);
                                    color: var(--white-txt-clr);
                                }
                            }
                            .sub-menu {
                                opacity: 1;
                                left: 100%;
                                visibility: visible;
                            }
                        }
                        a {
                            &::before {
                                background: var(--theme-bg-clr);
                                width: 100%;
                                height: 100%;
                                top: 0;
                                right: 0;
                                position: absolute;
                                content: "";
                                z-index: -1;
                                opacity: 0;
                            }
                            &::after {
                                left: 20px;
                                top: 12px;
                                transform: translateX(-100%);
                            }
                            img,
                            svg {
                                display: block;
                                transform: rotate(-90deg);
                                transition: all .4s ease-in;
                            }
                        }
                        .sub-menu {
                            left: 110%;
                            top: 0;
                            opacity: 0;
                            visibility: hidden;
                            transform: scale(1);
                        }
                    }
                }
            }
        }
    }
    
    // Mega Menu
    .mega-menu {
        position: absolute;
        width: 100%;
        left: 0;
        top: 100%;
        background: var(--white-bg-clr);
        filter: drop-shadow(10px 20px 30px rgba(0, 0, 0, 0.1));
        padding: 30px 110px;
        display: flex !important;
        justify-content: space-between;
        z-index: 999;
        opacity: 0;
        visibility: hidden;
        transform: scale(1.02);
        transition: all .3s ease-in-out;
        .shape-1,
        .shape-2 {
            position: absolute;
            content: "";
            top: 0;
            height: 100%;
            z-index: -1;
            img,
            svg {
                max-width: 100%;
                max-height: 100%;
                height: 100%;
                width: 100%;
            }
        }
        .shape-1 {
            left: 0;
        }
        .shape-2 {
            right: 0;
        }
    }
    .single-menu {
        .menu {
            display: block !important;
        }
    }

}

// Responsive
@media screen and (max-width: 1400px) {
    .logo {
        max-width: 200px;
        margin-right: 10px;
    }
    .main-menu {
        ul {
            li {
                margin: 0px 8px;
            }
        }
    }
    .hdr-srch, 
    .hdr-cart {
        margin-right: 10px;
    }
    .mega-menu {
        padding: 30px 70px;
    }
}
@media screen and (max-width: 1199px) {
    .header-wrapper {
        padding: 15px 0px;
    }
    .hamgurger {
        display: block;
    }
    .main-menu {
        position: absolute;
        top: 100%;
        width: 100%;
        left: 0;
        right: 0;
        background: var(--white-bg-clr);
        filter: drop-shadow(10px 20px 30px rgba(0, 0, 0, 0.1));
        padding: 20px 30px;
        clip-path: polygon(0 0, 100% 0%, 100% 0, 0 0);
        transition: all .4s ease-in-out;
        z-index: 999;
        max-height: calc(100vh - 90px);
        overflow-y: auto;
        &::-webkit-scrollbar {
            width: 3px;
        }
        &::-webkit-scrollbar-thumb {
            background: var(--theme-bg-clr);
            border-radius: 20px;
        }
        &.active {
            clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
        }
        ul {
            li {
                display: block;
                margin: 0;
                
                &.active {
                    > a {
                        span {
                            opacity: 0;
                            transform: translateY(-20px);
                        }
                        &::after {
                            opacity: 1;
                            color: var(--theme-txt-clr);
                        }
                        img,
                        svg {
                            color: var(--theme-txt-clr);
                            transform: rotate(180deg);
                        }
                    }
                    .sub-menu, .mega-menu {
                        opacity: 1;
                        visibility: visible;
                    }
                }
                a {
                    padding: 10px 0px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    &::before {
                        display: none;
                    }
                    img,
                    svg {
                        display: block;
                        transition: all .3s ease-in-out;
                    }
                }
                .sub-menu {
                    padding: 0px 20px;
                    display: none;
                    li {
                        &.active {
                           > a {
                                span {
                                    opacity: 0;
                                }
                                &::after {
                                    opacity: 1;
                                    color: var(--theme-txt-clr);
                                    transform: translateX(0) translateY(-50%);
                                }
                            }
                        }
                        a {
                            padding: 7px 0px;
                            &::after {
                                color: var(--theme-txt-clr) !important;
                                left: 0;
                            }
                        }
                    }
                }
            }
        }

        .lgn-rgstr {
            display: block !important;
            margin-top: 20px;
            a {
                &:first-child {
                    margin-right: 10px;
                }
            }
        }
    }
    .mega-menu {
        padding: 0px 10px;
        display: none;
        .shape-1,
        .shape-2 {
            display: none;
        }
    }
    .single-menu {
        margin: 10px 0px;
        .title {
            margin-bottom: 0px;
            h6 {
                font-size: 17px;
                font-weight: 400;
            }
            &.active {
                img,
                svg {
                    transform: rotate(180deg);
                    color: var(--theme-txt-clr);
                }
            }
            img,
            svg {
                display: block;
            }
        }
        .menu {
            display: none;
            transition: 0s;
            padding: 10px;
            ul {
                li {
                    padding: 5px 0px;
                }
            }
        }
    }

    .lgn-rgstr {
        display: none;
    }
    .hdr-cart {
        margin-right: 0;
    }
}
@media screen and (max-width: 767px) {
    .header-wrapper {
        padding: 10px 0px;
    }
    .hdr-srch, 
    .hdr-cart {
        width: 40px;
        height: 40px;
        &:hover {
            svg, 
            img {
                transform: scale(1.1);
            }
        }
    }
    .hamgurger {
        ul {
            width: 25px;
            height: 40px;
            li {
                height: 2px;
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .logo {
        max-width: 150px;
    }
    .main-menu {
        ul {
            li {
                a {
                    font-size: 15px;
                }
            }
        }
    }
    .single-menu {
        .title {
            h6 {
                font-size: 16px;
            }
        }
    }
    .hdr-srch, 
    .hdr-cart {
        width: 34px;
        height: 34px;
        img,
        svg {
            font-size: 16px;
        }
        &:hover {
            svg, 
            img {
                transform: scale(1.1);
            }
        }
    }
}