

.comingSoon-wrapper {
    display: flex;
}

.comingSoon-img {
    position: relative;
    width: 50%;
    .countdown-clock {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        background: var(--snow-bg);
        border-radius: 20px 0px 0px 20px;
        padding: 30px;
        .countdown-item {
            margin-bottom: 30px;
            height: 100px;
            width: 100px;
            border-radius: 10px;
            border: 1px solid var(--theme-bdr-clr);
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            &:last-child {
                margin-bottom: 0;
            }
            .ci-inner {
                font-size: 30px;
                font-weight: 700;
                color: var(--theme-txt-clr);
                line-height: 1;
            }
            h4 {
                font-size: 18px;
                font-weight: 500;
                color: var(--theme-txt-clr);
                line-height: 1;
                margin-top: 10px;
                font-family: var(--raleway);
            }
        }
    }
}

.comingSonn-content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 20px;
    z-index: 1;
    width: 50%;
    .shape-01,
    .shape-02,
    .shape-03,
    .shape-04,
    .shape-05 {
        position: absolute;
        z-index: -1;
    }
    .shape-01 {
        top: 15%;
        left: 15%;
    }
    .shape-02 {
        top: 15%;
        right: 30%;
    }
    .shape-03 {
        bottom: 35%;
        right: 15%;
    }
    .shape-04 {
        bottom: 10%;
        right: 40%;
    }
    .shape-05 {
        bottom: 20%;
        left: 10%;
    }
    .content {
        max-width: 490px;
        margin-left: 140px;
        h2 {
            color: var(--theme-txt-clr);
            font-family: var(--raleway);
            font-size: 48px;
            font-weight: 800;
            line-height: 1.2;
        }
        p {
            margin-top: 10px;
        }
        a {
            font-size: 24px;
            line-height: 1.4;
            color: var(--font-txt-clr);
            font-weight: 700;
            max-width: 266px;
            display: block;
            text-decoration: underline;
            margin-top: 30px;
        }
        .input-fild {
            max-width: 400px;
            margin-top: 20px;
            button {
                img,
                svg {
                    max-width: 40px;
                    max-height: 40px;
                }
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .comingSoon-img {
        height: 600px;
        img {
            height: 100%;
        }
    }
    .comingSonn-content {
        .content {
            margin-left: 50px;
        }
    }
}

@media screen and (max-width: 991px) {
    .comingSoon-wrapper {
        position: relative;
        padding: 100px 30px 280px;
        z-index: 1;
    }
    .comingSoon-img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        &.bg-overlay {
            &::before {
                background: var(--cpright);
                z-index: 0;
                opacity: 0.8;
            }
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        .countdown-clock {
            // position: relative;
            // transform: translate(0) translateY(0);
            top: auto;
            right: auto;
            transform: translateY(0);
            display: flex;
            bottom: 0;
            left: 0;
            padding: 50px 0px;
            width: 100%;
            border-radius: 0;
            justify-content: center;
            .countdown-item {
                margin: 0 10px;
            }
        }
    }
    .comingSonn-content {
        width: 100%;
        max-width: 520px;
        margin: 0 auto;
        .shape-01 {
            top: -20%;
        }
        .shape-02 {
            top: -20%;
        }
        .shape-04 {
            bottom: -20%;
            right: 0;
        }
        .shape-05 {
            bottom: -20%;
            left: -10%;
        }
        .content {
            margin-left: 0;
            max-width: 100%;
            p {
                color: var(--white-txt-clr);
            }
            a {
                color: var(--white-txt-clr);
            }
        }
    }
}

@media screen and (max-width: 575px) {
    .comingSoon-wrapper {
        padding: 50px 20px 200px;
    }
    .comingSonn-content {
        .shape-01,
        .shape-02,
        .shape-03,
        .shape-04,
        .shape-05 {
            display: none;
        }
        .content {
            h2 {
                font-size: 24px;
            }
            p {
                margin-top: 8px;
            }
            a {
                font-size: 18px;
                margin-top: 20px;
            }
        }
    }
    .comingSoon-img {
        .countdown-clock {
            padding: 35px 0px;
            .countdown-item {
                height: 75px;
                width: 75px;
                border-radius: 5px;
                margin: 5px;
                .ci-inner {
                    font-size: 24px;
                }
                h4 {
                    font-size: 15px;
                    margin-top: 5px;
                }
            }
        }
    }
}