
.fMenu-v2-topbar {
    background: var(--white-bg-clr);
    .fMenuV1-tab-navs {
        .nav-tabs {
            background: transparent;
            border-radius: 0px;
        }
    }
}

.fMenuV2-tab-content {
    background: var(--food-menu-v2);
    backdrop-filter: blur(40px);    
    .fMenu-item-select {
        color: var(--font-txt-clr);
        .title {
            .label-check {
                .box {
                    border: 1px solid var(--font-bdr-clr);
                }
            }
        }
    }
}

.fMenuV2-content {
    padding: 40px 150px 100px;
}

.singel-fMenu-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 100px;
    &:last-child {
        margin-bottom: 40px;
        border-bottom: 1px dashed red;
        padding-bottom: 40px;
    }
    &:nth-child(even) {
        flex-direction: row-reverse;
        .items {
            margin-right: 0;
            margin-left: 30px;
        }
    }
    .items {
        max-width: 540px;
        width: 100%;
        margin-right: 30px;
        .single-fMenu-item {
            width: 100%;
        }
    }
    .thumb {
        max-width: 570px;
        width: 100%;
        img {
            width: 100%;
        }
    }
}

.fMenuV2-totalPrice {
    border-top: 1px dashed var(--theme-bdr-clr);
    max-width: 100%;
    .inner {
        max-width: 470px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        .content {
            h3 {
                color: var(--theme-txt-clr);
            }
            span {
                color: var(--theme-txt-clr);
            }
        }
    }
}

// Resposive
@media screen and (max-width: 1199px) {
    .fMenuV2-content {
        padding: 40px 50px 70px;
    }
    .singel-fMenu-wrap {
        margin-bottom: 70px;
    }
}
@media screen and (max-width: 991px) {
    .singel-fMenu-wrap {
        margin-bottom: 50px;
        flex-direction: column;
        &:nth-child(even) {
            flex-direction: column;
            .items {
                margin-left: 0px;
            }
        }
        .items {
            margin-right: 0px;
        }
        .thumb {
            margin-top: 50px;
        }
    }
}
@media screen and (max-width: 767px) {
    .fMenu-v2-topbar {
        .fMenuV1-tab-navs {
            .nav-tabs {
                .nav-link {
                    padding: 30px 10px;
                }
            }
        }
    }
    
}
@media screen and (max-width: 575px) {
    .fMenu-v2-topbar {
        .fMenuV1-tab-navs {
            .nav-tabs {
                .nav-link {
                    padding: 20px 10px;
                }
            }
        }
    }
    .fMenuV2-content {
        padding: 30px 30px 50px;
    }
    .singel-fMenu-wrap {
        margin-bottom: 30px;
        .thumb {
            margin-top: 30px;
        }
    }
    .fMenuV2-totalPrice {
        .inner {
            flex-direction: column;
        }
    }
}