// Banner ========================================================
.homeV1-banner-area {
    padding: 265px 0px 100px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    &::before {
        background: var(--hv1-banner);
    }
    .shape {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        animation: zoom 7s linear infinite alternate;
        img {
            width: 100%;
            height: 100%;
        }
    }
}
.hv1-banner-wrapper {
    padding-bottom: 165px;
    max-width: 945px;
    width: 100%;
    margin: 0 auto;
    .owl-dots {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        display: flex; 
        align-items: center;
        .owl-dot {
            background: var(--white-txt-clr);
            width: 10px;
            height: 10px;
            margin: 0px 10px;
            transition: all .3s ease-in-out;
            &.active {
                background: var(--theme-bg-clr);
            }
        }
    }
    .owl-item {
        .hv1-banner-content {
            h2,
            .title,
            p,
            a {
                transform: translateY(-100px);
                opacity: 0;
                transition: all 1s ease-in-out;
            }
            h2 {
                transition-duration: 1s;
            }
            .title {
                transition-duration: .9s;
            }
            p {
                transition-duration: .7s;
            }
            a {
                transition-duration: .5s;
            }
        }
        &.active, 
        &.cloned {
            .hv1-banner-content {
                h2,
                .title,
                p,
                a {
                    transform: translateY(0px);
                    opacity: 1;
                }
            }
        }
    }
}
.hv1-banner-content {
    text-align: center;
    h2 {
        font-size: 48px;
        color: var(--theme-txt-clr);
        line-height: 1;
    }
    .title {
        color: var(--white-txt-clr);
        display: block;
        font-size: 96px;
        font-weight: 800;
        line-height: 1;
        margin-top: 20px;
    }
    p {
        max-width: 530px;
        font-size: 18px;
        font-weight: 500;
        color: var(--white-txt-clr);
        margin: 20px auto 0px;
    }
    a {
        margin-top: 30px;
        border: none;
    }
}

