// Gallery =======================================================
.hv1-gallery-wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);   
}
.single-hv1-gallery {
    position: relative;
    overflow: hidden;
    &.overlay {
        position: relative;
        z-index: 1;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: var(--font-bg-clr);
            mix-blend-mode: multiply;
            backdrop-filter: blur(5px);
            z-index: 1;
            transition: all .4s ease-in-out;
        }
    }
    &:hover {
        .hv1-g-content {
            transform: translate(-50%, -50%) scale(1);
            opacity: 1;
        }
        .hv1-g-img {
            img {
                transform: scale(1.1);
            }
        }
    }
}
.hv1-g-img {
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all .4s ease-in-out;
        transform: scale(1.01);
    }
}
.hv1-g-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.2);
    max-width: 365px;
    max-height: 275px;
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    padding: 60px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background: rgba(31, 34, 40, 0.9);
    transition: all .4s ease-in-out;
    z-index: 99;
    opacity: 0;
    h3 {
        color: var(--white-txt-clr);
        line-height: 1;
        position: relative;
        padding-left: 55px;
        &::before,
        &::after {
            position: absolute;
            content: "";
            width: 35px;
            height: 2px;
            background: var(--white-bg-clr);
            left: 0;
        }
        &::before {
            top: 5px;
        }
        &::after {
            bottom: 5px;
            width: 15px;
        }
    }
    p {
        color: var(--white-txt-clr);
        margin-top: 15px;
        font-size: 18px;
        font-weight: 500;
        line-height: 1;
        img,svg {
            margin-right: 20px;
        }
    }
    a {
        margin-top: 40px;
        border: none;
    }
}

