// Menu ============================================================
.hv1-menu-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    max-width: 1080px;
    margin: 0 auto;
}
.single-hv1-menu {
    text-align: center;
    &:hover {
        .image {
            &::before {
                opacity: 1;
            }
            img {
                transform: scale(1.2) rotate(10deg);
            }
            .button {
                top: 50%;
                opacity: 1;
            }
        }
        .content {
            h3 {
                color: var(--theme-txt-clr);
            }
        }
    }
    .image {
        position: relative;
        overflow: hidden;
        z-index: 1;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: var(--ab-chef-bg);
            transform: rotate(180deg);
            mix-blend-mode: multiply;
            opacity: 0;
            transition: all .3s ease-in-out;
            z-index: 2;
        }
        .discount {
            position: absolute;
            z-index: 999;
            right: 0;
            top: 0;
            span {
                display: inline-block;
                padding: 8px 12px;
                background: var(--theme-bg-clr);
                color: var(--white-txt-clr);
                font-size: 18px;
                line-height: 1;
                font-weight: 500;
            }
        }
        img {
            transition: all .5s ease;
            width: 100%;
            height: 100%;
        }
        .button {
            position: absolute;
            content: "";
            top: 60%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: all .3s ease-in;
            opacity: 0;
            z-index: 99;
            a {
                background: var(--theme-bg-clr);
                img,svg {
                    margin-right: 10px;
                }
                &:hover {
                    background: var(--white-bg-clr);
                    color: var(--theme-txt-clr);
                }
            }
        }
    }
    .content {
        margin-top: 20px;
        h3 {
            color: var(--body-txt-clr);
            line-height: 1;
            transition: all .3s ease-in;
        }
        h4 {
            line-height: 1;
            a { 
                color: var(--body-txt-clr);
                font-family: var(--raleway);
                font-size: 20px;
                font-weight: 500;
                line-height: 1.3;
                display: block;
                margin-top: 20px;   
            }
        }
    }
}

