// Video Area ======================================================
.hv1-vider-area {
    position: relative;
    height: 700px;
    .background-img {
        &::before {
            background: var(--font-bg-clr);
            mix-blend-mode: multiply;
            opacity: 0.3;
        }
    }
}
.hv1-video {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    a {
        width: 130px;
        height: 130px;
        background: var(--theme-bg-clr);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 25px;
        img,
        svg {
            max-width: 100%;
            max-height: 100%;
        }
    }
}

// Responsive 
@media screen and (max-width: 1199px) {
