// Testimonial =====================================================
.hv1-testimonial-area {
    position: relative;
    z-index: 1;
    overflow-x: hidden;
    .shape-1, 
    .shape-2, 
    .shape-3, 
    .shape-4, 
    .shape-5 {
        position: absolute;
        z-index: -1;
    }
    .shape-1 {
        left: 0;
        top: 0;
    }
    .shape-2 {
        top: 40%;
        left: 0;
    }
    .shape-3 {
        bottom: 0;
        left: 0;
    }
    .shape-4 {
        right: 0;
        top: 0;
    }
    .shape-5 {
        bottom: 0;
        right: 0;
    }
}
.hv1-tstimnl-wrapper {
    max-width: 580px;
    margin: 0 auto;
    .owl-nav {
        position: absolute;
        top: 30%;
        width: 100%;
        left: 50%;
        transform: translateX(-50%);
        .owl-prev,
        .owl-next {
            position: absolute;
            width: 44px;
            height: 44px;
            background: var(--font-txt-clr);
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            img,
            svg {
                width: 24px;
                height: 24px;
                max-width: 100%;
                max-height: 100%;
                path {
                    color: var(--white-txt-clr);
                    transition: all .3s ease-in;
                }
            }
            &:hover {
                img,
                svg {
                    path {
                        color: var(--theme-txt-clr);
                    }
                }
            }
        }
        .owl-prev {
            left: -40%;
        }
        .owl-next {
            right: -40%;
        }
    }
}
.single-hv1-tstimnl {
    text-align: center;
    img,
    svg {
        max-width: 32px;
        max-height: 23px;
        width: 100%;
        height: 100%;
        path {
            fill: var(--theme-txt-clr);
        }   
    }
    h4 {
        color: var(--white-txt-clr);
        font-size: 18px;
        margin-top: 30px;
        font-family: var(--raleway);
    }
    .line {
        height: 32px;
        width: 2px;
        background: var(--theme-bg-clr);
        display: block;
        margin: 10px auto 0px;
    }
    a {
        color: var(--white-txt-clr);
        font-size: 20px;
        font-weight: 600;
        line-height: 1;
        display: block;
        margin-top: 10px;
        span {
            display: block;
            font-weight: 400;
            font-size: 16px;
            margin-top: 10px;
        }
    }
}

